import { Input, Directive, Output, EventEmitter } from '@angular/core';

import { InputBoolean } from '../../decorators';
import { convertToBoolean, isExternalLink } from '../../utils/util';

/**
 * @description
 *
 * Os links so utilizados como rota. O destino dessa rota pode ser externo ou interno  aplicao. Eles podem ser usados dentro de texto ou isoladamente.
 *
 * #### Boas Prticas
 *
 * O componente link foi projetado para atender os requisitos das Diretrizes de Acessibilidade para Contedo Web (WCAG) 2.1. Tambm foram estruturadas padres de usabilidade para auxiliar na utilizao do componente e garantir uma boa experincia para os usurios. Por isso,  muito importante que, ao aplicar esse componente, o proprietrio do contedo leve em considerao alguns critrios e prticas:
 * ##### Uso
 * - Evite usar muitos links em uma nica tela, pois isso pode confundir o usurio quanto a identificao e rotas.
 * - Em caso de interaes que alteram ou manipulam dados ou acionam alguma ao, priorize o uso de botes ao invs de link.
 * - Use labels (rtulos) que descrevam a finalidade do link. Evite texto como "Clique aqui", por exemplo. (WCAG [2.4.9: Link Purpose - Link Only](https://www.w3.org/WAI/WCAG21/Understanding/link-purpose-link-only))
 * - No use cores para o link com baixo contraste entre o fundo e o texto, pois isso dificulta o entendimento do contedo.  possvel conferir se o contraste est adequado e atingindo o valor de 7:1 em um [Contrast Checker](https://webaim.org/resources/contrastchecker/) (WCAG [1.4.6: Contrast](https://www.w3.org/WAI/WCAG21/Understanding/contrast-enhanced.html))
 *
 * ##### Interao
 * - A interao de foco deve compor o componente, sendo a navegao por mouse ou teclado, e em sequncia lgica. Como por exemplo, ao pressionar Enter, executa o link e move o foco para o destino do link. (WCAG [2.4.3: Focus Order](https://www.w3.org/WAI/WCAG21/Understanding/focus-order))
 * - A distncia entre o link e outras reas interativas deve ter no mnimo 44px de altura e largura, para permitir distanciamento seguro e para que nenhum outro elemento seja acionado sem inteno. (WCAG [2.5.8: Pointer Target Spacing](https://w3c.github.io/wcag/understanding/pointer-target-spacing))
 * - Quando se tratar de um link para uma pgina externa,  recomendvel que esta informao seja adicionada na label do componente link, para que a mudana de contexto seja informada ao usurio (WCAG [3.2.5: Change on Request](https://www.w3.org/WAI/WCAG21/Understanding/change-on-request)).
 *
 * #### Acessibilidade tratada no componente
 * Algumas diretrizes de acessibilidade j so tratadas no componente, internamente, e no podem ser alteradas pelo proprietrio do contedo. So elas:
 * - O link foi desenvolvido utilizando controles padres HTML para permitir a identificao do mesmo na interface por tecnologias assistivas. (WCAG [4.1.2: Name, Role, Value](https://www.w3.org/WAI/WCAG21/Understanding/name-role-value))
 * - A cor no deve ser o nico meio para diferenciar o link de textos comuns ou outros elementos, por isso deve-se manter o underline no link, para proporcionar essa diferena. (WCAG [1.4.1: Use of Color](https://www.w3.org/WAI/WCAG21/Understanding/use-of-color)/ [3.2.4: Consistent Identification](https://www.w3.org/WAI/WCAG21/Understanding/consistent-identification))
 * - O foco precisa ter uma rea mnima do dobro do permetro do link, ter contraste de pelo menos 4.5:1 entre o estado focado e no focado do componente; e o foco no pode ficar escondido por outros elementos da tela. (WCAG [2.4.12: Focus Appearance - Enhanced](https://w3c.github.io/wcag/understanding/focus-appearance-enhanced))
 * - O estado de foco do componente deve ser sempre preservado. (WCAG [2.4.7: Focus Visible](https://www.w3.org/WAI/WCAG21/Understanding/focus-visible))
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                    |
 * |----------------------------------------|-------------------------------------------------------|-------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                 |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                      |
 * | `--text-color`                         | Cor do texto                                          | `var(--color-action-default)`                   |
 * | **Hover**                              |                                                       |                                                 |
 * | `--text-color-hover`                   | Cor do texto no estado de hover                       | `var(--color-action-hover)`                     |
 * | **Pressed**                            |                                                       |                                                 |
 * | `--text-color-pressed`                 | Cor do texto no estado pressionado                    | `var(--color-action-pressed)`                   |
 * | **Focused**                            |                                                       |                                                 |
 * | `--outline-color-focused`              | Cor do outline do estado de focus                     | `var(--color-action-focus)`                     |
 * | **Visited**                            |                                                       |                                                 |
 * | `--text-color-visited`                 | Cor do texto do estado Visited                        | `var(--color-action-pressed)`                   |
 *
 */
@Directive()
export class PoLinkBaseComponent {
  /** Valor do rtulo a ser exibido. */
  @Input('p-label') label: string;

  /** Indica se o link dever ser aberto em uma nova aba. Sempre que utilizar essa propriedade,  importante informar isso ao usurio atravs da label. */
  @Input({ alias: 'p-open-new-tab', transform: convertToBoolean }) openNewTab: boolean = false;

  /** Url que ser aberta ao clicar no link. */
  @Input('p-url') url: string;

  get type(): string {
    if (!this.url && this.action.observed) {
      return 'action';
    }
    return isExternalLink(this.url) ? 'externalLink' : 'internalLink';
  }

  /** Ao que ser executada quando o usurio clicar sobre o `po-link`.
   * > Ao utilizar junto da propriedade `p-url` a ao ser ignorada.
   */
  @Output('p-action') action = new EventEmitter<null>();
}
