import {
  AfterViewInit,
  Directive,
  EventEmitter,
  Inject,
  InjectOptions,
  Injector,
  input,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges
} from '@angular/core';
import { AbstractControl, ControlValueAccessor, NgControl, UntypedFormControl, Validator } from '@angular/forms';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { poLocaleDefault } from '../../../services/po-language/po-language.constant';
import { PoLanguageService } from '../../../services/po-language/po-language.service';

import { PoFieldSize } from '../../../enums/po-field-size.enum';
import { convertToBoolean, getA11yLevel, getDefaultSizeFn, isTypeof, validateSizeFn } from '../../../utils/util';
import { PoTableColumnSpacing } from '../../po-table/enums/po-table-spacing.enum';
import { requiredFailed } from '../validators';
import { PoLookupAdvancedFilter } from './interfaces/po-lookup-advanced-filter.interface';
import { PoLookupColumn } from './interfaces/po-lookup-column.interface';
import { PoLookupFilter } from './interfaces/po-lookup-filter.interface';
import { PoLookupLiterals } from './interfaces/po-lookup-literals.interface';
import { PoLookupFilterService } from './services/po-lookup-filter.service';
import { PoLookupModalService } from './services/po-lookup-modal.service';
import { PoHelperOptions } from '../../po-helper';
import { PoThemeA11yEnum } from '../../../services';

export const poLookupLiteralsDefault = {
  en: <PoLookupLiterals>{
    search: 'Search',
    clean: 'Clean'
  },
  es: <PoLookupLiterals>{
    search: 'Buscar',
    clean: 'Limpiar'
  },
  pt: <PoLookupLiterals>{
    search: 'Pesquisar',
    clean: 'Apagar'
  },
  ru: <PoLookupLiterals>{
    search: '',
    clean: ''
  }
};

/**
 * @description
 *
 * Componente utilizado para abrir uma janela de busca com uma tabela que lista dados de um servio. Nesta janela  possvel buscar e
 * selecionar um ou mais registros que sero enviados para o campo. O `po-lookup` permite que o usurio digite um valor e pressione a tecla *TAB* para
 * buscar um registro.
 *
 * > Caso o campo seja iniciado ou preenchido com um valor inexistente na busca, o mesmo ser limpado.
 * No segundo caso ocorrer aps este perder o foco; ambos os casos o campo ficar invlido quando requerido.
 *
 * > Enquanto o componente realiza a requisio ao servidor, o componente ficar desabilitado e com o status interno do
 * [modelo](https://angular.io/guide/form-validation#creating-asynchronous-validators) como `pending`.
 *
 * Este componente no  recomendado quando a busca dos dados possuir poucas informaes, para isso utilize outros componentes como o
 * `po-select` ou o `po-combo`.
 */
@Directive()
export abstract class PoLookupBaseComponent
  implements ControlValueAccessor, OnDestroy, OnInit, Validator, AfterViewInit, OnChanges
{
  private _literals?: PoLookupLiterals;
  private readonly language: string;
  private _size?: string = undefined;
  private _spacing: PoTableColumnSpacing;

  // Propriedade interna que define se o cone de ajuda adicional ter cursor clicvel (evento) ou padro (tooltip).
  @Input() additionalHelpEventTrigger: string | undefined;

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Exibe um cone de ajuda adicional, com o texto desta propriedade sendo passado para o popover do componente `po-helper`.
   * **Como boa prtica, indica-se utilizar um texto com at 140 caracteres.**
   * > Requer um recuo mnimo de 8px se o componente estiver prximo  lateral da tela.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Input('p-additional-help-tooltip') additionalHelpTooltip?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define que o popover (`p-helper` e/ou `p-error-limit`) ser includo no body da pgina e no
   * dentro do componente. Essa opo pode ser necessria em cenrios com containers que possuem scroll ou overflow
   * escondido, garantindo o posicionamento correto do tooltip prximo ao elemento.
   *
   * > Quando utilizado com `p-helper`, leitores de tela como o NVDA podem no ler o contedo do popover.
   *
   * @default `false`
   */
  @Input({ alias: 'p-append-in-body', transform: convertToBoolean }) appendBox?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Aplica foco no elemento ao ser iniciado.
   *
   * > Caso mais de um elemento seja configurado com essa propriedade, apenas o ltimo elemento declarado com ela ter o foco.
   *
   * @default `false`
   */
  @Input({ alias: 'p-auto-focus', transform: convertToBoolean }) autoFocus: boolean = false;

  /**
   * Label do campo.
   *
   * > Quando utilizar esta propriedade o seu valor ser utilizado como ttulo da modal do componente caso no tenha
   * sido definido um `modalTitle` na propriedade `p-literals`.
   */
  @Input('p-label') label?: string;

  /**
   * @description
   *
   * Objeto com as literais usadas no `po-lookup`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: PoLookupLiterals = {
   *    modalPrimaryActionLabel: 'Select',
   *    modalSecondaryActionLabel: 'Cancel',
   *    modalPlaceholder: 'Search Value',
   *    modalTableNoColumns: 'No columns',
   *    modalTableNoData: 'No data',
   *    modalTableLoadingData: 'Loading data',
   *    modalTableLoadMoreData: 'Load more',
   *    modalTitle: 'Select a user',
   *    modalAdvancedSearch: 'Advanced search',
   *    modalAdvancedSearchTitle: 'Advanced search',
   *    modalAdvancedSearchPrimaryActionLabel: 'Filter',
   *    modalAdvancedSearchSecondaryActionLabel: 'Return',
   *    modalDisclaimerGroupTitle: 'Presenting results filtered by:'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: PoLookupLiterals = {
   *    modalPrimaryActionLabel: 'Select'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <po-lookup
   *   [p-literals]="customLiterals">
   * </po-lookup>
   * ```
   *
   * > O objeto padro de literais ser traduzido de acordo com o idioma do
   * [`PoI18nService`](/documentation/po-i18n) ou do browser.
   */
  @Input('p-literals') set literals(value: PoLookupLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...poLookupLiteralsDefault[poLocaleDefault],
        ...poLookupLiteralsDefault[this.language],
        ...value
      };
    } else {
      this._literals = poLookupLiteralsDefault[this.language];
    }
  }

  get literals() {
    return this._literals || poLookupLiteralsDefault[this.language];
  }

  /** Texto de apoio do campo. */
  @Input('p-help') help?: string;

  /** Mensagem que aparecer enquanto o campo no estiver preenchido. */
  @Input('p-placeholder') set placeholder(value: string) {
    this._placeholder = value || '';
  }

  get placeholder() {
    return this._placeholder;
  }

  /** Nome e Id do componente. */
  @Input('name') name: string;

  /**
   * @description
   *
   * Indica a coluna que ser utilizada como valor do campo.
   *
   * > Ateno: Caso no seja passada ou tenha o contedo incorreto, no ir atualizar o model do formulrio.
   */
  @Input('p-field-value') fieldValue: string;

  /** Indica a coluna que ser utilizada como descrio do campo e como filtro dentro da janela. */
  @Input('p-field-label') set fieldLabel(value: string) {
    this._fieldLabel = value;
    this.keysDescription = [this.fieldLabel];
  }

  get fieldLabel() {
    return this._fieldLabel;
  }

  /** Valor que ser repassado como parmetro para a URL ou aos mtodos do servio que implementam a interface `PoLookupFilter`. */
  @Input('p-filter-params') filterParams?: any;

  /**
   * @optional
   *
   * @description
   *
   * Formato de exibio do campo.
   *
   * Recebe uma funo que deve retornar uma *string* com o/os valores do objeto formatados para exibio, por exemplo:
   *
   * ```
   * fieldFormat(obj) {
   *   return `${obj.id} - ${obj.name}`;
   * }
   * ```
   * > Esta propriedade sobrepe o valor da propriedade `p-field-label` na descrio do campo.
   *
   * Pode-se informar uma lista de propriedades que deseja exibir como descrio do campo, Por exemplo:
   * ```
   * <po-lookup
   *  ...
   *  [p-field-format]="['id','nickname']"
   *  ...
   * >
   *
   * Objeto retornado:
   *   {
   *      id:123,
   *      name: 'Kakaroto',
   *      nickname: 'Goku',
   *   }
   * Apresentao no campo: 123 - Goku
   * ```
   *
   * > Ser utilizado ` - ` como separador.
   */
  @Input('p-field-format') fieldFormat?: ((value) => string) | Array<string>;

  /**
   * Lista das colunas da tabela.
   * Essa propriedade deve receber um array de objetos que implementam a interface PoLookupColumn.
   */
  @Input('p-columns') columns?: Array<PoLookupColumn>;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `p-required`;
   * - No possuir `p-help` e/ou `p-label`.
   *
   * @default `false`
   */
  @Input('p-optional') optional: boolean;

  /**
   *
   * @optional
   *
   * @description
   *
   * Lista de objetos dos campos que sero criados na busca avanada.
   *
   * > Caso no seja passado um objeto ou ento ele esteja em branco o link de busca avanada ficar escondido.
   *
   * Exemplo de URL com busca avanada:
   *
   * ```
   * url + ?page=1&pageSize=20&name=Tony%20Stark&nickname=Homem%20de%20Ferro
   * ```
   *
   * Caso algum parmetro seja uma lista, a concatenao  feita utilizando vrgula.
   * Exemplo:
   *
   * ```
   * url + ?page=1&pageSize=20&name=Tony%20Stark,Peter%20Parker,Gohan
   * ```
   *
   */
  @Input('p-advanced-filters') advancedFilters: Array<PoLookupAdvancedFilter>;

  /**
   * @optional
   *
   * @description
   *
   * Permite que o gerenciador de colunas, responsvel pela definio de quais colunas sero exibidas, seja escondido.
   *
   * @default `false`
   */
  @Input({ alias: 'p-hide-columns-manager', transform: convertToBoolean })
  hideColumnsManager: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Ativa a funcionalidade de scroll infinito para a tabela exibida no retorno da consulta.
   *
   * @default `false`
   */
  @Input({ alias: 'p-infinite-scroll', transform: convertToBoolean }) infiniteScroll: boolean = false;

  /** Exibe um cone que permite limpar o campo. */
  @Input({ alias: 'p-clean', transform: convertToBoolean }) clean: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Permite a seleo de mltiplos itens.
   *
   * > Quando habilitado o valor do campo passar a ser uma lista de valores, por exemplo: `[ 12345, 67890 ]`
   *
   * @default `false`
   */
  @Input({ alias: 'p-multiple', transform: convertToBoolean }) multiple: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define que a altura do componente ser auto ajustvel, possuindo uma altura minima porm a altura mxima ser de acordo
   * com o nmero de itens selecionados e a extenso dos mesmos, mantendo-os sempre visveis.
   *
   * @default `false`
   */
  @Input({ alias: 'p-auto-height', transform: convertToBoolean }) autoHeight: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Exibe a mensagem setada se o campo estiver vazio e for requerido.
   *
   * > Necessrio que a propriedade `p-required` esteja habilitada.
   *
   */
  @Input('p-field-error-message') fieldErrorMessage: string;

  /**
   * @optional
   *
   * @description
   *
   * Limita a exibio da mensagem de erro a duas linhas e exibe um tooltip com o texto completo.
   *
   * > Caso essa propriedade seja definida como `true`, a mensagem de erro ser limitada a duas linhas
   * e um tooltip ser exibido ao passar o mouse sobre a mensagem para mostrar o contedo completo.
   *
   * @default `false`
   */
  @Input('p-error-limit') errorLimit: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho do componente:
   * - `small`: altura do input como 32px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura do input como 44px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o espaamento interno das clulas, impactando diretamente na altura das linhas do table dentro do modal. Os
   * valores permitidos so definidos pelo enum **PoTableColumnSpacing**.
   *
   * > Em nvel de acessibilidade **AA**, caso o valor de `p-spacing` no seja definido, o valor padro ser `extraSmall`
   * > nos seguintes cenrios:
   * > - Quando o valor de `p-size` for `small`;
   * > - Quando o valor padro dos componentes for configurado como `small` no
   * > [servio de tema](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-spacing') set spacing(value: string) {
    const isExtraSmall = value === PoTableColumnSpacing.ExtraSmall;
    const isA11yAAA = getA11yLevel() === PoThemeA11yEnum.AAA;

    if (Object.values(PoTableColumnSpacing).includes(value as PoTableColumnSpacing)) {
      if (isA11yAAA && isExtraSmall) {
        this._spacing = PoTableColumnSpacing.Medium;
      } else {
        this._spacing = value as PoTableColumnSpacing;
      }
    } else {
      this._spacing = this.getDefaultSpacing();
    }
  }

  get spacing() {
    return this._spacing ?? this.getDefaultSpacing();
  }

  /**
   * @optional
   *
   * @description
   *
   * Habilita ou desabilita a quebra automtica de texto. Quando ativada, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * Esta propriedade aplica-se ao texto contido nas clulas da tabela.
   *
   * > Incompatvel com `virtual-scroll`, que requer altura fixa nas linhas.
   *
   * @default `false`
   */
  @Input({ alias: 'p-text-wrap', transform: convertToBoolean }) textWrap?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Habilita o `virtual-scroll` na tabela para melhorar a performance com grandes volumes de dados.
   * A altura da tabela j  pr-definida, portanto o `virtual-scroll` ser ativado automaticamente.
   *
   * > Incompatvel com `p-text-wrap` e `master-detail`, pois o `virtual-scroll` exige altura fixa nas linhas.
   *
   * @default `true`
   */
  @Input({ alias: 'p-virtual-scroll', transform: convertToBoolean }) virtualScroll?: boolean = true;

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define as opes do componente de ajuda (po-helper) que ser exibido ao lado do label quando a propriedade `p-label` for definida, ou, ao lado do componente na ausncia da propriedade `p-label`.
   * > Para mais informaes acesse: https://po-ui.io/documentation/po-helper.
   *
   * > Ao configurar esta propriedade, o antigo cone de ajuda adicional (`p-additional-help-tooltip` e `p-additional-help`) ser ignorado.
   */
  poHelperComponent = input<PoHelperOptions | string>(undefined, { alias: 'p-helper' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Habilita a quebra automtica do texto da propriedade `p-label`. Quando `p-label-text-wrap` for verdadeiro, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * @default `false`
   */
  labelTextWrap = input<boolean>(false, { alias: 'p-label-text-wrap' });

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Evento disparado ao clicar no cone de ajuda adicional.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Output('p-additional-help') additionalHelp = new EventEmitter<any>();

  /**
   * Evento ser disparado quando ocorrer algum erro na requisio de busca do item.
   * Ser passado por parmetro o objeto de erro retornado.
   */
  @Output('p-error') onError: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   * Evento disparado quando uma tecla  pressionada enquanto o foco est no componente.
   * Retorna um objeto `KeyboardEvent` com informaes sobre a tecla.
   */
  @Output('p-keydown') keydown: EventEmitter<KeyboardEvent> = new EventEmitter<KeyboardEvent>();

  /**
   * @optional
   *
   * @description
   *
   * Evento ser disparado quando ocorrer alguma seleo.
   * Ser passado por parmetro o objeto com o valor selecionado.
   */
  @Output('p-selected') selected: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   *  Evento que ser disparado ao alterar o model.
   *  Por parmetro ser passado o novo valor.
   */
  @Output('p-change') change: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   * Evento disparado ao fechar o popover do gerenciador de colunas aps alterar as colunas visveis.
   *
   * O componente envia como parmetro um array de string com as colunas visveis atualizadas.
   * Por exemplo: ["idCard", "name", "hireStatus", "age"].
   */
  @Output('p-change-visible-columns') changeVisibleColumns = new EventEmitter<Array<string>>();

  /**
   * @optional
   *
   * @description
   * Evento disparado ao clicar no boto de restaurar padro no gerenciador de colunas.
   *
   * O componente envia como parmetro um array de string com as colunas configuradas inicialmente.
   * Por exemplo: ["idCard", "name", "hireStatus", "age"].
   */
  @Output('p-restore-column-manager') columnRestoreManager = new EventEmitter<Array<string>>();

  displayAdditionalHelp: boolean = false;
  service: any;

  protected selectedOptions = [];
  protected getSubscription: Subscription;
  protected keysDescription: Array<any>;
  protected oldValue: string = '';
  protected valueToModel;
  protected oldValueToModel = null;
  // eslint-disable-next-line
  protected onTouched: any = null;
  protected resizeListener: () => void;

  private _disabled?: boolean = false;
  private _fieldLabel: string;
  private _filterService: PoLookupFilter | string;
  private _noAutocomplete: boolean;
  private _placeholder: string = '';
  private _required?: boolean = false;
  private readonly _autoHeight: boolean = false;

  private readonly autoHeightInitialValue: boolean;
  private onChangePropagate: any = null;
  private validatorChange: any;

  private control!: AbstractControl;

  private readonly injectOptions: InjectOptions = {
    self: true
  };

  /**
   * Servio responsvel por buscar os dados da tabela na janela. Pode ser informado um servio que implemente a interface
   * `PoLookupFilter` ou uma URL.
   *
   * Quando utilizada uma URL de um servio, ser concatenada nesta URL o valor que deseja-se filtrar, por exemplo:
   *
   * ```
   * url + ?page=1&pageSize=20&filter=Peter
   * ```
   *
   * Caso utilizar ordenao, a coluna ordenada ser enviada atravs do parmetro `order`, por exemplo:
   * - Coluna decrescente:
   *   ```
   *    url + ?page=1&pageSize=20&filter=Peter&order=-name
   *   ```
   *
   * - Coluna ascendente:
   *   ```
   *    url + ?page=1&pageSize=20&filter=Peter&order=name
   *   ```
   *
   * Se for definido a propriedade `p-filter-params`, o mesmo tambm ser concatenado. Por exemplo, para o
   * parmetro `{ age: 23 }` a URL ficaria:
   *
   * ```
   * url + ?page=1&pageSize=20&age=23&filter=Peter
   * ```
   *
   * Ao iniciar o campo com valor, os registros sero buscados da seguinte forma:
   * ```
   * model = 1234;
   *
   * GET url/1234
   * ```
   *
   * Caso estiver com mltipla seleo habilitada:
   * ```
   * model = [1234, 5678]
   *
   * GET url?${fieldValue}=1234,5678
   * ```
   *
   * > Esta URL deve retornar e receber os dados no padro de [API do PO UI](https://po-ui.io/guides/api) e utiliza os valores
   * definidos nas propriedades `p-field-label` e `p-field-value` para a construo do `po-lookup`.
   *
   * Caso o usurio digite um valor e pressione a tecla *TAB* para realizar a busca de um registro especfico, o valor que se
   * deseja filtrar ser codificado utilizando a funo [encodeURIComponent](https://tc39.es/ecma262/#sec-encodeuricomponent-uricomponent)
   * e concatenado na URL da seguinte forma:
   *
   * ```
   * url/valor%20que%20se%20deseja%20filtrar
   * ```
   *
   * > Quando informado um servio que implemente a interface `PoLookupFilter` o tratamento de encoding do valor a ser filtrado ficar a cargo do desenvolvedor.
   *
   */
  @Input('p-filter-service') set filterService(filterService: PoLookupFilter | string) {
    this._filterService = filterService;
    this.setService(this.filterService);
  }

  get filterService() {
    return this._filterService;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a propriedade nativa `autocomplete` do campo como `off`.
   *
   * @default `false`
   */
  @Input('p-no-autocomplete') set noAutocomplete(value: boolean) {
    this._noAutocomplete = convertToBoolean(value);
  }

  get noAutocomplete() {
    return this._noAutocomplete;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define que o campo ser obrigatrio.
   * > Esta propriedade  desconsiderada quando o input est desabilitado `(p-disabled)`.
   *
   * @default `false`
   */
  @Input('p-required') set required(required: boolean) {
    this._required = convertToBoolean(required);

    this.validateModel(this.valueToModel);
  }

  get required(): boolean {
    return this._required;
  }

  /**
   * Define se a indicao de campo obrigatrio ser exibida.
   *
   * > No ser exibida a indicao se:
   * - No possuir `p-help` e/ou `p-label`.
   */
  @Input('p-show-required') showRequired: boolean = false;

  /**
   * @description
   *
   * Indica que o campo ser desabilitado.
   *
   * @default false
   * @optional
   */
  @Input('p-disabled') set disabled(disabled: boolean) {
    this._disabled = <any>disabled === '' ? true : convertToBoolean(disabled);
  }

  get disabled(): boolean {
    return this._disabled;
  }

  constructor(
    private readonly defaultService: PoLookupFilterService,
    @Inject(Injector) private readonly injector: Injector,
    public poLookupModalService: PoLookupModalService,
    languageService: PoLanguageService
  ) {
    this.language = languageService.getShortLanguage();
  }

  ngOnDestroy() {
    if (this.getSubscription) {
      this.getSubscription.unsubscribe();
    }
  }

  ngOnInit(): void {
    this.initializeColumn();
  }

  ngAfterViewInit(): void {
    this.setControl();
  }

  cleanModel() {
    this.cleanViewValue();
    this.callOnChange(undefined);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.columns?.currentValue) {
      this.columns = changes.columns.currentValue;
      this.poLookupModalService?.setChangeColumns(this.columns);
    }

    if (changes.multiple && isTypeof(this.filterService, 'string')) {
      this.service.setConfig(this.filterService, this.fieldValue, this.multiple);
    }
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar os estados de habilitado via forms api
  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  // Funo implementada do ControlValueAccessor.
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model.
  registerOnChange(func: any): void {
    this.onChangePropagate = func;
  }

  // Funo implementada do ControlValueAccessor.
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model.
  registerOnTouched(func: any): void {
    this.onTouched = func;
  }

  // Seleciona o valor do model.
  selectValue(valueSelected: any) {
    this.valueToModel = valueSelected;
    if (this.multiple) {
      this.callOnChange(this.valueToModel);
    } else if (this.valueToModel) {
      this.callOnChange(this.valueToModel[this.fieldValue]);
    } else {
      this.callOnChange(undefined);
    }
    this.selected.emit(valueSelected);
  }

  callOnChange(value: any) {
    // Quando o input no possui um formulrio, ento esta funo no  registrada.
    if (this.onChangePropagate) {
      this.onChangePropagate(value);
    }

    if (this.oldValueToModel !== this.valueToModel) {
      this.change.emit(value);
    }

    // Armazenar o valor antigo do model
    this.oldValueToModel = this.valueToModel;
  }

  searchById(value) {
    let checkedValue = value;

    if (typeof checkedValue === 'string') {
      checkedValue = checkedValue.trim();
    }

    if (checkedValue !== '' && checkedValue !== '.') {
      const oldDisable = this.disabled;
      this.disabled = true;

      if (this.control) {
        // :TODO: Retirar no futuro pois esse setTimeout foi feito
        // pois quando o campo  acionado pelos mtodos setValue ou patchValue
        // a mudana no  detectada
        setTimeout(() => this.control.markAsPending());
      }

      this.getSubscription = this.service
        .getObjectByValue(value, this.filterParams)
        .pipe(
          finalize(() => {
            this.disabled = oldDisable;

            if (this.control) {
              this.control.updateValueAndValidity();
            }
          })
        )
        .subscribe(
          element => {
            if (element?.length || (!Array.isArray(element) && element)) {
              if (Array.isArray(element) && element.length > 1) {
                this.setDisclaimers(element);
                this.updateVisibleItems();
              }

              this.selectModel(Array.isArray(element) ? element : [element]);
            } else {
              this.cleanModel();
            }
          },
          error => {
            this.cleanModel();
            this.onError.emit(error);
          }
        );
    } else {
      this.cleanModel();
    }
  }

  validate(abstractControl: AbstractControl): { [key: string]: any } {
    if (requiredFailed(this.required, this.disabled, abstractControl.value)) {
      return {
        required: {
          valid: false
        }
      };
    }
  }

  writeValue(value: any): void {
    if (value?.length || (!Array.isArray(value) && value)) {
      // Esta condio  executada somente quando  passado o ID para realizar a busca pelo ID.
      this.searchById(value);
    } else {
      this.cleanViewValue();
    }
  }

  // Retorna o Subscriber da propriedade getSubscription que ocorre
  // no evento de blur que executa o mtodo searchEvent().
  getSubscriptionFunction(): Subscription {
    return this.getSubscription;
  }

  protected cleanViewValue() {
    this.setDisclaimers([]);
    this.setViewValue('', {});
    this.oldValue = '';
    this.valueToModel = null;
  }

  // Formata a label do campo.
  protected getFormattedLabel(value: any): string {
    return value ? this.keysDescription.map(column => value[column]).join(' - ') : '';
  }

  // Chama o mtodo writeValue e preenche o model.
  protected selectModel(options: Array<any>) {
    if (options.length) {
      this.selectedOptions = [...options];

      const newModel = this.multiple ? options.map(option => option[this.fieldValue]) : options[0];
      this.selectValue(newModel);

      if (options.length === 1) {
        this.oldValue = options[0][this.fieldLabel];
        this.setViewValue(this.getFormattedLabel(options[0]), options[0]);
      }
    } else {
      this.selectValue(undefined);
      this.cleanViewValue();
    }
  }

  protected validateModel(model: any) {
    if (this.validatorChange) {
      this.validatorChange(model);
    }
  }

  private setService(service: PoLookupFilter | string) {
    if (isTypeof(service, 'object')) {
      this.service = <PoLookupFilterService>service;
    }

    if (service && isTypeof(service, 'string')) {
      this.service = this.defaultService;
      this.service.setConfig(service, this.fieldValue, this.multiple);
    }
  }

  private setControl() {
    const ngControl: NgControl = this.injector.get(NgControl, null, this.injectOptions);

    if (ngControl) {
      this.control = ngControl.control as UntypedFormControl;
    }
  }

  private initializeColumn(): void {
    if (this.fieldLabel) {
      this.keysDescription = [this.fieldLabel];
    } else {
      this.keysDescription = [];

      this.keysDescription = this.columns.filter(element => element.fieldLabel).map(element => element.property);
    }
  }

  // Atribui um ou mais valores ao campo.
  abstract setViewValue(value: any, object: any): void;

  // Mtodo com a implementao para abrir o lookup.
  abstract openLookup(): void;

  abstract setDisclaimers(a);

  abstract updateVisibleItems();

  protected abstract getDefaultSpacing();
}
