/**
 * @usedBy PoButtonComponent
 *
 * @description
 *
 * Enumerao que define os tipos possveis para o `PoButtonComponent`. Estes tipos esto relacionados ao comportamento
 * do boto quando utilizado dentro de um formulrio HTML.
 *
 * @example
 * No uso com o `PoButtonComponent`, a propriedade `p-type` pode ser utilizada para configurar o comportamento:
 *
 * ```
 * <po-button p-label="Enviar" p-type="submit"></po-button>
 * <po-button p-label="Cancelar" p-type="button"></po-button>
 * <po-button p-label="Redefinir" p-type="reset"></po-button>
 * ```
 */
export enum PoButtonType {
  /**
   * Define o boto como do tipo `submit`. Quando clicado, o formulrio  enviado automaticamente,
   * disparando o evento `submit`.
   */
  Submit = 'submit',

  /**
   * Define o boto como do tipo `button`. Este tipo de boto no possui comportamento padro associado
   * e  utilizado principalmente para aes programticas como cliques e disparos de eventos customizados.
   */
  Button = 'button',

  /**
   * Define o boto como do tipo `reset`. Quando clicado, redefine os campos do formulrio ao qual pertence
   * para seus valores iniciais.
   */
  Reset = 'reset'
}
