import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  input,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { PoTooltipDirective } from '../../../../directives';
import { convertToBoolean } from '../../../../utils/util';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente de uso interno, responsvel por gerar uma margem inferior nos componentes que utilizam o po-field-container.
 * Essa margem inferior pode conter uma mensagem de erro.
 */
@Component({
  selector: 'po-field-container-bottom',
  templateUrl: './po-field-container-bottom.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: false
})
export class PoFieldContainerBottomComponent implements OnChanges {
  @ViewChild(PoTooltipDirective) poTooltip: PoTooltipDirective;

  /** Texto exibido no tooltip do cone de ajuda adicional. */
  /** @deprecated v23.x.x */
  @Input('p-additional-help-tooltip') additionalHelpTooltip?: string = '';

  /** Define se o tooltip ser inserido no `body` em vez do componente. */
  @Input({ alias: 'p-append-in-body', transform: convertToBoolean }) appendBox: boolean = false;

  @Input('p-disabled') disabled: boolean = false;

  /**
   * Mensagem que ser apresentada quando o pattern ou a mscara no for satisfeita.
   * Obs: Esta mensagem no  apresentada quando o campo estiver vazio, mesmo que ele seja requerido.
   */
  @Input('p-error-pattern') errorPattern?: string = '';

  /**
   * Limita a exibio da mensagem de erro a duas linhas e exibe um tooltip com o texto completo.
   */
  @Input('p-error-limit') errorLimit: boolean = false;

  @Input('p-help') help?: string;

  /** Ativa a exibio da ajuda adicional. */
  @Input('p-show-additional-help') showAdditionalHelp: boolean = false;

  /** Exibe o cone de ajuda adicional. */
  @Input('p-show-additional-help-icon') showAdditionalHelpIcon: boolean = false;

  /** Define o tamanho do componente. */
  @Input('p-size') size?: string;

  /** Evento disparado ao clicar no cone de ajuda adicional. */
  /** @deprecated v23.x.x */
  @Output('p-additional-help') additionalHelp = new EventEmitter<any>();

  /** Propriedade para controlar a visibilidade do additionalHelp de acordo com a visibilidade do p-label do field.
   * Caso o p-label esteja visvel, o additionalHelp no ser exibido.
   **/
  hideAdditionalHelpByLabel = input(false, { transform: booleanAttribute, alias: 'p-hide-additional-help-by-label' });

  private isInitialChange: boolean = true;

  ngOnChanges(changes: SimpleChanges) {
    if (changes.showAdditionalHelp) {
      if (!this.isInitialChange) {
        if (this.additionalHelpTooltip && this.poTooltip) {
          this.poTooltip.toggleTooltipVisibility(this.showAdditionalHelp);
        } else {
          this.additionalHelp.emit();
        }
      } else {
        this.isInitialChange = false;
      }
    }
  }
}
