import { Component, HostBinding, input } from '@angular/core';
import { PoHelperOptions } from './interfaces/po-helper.interface';
/**
 * @description
 *
 * O componente `po-helper` exibe um cone de ajuda ou informao ao lado de campos, botes ou outros elementos, permitindo ao usurio acessar contedos explicativos em um popover.
 *
 * Principais funcionalidades:
 * - Exibe cone de ajuda (`help`) ou informao (`info`) conforme configurao.
 * - Permite definir ttulo, contedo e aes no popover via propriedade `p-helper`.
 * - Suporte a acessibilidade: navegao por teclado, atributos ARIA e leitura do contedo por leitores de tela.
 * - Controle do tamanho do componente via propriedade `p-size` (`small` ou `medium`).
 * - Permite customizar aes no rodap do popover.
 *
 * Exemplo de uso:
 * ```html
 * <po-helper
 *   [p-helper]="{ title: 'Ajuda', content: 'Texto explicativo', type: 'help' }"
 *   [p-size]="'medium'"
 * ></po-helper>
 * ```
 *
 * Tambm  possvel passar apenas uma string para o contedo:
 * ```html
 * <po-helper p-helper="Texto explicativo"></po-helper>
 * ```
 *
 * A propriedade `p-helper` aceita um objeto do tipo `PoHelperOptions`:
 * ```typescript
 * interface PoHelperOptions {
 *   title?: string;
 *   content: string;
 *   type?: 'help' | 'info';
 *   eventOnClick?: Function;
 *   footerAction?: { label: string; action: Function };
 * }
 * ```
 *
 * > **Importante:** A propriedade `footerAction` no pode ser utilizada quando o tipo do helper for `info`, pois o cone de informao  destinado apenas para exibir informaes estticas sem aes adicionais.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                                | Descrio                                                     | Valor Padro                                      |
 * |--------------------------------------------|---------------------------------------------------------------|---------------------------------------------------|
 * | `--color`                                  | Cor principal do cone                                        | `var(--color-action-default)`                     |
 * | `--border-color-hover`                     | Cor da borda no estado hover                                  | `var(--color-brand-01-darkest)`                   |
 * | `--background-pressed`                     | Cor de background no estado de pressionado&nbsp;              | `var(--color-brand-01-light)`                     |
 * | `--color-disabled`                         | Cor principal no estado disabled                              | `var(--color-action-disabled)`                    |
 *
 */
@Component({
  selector: 'po-helper-base',
  template: '',
  standalone: false
})
export class PoHelperBaseComponent {
  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define o contedo e as opes do popover de ajuda/informao.
   *
   * Aceita uma string simples (exibida como contedo) ou um objeto do tipo `PoHelperOptions` para configurao avanada:
   * - `title`: Ttulo do popover.
   * - `content`: Contedo explicativo exibido no popover.
   * - `type`: Tipo do cone (`help` ou `info`).
   * - `eventOnClick`: Funo chamada ao clicar no cone.
   * - `footerAction`: Objeto com `label` e `action` para ao customizada no rodap do popover.
   *
   * Exemplo de uso:
   * ```html
   * <po-helper p-helper="Texto explicativo"></po-helper>
   * <po-helper [p-helper]="{ title: 'Ajuda', content: 'Texto', type: 'help' }"></po-helper>
   * ```
   *
   */
  helper = input<PoHelperOptions | string>(undefined, {
    alias: 'p-helper',
    transform: this.transformHelper.bind(this)
  });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define o tamanho do componente:
   * - `small`: altura do cone com seu valor de 16px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura do cone com seu valor de 24px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @HostBinding('attr.p-size')
  size = input<string>(undefined, { alias: 'p-size' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Indica se o helper deve ser exibido no estado desativado, desabilitando interaes do usurio.
   *
   * @default `false`
   */
  disabled = input<boolean>(false, { alias: 'p-disabled' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   * Define que o popover ser inserido no body da pgina em vez do elemento definido em `p-target`. Essa opo pode
   * ser necessria em cenrios com containers que possuem scroll ou overflow escondido, garantindo o posicionamento
   * correto do contedo prximo ao elemento.
   */
  appendBox = input<boolean>(false, { alias: 'p-append-in-body' });

  private transformHelper(value: PoHelperOptions | string): PoHelperOptions {
    if (!value) return;
    if (typeof value === 'string') {
      return {
        title: '',
        content: value,
        type: 'help'
      };
    }
    if (!value.type) {
      value.type = 'help';
    }
    if (value.type === 'info' && value.footerAction) {
      delete value.footerAction;
    }
    return value;
  }
}
