/**
 * @usedBy PoTableComponent, PoLookupComponent, PoPageDynamicTableComponent
 *
 * @description
 * Tipos de espaamento interno (padding) das clulas (**p-spacing**) do po-table.
 */
export enum PoTableColumnSpacing {
  /** Espaamento extra pequeno: 0.25rem (vertical) x 0.5rem (horizontal). */
  ExtraSmall = 'extraSmall',

  /** Espaamento pequeno: 0.5rem (vertical) x 1rem (horizontal). */
  Small = 'small',

  /** Espaamento mdio: 0.75rem (vertical) x 1rem (horizontal). */
  Medium = 'medium',

  /** Espaamento grande: 1rem (vertical) x 1rem (horizontal). */
  Large = 'large'
}
