import { Directive, EventEmitter, Input, Output } from '@angular/core';

import { PoFieldSize } from '../../../enums/po-field-size.enum';
import { convertToBoolean, getDefaultSizeFn, validateSizeFn } from '../../../utils/util';

/**
 * @description
 *
 * O componente `po-page-slide`  utilizado para incluir contedos secundrios
 * adicionando controles e navegaes adicionais, mas mantendo o usurio na
 * pgina principal.
 *
 * Este componente  ativado a partir do mtodo `#open()` e pode ser  encerrado
 * atravs do boto que encontra-se no cabealho do mesmo ou atravs do mtodo
 * `#close()`.
 *
 * > Para o correto funcionamento do componente `po-page-slide`, deve ser
 * > importado o mdulo `BrowserAnimationsModule` no mdulo principal da sua
 * > aplicao.
 *
 * Mdulo da aplicao:
 * ```
 * import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
 * import { PoModule } from '@po-ui/ng-components';
 * ...
 *
 * @NgModule({
 *   imports: [
 *     BrowserModule,
 *     BrowserAnimationsModule,
 *     ...
 *     PoModule
 *   ],
 *   declarations: [
 *     AppComponent,
 *     ...
 *   ],
 *   providers: [],
 *   bootstrap: [AppComponent]
 * })
 * export class AppModule { }
 * ```
 *
 * Em aplicaes Standalone, utilize a seguinte configurao para o bootstrap:
 *
 * ```
 * import { bootstrapApplication } from '@angular/platform-browser';
 * import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
 * import { AppComponent } from './app.component';
 *
 * bootstrapApplication(AppComponent, {
 *   providers: [importProvidersFrom(BrowserAnimationsModule)]
 * }).catch(err => console.error(err));
 * ```
 *
 *  Caso utilize componentes de field dentro do page-slide, recomenda-se o uso do [Grid System](https://po-ui.io/guides/grid-system).
 *
 * No rodap  possvel utilizar o componente [`PoPageSlideFooter`](/documentation/po-page-slide-footer) para customizao do template.
 *
 *  * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                              | Descrio                                                         | Valor Padro                                                                  |
 * |------------------------------------------|-------------------------------------------------------------------|-------------------------------------------------------------------------------|
 * | `--font-family`                          | Famlia tipogrfica usada                                         | `var(--font-family-theme)`                                                    |
 * | `--font-weight`                          | Peso da fonte                                                     | `var(--font-weight-bold)`                                                     |
 * | `--padding-header`                       | Espaamento do header                                             | `var(--spacing-md)`                                                           |
 * | `--padding-body`                         | Espaamento do contedo                                           | `var(--line-height-none)`                                                     |
 * | `--padding-footer`                       | Espaamento do footer                                             | `var(--spacing-sm) var(--spacing-md) var(--spacing-xl) var(--spacing-md)`     |
 * | **Default Values**                       |                                                                   |                                                                               |
 * | `--color-overlay`                        | Cor do overlay                                                    | `var(--color-neutral-dark-80)`                                                |
 * | `--opacity-overlay`                      | Cor da opacidade do overlay                                       | `0.7`                                                                         |
 * | `--background-color`                     | Cor de background                                                 | `var(--color-neutral-light-00)`                                               |
 * | `--border-color`                         | Cor da borda                                                      | `var(--color-neutral-light-20)`                                               |
 * | `--color-title`                          | Cor do titulo do header                                           | `var(--color-neutral-dark-95)`                                                |
 * | `--border-radius`                        | Radius da borda                                                   | `var(--border-radius-md) 0 0 var(--border-radius-md)`                         |
 * | `--transition-duration`                  | Durao da transio                                              | `var(--duration-extra-fast)`                                                  |
 * | `--transition-timing`                    | Durao da transio com o tipo de transio                      | `var(--duration-extra-slow) var(--timing-standart)`                           |
 * | `--page-slide-width-sm`                  | Tamanho da largura do componente no tamanho `small`               | `40%`                                                                         |
 * | `--page-slide-width-md`                  | Tamanho da largura do componente no tamanho `medium`              | `50%`                                                                         |
 * | `--page-slide-width-lg`                  | Tamanho da largura do componente no tamanho `large`               | `60%`                                                                         |
 * | `--page-slide-width-xl`                  | Tamanho da largura do componente no tamanho `extra large`         | `70%`                                                                         |
 * | `--page-slide-min-width-auto`            | Tamanho da largura mnima do componente no tamanho `auto`         | `40%`                                                                         |
 * | `--page-slide-max-width-auto`            | Tamanho da largura mxima do componente no tamanho `auto`         | `90%`                                                                         |
 *
 */
@Directive()
export class PoPageSlideBaseComponent {
  /**
   * @description
   *
   * Ttulo da pgina.
   */
  @Input('p-title') title: string;

  /**
   * @description
   *
   * Subttulo da pgina.
   */
  @Input('p-subtitle') subtitle?: string;

  /**
   * @optional
   *
   * @description
   *
   * Oculta o boto de encerramento da pgina.
   *
   * Esta opo s  possvel se a propriedade `p-click-out` estiver habilitada.
   *
   * @default `false`
   */
  @Input({ alias: 'p-hide-close', transform: convertToBoolean }) hideClose: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define se permite o encerramento da pgina ao clicar fora da mesma.
   *
   * @default `false`
   */
  @Input({ alias: 'p-click-out', transform: convertToBoolean }) clickOut: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Permite a expanso dinmica da largura do `po-page-slide` quando `p-size` for `auto` (automtico).
   * Propriedade necessria para correto funcionamento da `po-table` dentro do `po-page-slide`
   *
   * @default `false`
   */
  @Input({ alias: 'p-flexible-width', transform: convertToBoolean }) flexibleWidth: boolean = false;

  /**
   * @optional
   *
   * @description
   * Evento executado ao fechar o page slide.
   */
  @Output('p-close') closePageSlide: EventEmitter<any> = new EventEmitter<any>();

  // Controla se a pgina est ou no oculta, por padro  oculto.
  public hidden = true;

  private _componentsSize?: string = undefined;
  private _size = 'md';

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho da pgina.
   *
   * Valores vlidos:
   *  - `sm` (pequeno)
   *  - `md` (mdio)
   *  - `lg` (grande)
   *  - `xl` (extra-grande)
   *  - `auto` (automtico)
   *
   * > Todas as opes de tamanho, exceto `auto`, possuem uma largura mxima de **768px**.
   *
   * @default `md`
   */
  @Input('p-size') set size(value: string) {
    const sizes = ['sm', 'md', 'lg', 'xl', 'auto'];
    this._size = sizes.indexOf(value) > -1 ? value : 'md';
  }

  get size() {
    return this._size;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho dos componentes de formulrio no template:
   * - `small`: aplica a medida small de cada componente (disponvel apenas para acessibilidade AA).
   * - `medium`: aplica a medida medium de cada componente.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-components-size') set componentsSize(value: string) {
    this._componentsSize = validateSizeFn(value, PoFieldSize);
  }

  get componentsSize(): string {
    return this._componentsSize ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * Ativa a visualizao da pgina.
   *
   * Para utiliz-la  necessrio ter a instncia do componente no DOM, podendo
   * ser utilizado o `ViewChild` da seguinte forma:
   *
   * ```typescript
   * import { PoPageSlideComponent } from '@po/ng-components';
   *
   * ...
   *
   * @ViewChild(PoPageSlideComponent, { static: true }) pageSlide: PoPageSlideComponent;
   *
   * public openPage() {
   *   this.pageSlide.open();
   * }
   * ```
   */
  public open() {
    // Evita com que a pgina seja aberta sem que seja possvel fech-la.
    if (this.hideClose && !this.clickOut) {
      this.hideClose = false;
    }

    this.hidden = false;
  }

  /**
   * Encerra a visualizao da pgina.
   *
   * Para utiliz-la  necessrio ter a instncia do componente no DOM, podendo
   * ser utilizado o `ViewChild` da seguinte forma:
   *
   * ```typescript
   * import { PoPageSlideComponent } from '@po-ui/ng-components';
   *
   * ...
   *
   * @ViewChild(PoPageSlideComponent, { static: true }) pageSlide: PoPageSlideComponent;
   *
   * public closePage() {
   *   this.pageSlide.close();
   * }
   * ```
   */
  public close(): void {
    this.hidden = true;
    this.closePageSlide.emit();
  }
}
