import { Directive, EventEmitter, input, Input, Output } from '@angular/core';
import { AbstractControl, ControlValueAccessor, Validator } from '@angular/forms';

import { requiredFailed } from '../validators';
import { convertToBoolean, convertToInt, getDefaultSizeFn, uuid, validateSizeFn } from './../../../utils/util';

import { PoFieldSize } from '../../../enums/po-field-size.enum';
import { PoCheckboxGroupOptionView } from './interfaces/po-checkbox-group-option-view.interface';
import { PoCheckboxGroupOption } from './interfaces/po-checkbox-group-option.interface';
import { PoHelperOptions } from '../../po-helper';

const poCheckboxGroupColumnsDefaultLength: number = 6;
const poCheckboxGroupColumnsTotalLength: number = 12;

/**
 * @description
 *
 * O componente `po-checkbox-group` exibe uma lista de mltipla escolha onde o usurio pode marcar e desmarcar,
 * utilizando a tecla de espao ou o clique do mouse, vrias opes.
 *
 * > Para seleo nica, utilize o [**PO Radio Group**](/documentation/po-radio-group).
 *
 * Por padro, o po-checkbox-group retorna um array com os valores dos itens selecionados para o model.
 *
 * ```
 * favorites = ['PO', 'Angular'];
 * ```
 *
 * Na maioria das situaes, o array com os objetos setados j atende as necessidades mas, caso o desenvolvedor
 * tenha necessidade de usar um valor indeterminado (`null`), ou seja, nem marcado (`true`) e nem desmarcado (`false`),
 * deve setar a propriedade `p-indeterminate` como `true`.
 *
 * Nesse caso, o po-checkbox-group vai retornar um objeto com todas as opes disponveis e seus valores.
 *
 * ```
 * favorites = {
 *  PO: true,
 *  Angular: true,
 *  VueJS: false,
 *  React: null // indeterminado
 * };
 * ```
 */
@Directive()
export class PoCheckboxGroupBaseComponent implements ControlValueAccessor, Validator {
  // Propriedade interna que define se o cone de ajuda adicional ter cursor clicvel (evento) ou padro (tooltip).
  @Input() additionalHelpEventTrigger: string | undefined;

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Exibe um cone de ajuda adicional, com o texto desta propriedade sendo passado para o popover do componente `po-helper`.
   * **Como boa prtica, indica-se utilizar um texto com at 140 caracteres.**
   * > Requer um recuo mnimo de 8px se o componente estiver prximo  lateral da tela.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Input('p-additional-help-tooltip') additionalHelpTooltip?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define que o popover (`p-helper` e/ou `p-error-limit`) ser includo no body da pgina e no
   * dentro do componente. Essa opo pode ser necessria em cenrios com containers que possuem scroll ou overflow
   * escondido, garantindo o posicionamento correto do tooltip prximo ao elemento.
   *
   * > Quando utilizado com `p-helper`, leitores de tela como o NVDA podem no ler o contedo do popover.
   *
   * @default `false`
   */
  @Input({ alias: 'p-append-in-body', transform: convertToBoolean }) appendBox?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Aplica foco no elemento ao ser iniciado.
   *
   * > Caso mais de um elemento seja configurado com essa propriedade, apenas o ltimo elemento declarado com ela ter o foco.
   *
   * @default `false`
   */
  @Input({ alias: 'p-auto-focus', transform: convertToBoolean }) autoFocus: boolean = false;

  /** Nome dos checkboxes */
  @Input('name') name: string;

  /** Texto de apoio do campo */
  @Input('p-help') help?: string;

  /** Label do campo */
  @Input('p-label') label?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `p-required`;
   * - No possuir `p-help` e/ou `p-label`.
   *
   * @default `false`
   */
  @Input('p-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Exibe a mensagem setada se o campo estiver vazio e for requerido.
   *
   * > Necessrio que a propriedade `p-required` esteja habilitada.
   *
   */
  @Input('p-field-error-message') fieldErrorMessage: string;

  /**
   * @optional
   *
   * @description
   *
   * Limita a exibio da mensagem de erro a duas linhas e exibe um tooltip com o texto completo.
   *
   * > Caso essa propriedade seja definida como `true`, a mensagem de erro ser limitada a duas linhas
   * e um tooltip ser exibido ao passar o mouse sobre a mensagem para mostrar o contedo completo.
   *
   * @default `false`
   */
  @Input('p-error-limit') errorLimit: boolean = false;

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define as opes do componente de ajuda (po-helper) que ser exibido ao lado do label quando a propriedade `p-label` for definida, ou, ao lado do componente na ausncia da propriedade `p-label`.
   * > Para mais informaes acesse: https://po-ui.io/documentation/po-helper.
   *
   * > Ao configurar esta propriedade, o antigo cone de ajuda adicional (`p-additional-help-tooltip` e `p-additional-help`) ser ignorado.
   */
  poHelperComponent = input<PoHelperOptions | string>(undefined, { alias: 'p-helper' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   * Habilita a quebra automtica do texto da propriedade `p-label`. Quando `p-label-text-wrap` for verdadeiro, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * @default `false`
   */
  labelTextWrap = input<boolean>(false, { alias: 'p-label-text-wrap' });

  /**
   * @optional
   *
   * @description
   *
   * Funo para atualizar o `ngModel` do componente, necessrio quando no for utilizado dentro da tag form.
   *
   * Na verso 12.2.0 do Angular a verificao `strictTemplates` vem true como default. Portanto, para utilizar
   * two-way binding no componente deve se utilizar da seguinte forma:
   *
   * ```
   * <po-checkbox-group ... [ngModel]="checkboxgroupModel" (ngModelChange)="checkboxgroupModel = $event"> </po-checkbox-group>
   * ```
   *
   */
  @Output('ngModelChange') ngModelChange: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Evento disparado ao clicar no cone de ajuda adicional.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Output('p-additional-help') additionalHelp = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao alterar valor do campo
   */
  @Output('p-change') change: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   * Evento disparado quando uma tecla  pressionada enquanto o foco est no componente.
   * Retorna um objeto `KeyboardEvent` com informaes sobre a tecla.
   */
  @Output('p-keydown') keydown: EventEmitter<KeyboardEvent> = new EventEmitter<KeyboardEvent>();

  checkboxGroupOptionsView: Array<PoCheckboxGroupOptionView>;
  checkedOptions: any = {};
  checkedOptionsList: any = [];
  displayAdditionalHelp: boolean = false;
  mdColumns: number = poCheckboxGroupColumnsDefaultLength;
  propagateChange: any;
  validatorChange: any;

  private _columns: number = poCheckboxGroupColumnsDefaultLength;
  private _disabled?: boolean = false;
  private _indeterminate?: boolean = false;
  private _options?: Array<PoCheckboxGroupOption>;
  private _required?: boolean = false;
  private _size?: string = undefined;

  /**
   * @optional
   *
   * @description
   *
   * Possibilita definir a quantidade de colunas para exibio dos itens do *checkbox*.
   * -  possvel exibir as opes entre `1` e `4` colunas.
   * - Para resoluo `sm` a colunagem invariavelmente passa para `1` coluna.
   * - Quando se trata de resoluo `md` e o valor estabelecido para colunas for superior a `2`,
   * o *grid system* ser composto por `2` colunas.
   * - Para evitar a quebra de linha, prefira a utilizao de `1` coluna para opes com textos grandes.
   *
   * @default `2`
   *
   */
  @Input('p-columns') set columns(value: number) {
    const columns = convertToInt(value, poCheckboxGroupColumnsDefaultLength);

    this._columns = this.getGridSystemColumns(columns, 4);
    this.mdColumns = this.getGridSystemColumns(columns, 2);
  }

  get columns() {
    return this._columns;
  }

  /**
   * @optional
   *
   * @description
   *
   * Desabilita todos os itens do checkbox.
   *
   * @default `false`
   */
  @Input('p-disabled') set disabled(value: boolean) {
    this._disabled = convertToBoolean(value);

    this.validateModel(this.checkIndeterminate());
  }

  get disabled(): boolean {
    return this._disabled;
  }

  /**
   * @optional
   *
   * @description
   *
   * Caso exista a necessidade de usar o valor indeterminado (`null`) dentro da lista de opes,  necessrio setar
   * a propriedade `p-indeterminate` como `true`, por padro essa propriedade vem desabilitada (`false`).
   *
   * Quando essa propriedade  setada como `true`, o *po-checkbox-group* passa a devolver um objeto completo para o
   * `ngModel`, diferente do array que contm apenas os valores selecionados.
   *
   * @default `false`
   */
  @Input('p-indeterminate') set indeterminate(indeterminate: boolean) {
    this._indeterminate = convertToBoolean(indeterminate);
  }

  get indeterminate() {
    return this._indeterminate;
  }

  /**
   * @optional
   *
   * @description
   * Lista de opes que sero exibidas
   * Nesta propriedade deve ser definido um array de objetos que implementam a interface PoCheckboxGroupOption
   */
  @Input('p-options') set options(value: Array<PoCheckboxGroupOption>) {
    this._options = Array.isArray(value) ? value : [];
    this.removeDuplicatedOptions();
    this.setCheckboxGroupOptionsView(this.options);
  }

  get options() {
    return this._options;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define que o campo ser obrigatrio.
   *
   * @default `false`
   */
  @Input('p-required') set required(required: boolean) {
    this._required = convertToBoolean(required);

    this.validateModel(this.checkIndeterminate());
  }

  get required() {
    return this._required;
  }

  /**
   * Define se a indicao de campo obrigatrio ser exibida.
   *
   * > No ser exibida a indicao se:
   * - No possuir `p-help` e/ou `p-label`.
   */
  @Input('p-show-required') showRequired: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho dos checkboxes do componente:
   * - `small`: 16x16 (disponvel apenas para acessibilidade AA).
   * - `medium`: 24x24.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   *
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  changeValue() {
    const value = this.checkIndeterminate();

    if (this.propagateChange) {
      this.propagateChange(value);
    } else {
      this.ngModelChange.emit(value);
    }

    this.change.emit(value);
  }

  checkIndeterminate() {
    return this.indeterminate ? this.checkedOptions : this.checkedOptionsList;
  }

  checkOption(value: PoCheckboxGroupOption) {
    if (!this._disabled && !value.disabled && !value.fixed) {
      this.checkOptionModel(value);
      this.changeValue();
    }
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar os estados de habilitado via forms api
  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
  }

  registerOnChange(fn: any): void {
    this.propagateChange = fn;
  }

  registerOnTouched(fn: any): void {}

  writeValue(optionsModel: any) {
    if (optionsModel && this.checkedOptions !== optionsModel) {
      this.generateCheckOptions(optionsModel);
    } else {
      this.checkedOptionsList = [];
      this.checkedOptions = {};
    }
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  validate(abstractControl: AbstractControl): { [key: string]: any } {
    if (
      (!this.indeterminate && requiredFailed(this.required, this.disabled, abstractControl.value)) ||
      this.isInvalidIndeterminate()
    ) {
      return {
        required: {
          valid: false
        }
      };
    }
  }

  protected validateModel(model: any) {
    if (this.validatorChange) {
      this.validatorChange(model);
    }
  }

  private checkColumnsRange(columns, maxColumns): boolean {
    const minColumns = 1;

    return columns >= minColumns && columns <= maxColumns;
  }

  private checkOptionModel(optionChecked: PoCheckboxGroupOption) {
    this.checkedOptions[optionChecked.value] = !this.checkedOptions[optionChecked.value];

    if (!this.indeterminate && this.checkedOptionsList.includes(optionChecked.value)) {
      this.checkedOptionsList.splice(this.checkedOptionsList.indexOf(optionChecked.value), 1);
    } else if (!this.indeterminate) {
      this.checkedOptionsList.push(optionChecked.value);
    }
  }

  private generateCheckOptions(optionsModel: any) {
    this.checkedOptions = {};

    if (optionsModel instanceof Array) {
      this.checkedOptionsList = optionsModel;

      this.options.forEach((option: PoCheckboxGroupOption) => {
        this.checkedOptions[option.value] = optionsModel.includes(option.value);
      });
    } else {
      this.options.forEach((option: PoCheckboxGroupOption) => {
        optionsModel[option.value] = optionsModel[option.value] === undefined ? false : optionsModel[option.value];
        this.checkedOptions = optionsModel;
      });
    }
  }

  private getGridSystemColumns(columns: number, maxColumns: number): number {
    const gridSystemColumns = poCheckboxGroupColumnsTotalLength / columns;

    return this.checkColumnsRange(columns, maxColumns) ? gridSystemColumns : poCheckboxGroupColumnsDefaultLength;
  }

  private isInvalidIndeterminate() {
    if (this.indeterminate && this.required && this.checkedOptions) {
      return (<any>Object).values(this.checkedOptions).every(value => value === false);
    }
    return false;
  }

  private removeDuplicatedOptions() {
    this.options.forEach((option, index) => {
      const duplicatedIndex = this.options.findIndex((optionFind: any) => optionFind.value === option.value) === index;
      if (!duplicatedIndex) {
        this.options.splice(this.options.indexOf(option), 1);
      }
    });
  }

  private setCheckboxGroupOptionsView(optionsList: Array<PoCheckboxGroupOption>) {
    this.checkboxGroupOptionsView = optionsList.map(option => ({ ...option, id: uuid() }));
  }
}
