import { Directive, EventEmitter, Input, Output, ViewChild } from '@angular/core';

import { poLocaleDefault } from '../../../services/po-language/po-language.constant';
import { PoLanguageService } from '../../../services/po-language/po-language.service';

import { PoFieldSize } from '../../../enums/po-field-size.enum';
import { getDefaultSizeFn, validateSizeFn } from '../../../utils/util';
import { PoBreadcrumb } from '../../po-breadcrumb/po-breadcrumb.interface';
import { PoPageContentComponent } from '../po-page-content/po-page-content.component';
import { PoPageEditLiterals } from './po-page-edit-literals.interface';

export const poPageEditLiteralsDefault = {
  en: <PoPageEditLiterals>{
    cancel: 'Cancel',
    save: 'Save',
    saveNew: 'Save and New'
  },
  es: <PoPageEditLiterals>{
    cancel: 'Cancelar',
    save: 'Guardar',
    saveNew: 'Guardar y Nuevo'
  },
  pt: <PoPageEditLiterals>{
    cancel: 'Cancelar',
    save: 'Salvar',
    saveNew: 'Salvar e Novo'
  },
  ru: <PoPageEditLiterals>{
    cancel: '',
    save: '',
    saveNew: '  '
  }
};

/**
 * @description
 *
 * O componente **po-page-edit**  utilizado como container principal para tela de edio ou adio de um
 * registro, tendo a possibilidade de usar as aes de "Salvar", "Salvar e Novo" e "Cancelar".
 *
 * Os botes "Salvar" e "Salvar e Novo" podem ser habilitados/desabilitados utilizando a propriedade `p-disable-submit`.
 * Esta propriedade pode ser utilizada para desabilitar os botes caso exista um formulrio invlido na pgina ou alguma
 * regra de negcio no tenha sido atendida.
 *
 * #### Tokens customizveis
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade         | Descrio                                   | Valor Padro                          |
 * |---------------------|---------------------------------------------|---------------------------------------|
 * | **Header**          |                                             |                                       |
 * | `--padding`         | Espaamento do header                       | `var(--spacing-xs) var(--spacing-md)` |
 * | `--gap`             | Espaamento entre os breadcrumbs e o ttulo | `var(--spacing-md)`                   |
 * | `--gap-actions`     | Espaamento entre as aes                  | `var(--spacing-xs)`                   |
 * | `--font-family`     | Famlia tipogrfica do ttulo               | `var(--font-family-theme)`            |
 * | **Content**         |                                             |                                       |
 * | `--padding-content` | Espaamento do contedo                     | `var(--spacing-xs) var(--spacing-sm)` |
 */
@Directive()
export class PoPageEditBaseComponent {
  @ViewChild(PoPageContentComponent, { static: true }) poPageContent: PoPageContentComponent;

  /** Objeto com propriedades do breadcrumb. */
  @Input('p-breadcrumb') breadcrumb?: PoBreadcrumb;

  /** Desabilita botes de submisso (save e saveNew) */
  @Input('p-disable-submit') disableSubmit?: boolean;

  /**
   * Evento que ser disparado ao clicar no boto de "Cancelar".
   *
   * ```
   * <po-page-edit (p-cancel)="myCancelFunction()">
   * </po-page-edit>
   * ```
   *
   * > Caso no utilizar esta propriedade, o boto de "Cancelar" no ser exibido.
   */
  @Output('p-cancel') cancel = new EventEmitter();

  /**
   * Evento que ser disparado ao clicar no boto de "Salvar".
   *
   * ```
   * <po-page-edit (p-save)="mySaveFunction()">
   * </po-page-edit>
   * ```
   *
   * > Caso no utilizar esta propriedade, o boto de "Salvar" no ser exibido.
   */
  @Output('p-save') save = new EventEmitter();

  /**
   * Evento que ser disparado ao clicar no boto de "Salvar e Novo".
   *
   * ```
   * <po-page-edit (p-save-new)="mySaveNewFunction()">
   * </po-page-edit>
   * ```
   *
   * > Caso no utilizar esta propriedade, o boto de "Salvar e Novo" no ser exibido.
   */
  @Output('p-save-new') saveNew = new EventEmitter();

  private _componentsSize?: string = undefined;
  private _literals: PoPageEditLiterals;
  private _title: string;
  private language: string;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho dos componentes de formulrio no template:
   * - `small`: aplica a medida small de cada componente (disponvel apenas para acessibilidade AA).
   * - `medium`: aplica a medida medium de cada componente.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-components-size') set componentsSize(value: string) {
    this._componentsSize = validateSizeFn(value, PoFieldSize);
  }

  get componentsSize(): string {
    return this._componentsSize ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `po-page-edit`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: PoPageEditLiterals = {
   *    cancel: 'Voltar',
   *    save: 'Confirmar',
   *    saveNew: 'Confirmar e criar um novo'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: PoPageEditLiterals = {
   *    cancel: 'Cancelar processo'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <po-page-edit
   *   [p-literals]="customLiterals">
   * </po-page-edit>
   * ```
   *
   * > O objeto padro de literais ser traduzido de acordo com o idioma do
   * [`PoI18nService`](/documentation/po-i18n) ou do browser.
   */
  @Input('p-literals') set literals(value: PoPageEditLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...poPageEditLiteralsDefault[poLocaleDefault],
        ...poPageEditLiteralsDefault[this.language],
        ...value
      };
    } else {
      this._literals = poPageEditLiteralsDefault[this.language];
    }
  }
  get literals() {
    return this._literals || poPageEditLiteralsDefault[this.language];
  }

  /** Ttulo da pgina. */
  @Input('p-title') set title(title: string) {
    this._title = title;
    setTimeout(() => this.poPageContent.recalculateHeaderSize());
  }

  get title() {
    return this._title;
  }

  /**
   * @optional
   *
   * @description
   *
   * Subtitulo do Header da pgina
   */
  @Input('p-subtitle') subtitle: string;

  constructor(languageService: PoLanguageService) {
    this.language = languageService.getShortLanguage();
  }
}
