import { Directive, EventEmitter, Input, Output, TemplateRef } from '@angular/core';

import { convertToBoolean, getDefaultSizeFn, validateSizeFn } from './../../utils/util';
import { PoModalAction } from './po-modal-action.interface';

import { PoFieldSize } from '../../enums/po-field-size.enum';
import { PoLanguageService } from '../../services/po-language/po-language.service';
import { poModalLiterals } from './po-modal.literals';

/**
 * @description
 *
 * O componente `po-modal`  utilizado para incluir contedos rpidos e informativos.
 *
 * No cabealho do componente  possvel definir um ttulo e como tambm permite ocultar o cone de fechamento da modal.
 *
 * Em seu corpo  possvel definir um contedo informativo, podendo utilizar componentes como por exemplo `po-chart`,
 * `po-table` e os demais componentes do PO.
 *
 * No rodap encontram-se os botes de ao primria e secundria, no qual permitem definir uma ao e um rtulo, bem como
 * definir um estado de carregando e / ou desabilitado e / ou definir o boto com o tipo *danger*. Tambm  possvel utilizar
 * o componente [`PoModalFooter`](/documentation/po-modal-footer).
 *
 * >  possvel fechar a modal atravs da tecla *ESC*, quando a propriedade `p-hide-close` no estiver habilitada.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                                  | Descrio                                             | Valor Padro                                                                        |
 * |----------------------------------------------|-------------------------------------------------------|-------------------------------------------------------------------------------------|
 * | **Default Values**                           |                                                       |                                                                                     |
 * | `--border-radius` &nbsp;                     | Contm o valor do raio dos cantos do elemento&nbsp;   | `var(--border-radius-md)`                                                           |
 * | `--border-width` &nbsp;                      | Contm o valor da largura dos cantos do elemento&nbsp;| `var(--border-width-sm)`                                                            |
 * | `--border-color` &nbsp;                      | Cor da borda                                          | `var(--color-neutral-light-20)`                                                     |
 * | `--background` &nbsp;                        | Cor de background                                     | `var(--color-neutral-light-00)`                                                     |
 * | `--shadow` &nbsp;                            | Contm o valor da sombra do elemento                  | `var(--shadow-md)`                                                                  |
 * | `--color-overlay` &nbsp;                     | Cor da camada visual temporria                       | `var(--color-neutral-dark-80)`                                                      |
 * | `--opacity-overlay` &nbsp;                   | Opacidade da camada visual temporria &nbsp;          | `0.7`                                                                               |
 * | `--color-divider` &nbsp;                     | Cor das divises do modal                             | `var(--color-neutral-light-20)`                                                     |
 * | `--padding-header` &nbsp;                    | Padding do header do modal                            | `var(--spacing-sm) var(--spacing-md)`                                               |
 * | `--padding-body` &nbsp;                      | Padding do corpo do modal                             | `var(--spacing-md) var(--spacing-2xl) var(--spacing-2xl) var(--spacing-md) `        |
 *
 */
@Directive()
export class PoModalBaseComponent {
  /** Ttulo da modal. */
  @Input('p-title') title: string;

  /** Evento disparado ao fechar o modal. */
  @Output('p-close') closeModal: EventEmitter<any> = new EventEmitter();

  /**
   * Deve ser definido um objeto que implementa a interface `PoModalAction` contendo a label e a funo da primeira ao.
   * Caso esta propriedade no seja definida ou esteja incompleta, automaticamente ser adicionado um boto de ao com
   * a funo de fechar a modal.
   */
  @Input('p-primary-action') primaryAction?: PoModalAction;

  /** Deve ser definido um objeto que implementa a interface `PoModalAction` contendo a label e a funo da segunda ao. */
  @Input('p-secondary-action') secondaryAction?: PoModalAction;

  language;
  literals;

  // Controla se a modal fica oculto ou visvel, por padro  oculto
  isHidden = true;

  // Event emmiter para quando a modal  fechada pelo 'X'.
  public onXClosed = new EventEmitter<boolean>();
  private _componentsSize?: string = undefined;
  private _hideClose?: boolean = false;
  private _size?: string = 'md';

  /**
   * Define o tamanho da modal.
   *
   * Valores vlidos:
   *  - `sm` (pequeno)
   *  - `md` (mdio)
   *  - `lg` (grande)
   *  - `xl` (extra grande)
   *  - `auto` (automtico)
   *
   * > Quando informado `auto` a modal calcular automaticamente seu tamanho baseado em seu contedo.
   * Caso no seja informado um valor, a modal ter o tamanho definido como `md`.
   *
   */
  @Input('p-size') set size(value: string) {
    const sizes = ['sm', 'md', 'lg', 'xl', 'auto'];
    this._size = sizes.indexOf(value) > -1 ? value : 'md';
  }

  get size() {
    return this._size;
  }

  /**
   * Define o fechamento da modal ao clicar fora da mesma.
   * Informe o valor `true` para ativar o fechamento ao clicar fora da modal.
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  clickOut?: boolean = false;
  @Input('p-click-out') set setClickOut(value: boolean | string) {
    this.clickOut = value === '' ? false : convertToBoolean(value);
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho dos componentes de formulrio no modal:
   * - `small`: aplica a medida small de cada componente (disponvel apenas para acessibilidade AA).
   * - `medium`: aplica a medida medium de cada componente.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-components-size') set componentsSize(value: string) {
    this._componentsSize = validateSizeFn(value, PoFieldSize);
  }

  get componentsSize(): string {
    return this._componentsSize ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * @optional
   *
   * @description
   *
   * Oculta o cone de fechar do cabealho da modal.
   *
   * > Caso a propriedade estiver habilitada, no ser possvel fechar a modal atravs da tecla *ESC*.
   *
   * @default `false`
   */
  @Input('p-hide-close') set hideClose(value: boolean) {
    this._hideClose = convertToBoolean(value);
  }

  get hideClose() {
    return this._hideClose;
  }

  /**
   * @optional
   *
   * @description
   * cone exibido ao lado esquerdo do label do titlo da modal.
   *
   *  possvel usar qualquer um dos cones da [Biblioteca de cones](https://po-ui.io/icons). conforme exemplo abaixo:
   * ```
   * <po-modal p-icon="an an-user" p-title="PO Modal"></po-modal>
   * ```
   * Tambm  possvel utilizar outras fontes de cones, por exemplo a biblioteca *Font Awesome*, da seguinte forma:
   * ```
   * <po-modal p-icon="fa fa-podcast" p-title="PO Modal"></po-modal>
   * ```
   * Outra opo seria a customizao do cone atravs do `TemplateRef`, conforme exemplo abaixo:
   * ```
   * <po-modal [p-icon]="template" p-title="PO Modal"></po-modal>
   *
   * <ng-template #template>
   *  <ion-icon style="font-size: inherit" name="heart"></ion-icon>
   * </ng-template>
   * ```
   */
  @Input('p-icon') icon?: string | TemplateRef<void>;

  constructor(poLanguageService: PoLanguageService) {
    this.language = poLanguageService.getShortLanguage();

    this.literals = {
      ...poModalLiterals[this.language]
    };
  }

  /** Funo para fechar a modal. */
  close(xClosed = false): void {
    this.closeModal.emit();

    this.isHidden = true;
    if (xClosed) {
      this.onXClosed.emit(xClosed);
    }
  }

  /** Funo para abrir a modal. */
  open(): void {
    this.validPrimaryAction();

    this.isHidden = false;
  }

  validPrimaryAction() {
    if (!this.primaryAction) {
      this.primaryAction = {
        action: () => this.close(),
        label: this.literals.close
      };
    }

    if (!this.primaryAction['action']) {
      this.primaryAction['action'] = () => this.close();
    }
    if (!this.primaryAction['label']) {
      this.primaryAction['label'] = this.literals.close;
    }
  }
}
