/**
 * @description
 *
 * Tamanhos da propriedade `p-size`. A medida `small` est disponvel apenas para acessibilidade AA.
 */
export enum PoCheckboxSize {
  /** Define a caixa de seleo como 16px. */
  Small = 'small',

  /** Define a caixa de seleo comoc24px. */
  Medium = 'medium',

  /** Define a caixa de seleo comoc32px. */
  Large = 'large'
}
