/**
 * @usedBy PoRichTextComponent
 *
 * @description
 * Enumerao que define as aes disponveis na barra de ferramentas do `PoRichTextComponent`.
 * Cada ao corresponde a um conjunto de botes ou funcionalidades que podem ser habilitados ou desabilitados
 * na barra de ferramentas do editor do rich-text.
 */
export enum PoRichTextToolbarActions {
  /**
   * Seletor de cores, Ao que permite que o usurio altere a cor do texto selecionado.
   */
  Color = 'color',

  /**
   * Alinhamento de texto, incluindo alinhamento  esquerda, centralizado,  direita e justificado.
   */
  Align = 'align',

  /**
   * Formatao de texto, como aplicar negrito, itlico ou sublinhado ao texto selecionado.
   */
  Format = 'format',

  /**
   * Listas com marcadores (bullet points) ou listas numeradas.
   */
  List = 'list',

  /**
   * Links no contedo, aplica partes do texto para serem clicveis e direcionem para URLs especificadas.
   */
  Link = 'link',

  /**
   * Mdias, como imagens, no contedo do editor.
   */
  Media = 'media'
}
