import { Component, Input, input } from '@angular/core';

import { PoFieldSize } from '../../enums/po-field-size.enum';
import { getDefaultSizeFn, validateSizeFn } from '../../utils/util';
import { PoButtonGroupToggle } from './enums/po-button-group-toggle.enum';
import { PoButtonGroupItem } from './po-button-group-item.interface';

const PO_TOGGLE_TYPE_DEFAULT = 'none';

/**
 * @description
 *
 * O componente `po-button-group`  formado por um conjunto de botes distribudos horizontalmente.
 * Cada boto do grupo  tratado de forma individual, recebendo assim um rtulo, uma ao bem como se dever estar habilitado ou no.
 *
 * Este componente alm de servir como um agrupador de botes para ao, tambm permite que sejam utilizados
 * para selees multiplas e nicas.
 *
 * O grupo de botes deve ser utilizado para organizar as aes de maneira uniforme e transmitir a ideia de que os botes fazem
 * parte de um mesmo contexto.
 *
 * #### Boas prticas
 *
 * - Evite usar o `po-button-group` com apenas 1 ao, para isso utilize o `po-button`.
 * - Procure utilizar no mximo 3 aes para cada `po-button-group`.
 *
 * > As recomendaes do `po-button` tambm valem para o `po-button-group`.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                    |
 * |----------------------------------------|-------------------------------------------------------|-------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                 |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                      |
 * | `--font-size`                          | Tamanho da fonte                                      | `var(--font-size-default)`                      |
 * | `--font-weight`                        | Peso da fonte                                         | `var(--font-weight-bold)`                       |
 * | `--line-height`                        | Tamanho da label                                      | `var(--line-height-none)`                       |
 * | `--border-radius`                      | Contm o valor do raio dos cantos do elemento&nbsp;   | `var(--border-radius-md)`                       |
 * | `--border-width`                       | Contm o valor da largura dos cantos do elemento&nbsp;| `var(--border-width-md)`                        |
 * | `--padding`                            | Preenchimento                                         | `0 1em`                                         |
 * | `--text-color`                         | Cor do texto                                          | `var(--color-neutral-light-00)`                 |
 * | `--color`                              | Cor principal do boto                                | `var(--color-action-default)`                   |
 * | `--background-color`                   | Cor de background                                     | `var(--color-transparent)`                      |
 * | `--shadow`                             | Contm o valor da sombra do elemento                  | `var(--shadow-none)`                            |
 * | **Hover**                              |                                                       |                                                 |
 * | `--color-hover`                        | Cor principal no estado hover                         | `var(--color-action-hover)`                     |
 * | `--background-hover`                   | Cor de background no estado hover                     | `var(--color-brand-01-lighter)`                 |
 * | `--border-color-hover`                 | Cor da borda no estado hover                          | `var(--color-brand-01-darkest)`                 |
 * | **Focused**                            |                                                       |                                                 |
 * | `--outline-color-focused`              | Cor do outline do estado de focus                     | `var(--color-action-focus)`                     |
 * | **Pressed**                            |                                                       |                                                 |
 * | `--color-pressed`                      | Cor principal no estado de pressionado                | `var(--color-action-pressed)`                   |
 * | `--background-pressed`                 | Cor de background no estado de pressionado&nbsp;      | `var(--color-brand-01-light)`                   |
 * | **Disabled**                           |                                                       |                                                 |
 * | `--color-disabled`                     | Cor principal no estado disabled                      | `var(--color-action-disabled)`                  |
 * | `--background-color-disabled` &nbsp;   | Cor de background no estado disabled                  | `var(--color-transparent)`                      |
 *
 */
@Component({
  selector: 'po-button-group-base',
  template: '',
  standalone: false
})
export class PoButtonGroupBaseComponent {
  /**
   * @Input
   *
   * @description
   *
   * Lista de botes.
   */
  readonly buttons = input<Array<PoButtonGroupItem>>([], { alias: 'p-buttons' });

  private _size?: string = undefined;

  private _toggle?: string = PO_TOGGLE_TYPE_DEFAULT;

  /**
   * @optional
   *
   * @description
   *
   * Define o modo de seleo dos botes no componente conforme valores especificados no enum `PoButtonGroupToggle`:
   *  - `multiple`: permite mltiplas selees.
   *  - `none`: desativa a funcionalidade de seleo.
   *  - `single`: restringe a seleo a um nico boto.
   *
   * @default `none`
   */
  @Input('p-toggle') set toggle(value: string) {
    this._toggle = (<any>Object).values(PoButtonGroupToggle).includes(value) ? value : PO_TOGGLE_TYPE_DEFAULT;

    this.checkSelecteds(this._toggle);
  }

  get toggle(): string {
    return this._toggle;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho do componente:
   * - `small`: altura de 32px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura de 44px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  onButtonClick(buttonClicked: PoButtonGroupItem, buttonIndex: number) {
    if (this.toggle === PoButtonGroupToggle.Single) {
      this.buttons().forEach(
        (button, index) => (button.selected = index === buttonIndex ? !buttonClicked.selected : false)
      );
    } else if (this.toggle === PoButtonGroupToggle.Multiple) {
      buttonClicked.selected = !buttonClicked.selected;
    }
  }

  private checkSelecteds(toggleMode: string) {
    if (toggleMode === PoButtonGroupToggle.None) {
      this.deselectAllButtons();
    } else if (toggleMode === PoButtonGroupToggle.Single) {
      const hasMoreOneSelected = this.buttons().filter(button => button.selected).length > 1;
      if (hasMoreOneSelected) {
        this.deselectAllButtons();
      }
    }
  }

  private deselectAllButtons() {
    this.buttons().forEach(button => (button.selected = false));
  }
}
