/**
 * @usedBy PoChartComponent
 *
 * @description
 *
 * *Enum* `PoChartLabelFormat` para especificao dos tipos de formatao do eixo de valor no grfico.
 */
export enum PoChartLabelFormat {
  /**
   * Os valores sero exibidos no formato numrico com duas casas decimais. Equivalente ao formato `'1.2-2'` da [DecimalPipe](https://angular.io/api/common/DecimalPipe).
   */
  Number = 'number',

  /**
   * Os valores sero exibidos com o smbolo monetrio de acordo com a formatao padro da aplicao, isto , o valor do token [DEFAULT_CURRENCY_CODE](https://angular.dev/api/core/DEFAULT_CURRENCY_CODE). Para adequar ao padro numrico brasileiro,  necessrio configurar o [LOCALE_ID](https://angular.dev/api/core/LOCALE_ID) da aplicao. A configurao pode ser feita da seguinte forma:
   * ```
   * import { LOCALE_ID } from '@angular/core';
   * import { registerLocaleData } from '@angular/common';
   * import localePt from '@angular/common/locales/pt';
   *
   * registerLocaleData(localePt);
   *
   * @NgModule({
   *   providers: [
   *     { provide: LOCALE_ID, useValue: 'pt-BR' },
   *     { provide: DEFAULT_CURRENCY_CODE, useValue: 'BRL' }
   *   ]
   * })
   * export class AppModule { }
   * ```
   */
  Currency = 'currency'
}
