import { poLocaleDefault, poLocales } from '../services/po-language/po-language.constant';
import { ElementRef } from '@angular/core';
import { PoThemeA11yEnum } from '../services/po-theme/enum/po-theme-a11y.enum';
import { PoThemeService } from '../services/po-theme/po-theme.service';

/**
 * Converte e formata os bytes em formato mais legvel para o usurio.
 *
 * Por exemplo:
 * - 31457280 em 30 MB.
 * - 21474836480 em 20 GB.
 * - 12.5666666 em 12.57 Bytes (duas casas decimais).
 *
 * @param bytes {number} Valor em bytes
 * @param decimals {number} Quantidade de casas decimais que ter aps a converso.
 */
export function formatBytes(bytes: number, decimals = 2): string {
  if (!bytes) {
    return undefined;
  }

  const multiplier = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
  const result = Math.floor(Math.log(bytes) / Math.log(multiplier));
  decimals = decimals < 0 ? 0 : decimals;

  return `${parseFloat((bytes / Math.pow(multiplier, result)).toFixed(decimals))} ${sizes[result]}`;
}

/**
 * Retorna o idioma atual do navegador
 */
export function getBrowserLanguage(): string {
  // navigator.userLanguage is the value for IE10
  const language = navigator.language || navigator['userLanguage'];
  const shortLanguage = getShortLanguage(language);

  return poLocales.includes(shortLanguage) ? language : poLocaleDefault;
}

/**
 * @deprecated use getDefaultSizeFn
 *
 * Retorna o tamanho padro dos componentes conforme o nvel de acessibilidade.
 */
export function getDefaultSize<T>(poThemeService: PoThemeService, sizeEnum: T): T[keyof T] {
  return poThemeService.getA11yDefaultSize() === 'small' ? sizeEnum['Small'] : sizeEnum['Medium'];
}

/**
 * Retorna o tamanho padro dos componentes conforme o nvel de acessibilidade.
 */
export function getDefaultSizeFn<T>(sizeEnum: T): T[keyof T] {
  return getA11yDefaultSize() === 'small' ? sizeEnum['Small'] : sizeEnum['Medium'];
}

/**
 * Retorna o idioma do navegador, com somente as duas primeiras letras. Por exemplo: "pt" ou "es".
 *
 * Caso o valor retornado pelo navegador no estiver dentro dos idiomas suportados pelo PO,
 * ser retornado a linguagem padro (poLocaleDefault).
 */
export function getShortBrowserLanguage(): string {
  return getShortLanguage(getBrowserLanguage());
}

/**
 * Retorna o idioma com somente a abreviao do idioma (duas primeiras letras).
 * Por exemplo: "pt" ou "es".
 *
 * @param language {string} linguagem.
 *
 * @returns sigla do idioma padro {string}.
 *
 * @default pt
 */
export function getShortLanguage(language: string): string {
  return (language || poLocaleDefault).toLowerCase().substring(0, 2);
}

export function isLanguage(value) {
  const languageRegex = new RegExp('^[a-z]{2}(-[a-z]{2})?$', 'i');

  return languageRegex.test(value);
}

/* istanbul ignore next */
export function reloadCurrentPage() {
  const currentUrl = window.location.origin + window.location.pathname;

  if (isValidUrl(currentUrl)) {
    window.location.assign(currentUrl);
  }
}

export function isValidUrl(url: string, location: Location = window.location): boolean {
  return url === location.origin + location.pathname;
}

export function convertToBoolean(val: any): boolean {
  if (typeof val === 'string') {
    val = val.toLowerCase().trim();
    return val === 'true' || val === 'on' || val === '';
  }

  if (typeof val === 'number') {
    return val === 1;
  }

  return !!val;
}

export function convertToInt(value: any, valueDefault?: any): number {
  const validNumber = parseInt(value, 10);
  const validDefaultValue = parseInt(valueDefault, 10);
  const defaultValue = validDefaultValue || validDefaultValue === 0 ? validDefaultValue : undefined;

  return validNumber || validNumber === 0 ? validNumber : defaultValue;
}

export function isTypeof(object: any, type: any) {
  return typeof object === type;
}

/**
 *
 * @param fn Funo que ser executada dentro do contexto. Podendo ser o nome da funo
 * ou a referncia da mesma.
 *
 * @param context Contexto do qual a funo ser executada.
 */
export function callFunction(fn: any, context: any, param?): void {
  if (isTypeof(fn, 'function')) {
    fn.call(context, param);
  } else {
    context[fn](param);
  }
}

export function convertIsoToDate(value: string, start: boolean, end: boolean) {
  if (value) {
    const day = parseInt(value.substring(8, 10), 10);
    const month = parseInt(value.substring(5, 7), 10);
    const year = parseInt(value.substring(0, 4), 10);

    if (start) {
      const date = new Date(year, month - 1, day, 0, 0, 0);

      setYearFrom0To100(date, year);

      return date;
    } else if (end) {
      const date = new Date(year, month - 1, day, 23, 59, 59);

      setYearFrom0To100(date, year);

      return date;
    } else {
      return new Date(year, month - 1, day);
    }
  }
}

export function convertDateToISODate(date: Date) {
  if (date) {
    const getMonth = date.getMonth() + 1;
    const day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
    const month = getMonth < 10 ? '0' + getMonth : getMonth;
    const year = formatYear(date.getFullYear());

    return year + '-' + month + '-' + day;
  } else {
    return null;
  }
}

export function convertDateToISOExtended(date: Date, time?: string) {
  if (date) {
    const getMonth = date.getMonth() + 1;
    const day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
    const month = getMonth < 10 ? '0' + getMonth : getMonth;
    const year = formatYear(date.getFullYear());

    const dateString = date.toString();

    if (time !== null) {
      return year + '-' + month + '-' + day + time;
    } else {
      return (
        year +
        '-' +
        month +
        '-' +
        day +
        'T' +
        dateString.substring(16, 24) +
        dateString.substring(28, 31) +
        ':' +
        dateString.substring(31, 33)
      );
    }
  } else {
    return null;
  }
}

/**
 * Transforma o ano em uma string no formato yyyy e caso o ano seja menor que 1000 preenche com zeros a esquerda.
 *
 * @param year Ano
 */
export function formatYear(year: number) {
  if (year >= 1000) {
    return year.toString();
  }

  if (year > 99 && year < 1000) {
    return `0${year}`;
  }

  if (year > 9 && year < 100) {
    return `00${year}`;
  }

  if (year >= 0 && year < 10) {
    return `000${year}`;
  }
}

// Verifica se o navegador em que est sendo usado  Internet Explorer ou Edge
export function isIEOrEdge() {
  const userAgent = window.navigator.userAgent;

  return /msie\s|trident\/|edge\//i.test(userAgent);
}

// Verifica se o navegador em que est sendo usado  Internet Explorer
export function isIE() {
  const userAgent = window.navigator.userAgent;

  return /msie\s|trident/i.test(userAgent);
}

// Verifica se o navegador em que est sendo usado  Firefox
export function isFirefox() {
  const userAgent = window.navigator.userAgent;

  return userAgent.toLowerCase().indexOf('firefox') > -1;
}

// Verifica se o navegador em que est sendo usado  Safari
export function isSafari() {
  const userAgent = window.navigator.userAgent;

  return /^(?!.*chrome).*safari.*$/.test(userAgent.toLocaleLowerCase());
}

// Verifica qual o dispositivo que est sendo usado
export function isMobile() {
  const userAgent = window.navigator.userAgent;

  return userAgent.match(/Android|webOS|iPhone|iPad|iPod|BlackBerry|Windows Phone/i);
}

export function isEquals(value, comparedValue) {
  return JSON.stringify(value) === JSON.stringify(comparedValue);
}

export function isKeyCodeEnter(event: any): boolean {
  return event.keyCode === 13 || event.which === 13;
}

export function isKeyCodeSpace(event: any): boolean {
  return event.keyCode === 32 || event.which === 32;
}

/**
 * Caso o ano original da data seja entre 0 e 100 atribui esse valor ao ano, pois o `new Date` do javascript transforma o ano para 190X.
 *
 * @param date Data
 * @param year Ano original
 */
export function setYearFrom0To100(date: Date, year: number) {
  if (year >= 0 && year < 100) {
    date.setFullYear(year);
  }
}

export function sortOptionsByProperty(options: Array<any>, property: string) {
  options.sort((optionA, optionB) => {
    optionA = optionA[property].toString().toLowerCase();
    optionB = optionB[property].toString().toLowerCase();

    if (optionA < optionB) {
      return -1;
    }
    if (optionA > optionB) {
      return 1;
    }
    return 0;
  });
}

/**
 * Ordena o campos baseado no valor da propriedade `order`.
 *
 * S sero aceitos valores com nmeros inteiros maiores do que zero para a ordenao.
 *
 * Campos sem `order` ou com valores negativos, zerados ou invlidos
 * recebero o valor default e seguiro o posicionamento dentro do
 * array.
 *
 * @param fields campo que se deseja ordenar.
 * @param defaultOrdering valor que ser utilizado para manter na posio do array.
 */
export function sortFields(fields = [], defaultOrdering = -1) {
  const resultClassification = { fieldAComesFirst: -1, fieldAComesAfter: 1, keepPositions: 0 };

  const isOrderValid = (order: number) => isTypeof(order, 'number') && order > 0;
  const applyDefaultOrdering = (order: number) => (isOrderValid(order) ? order : defaultOrdering);

  return fields.sort((fieldA, fieldB) => {
    const orderA = applyDefaultOrdering(fieldA.order);
    const orderB = applyDefaultOrdering(fieldB.order);

    if (orderA === orderB) {
      return resultClassification.keepPositions;
    }

    if (orderA === defaultOrdering) {
      return resultClassification.fieldAComesAfter;
    }

    if (orderB === defaultOrdering) {
      return resultClassification.fieldAComesFirst;
    }

    return orderA - orderB;
  });
}

export function removeDuplicatedOptions(list: Array<any>) {
  for (let i = 0; i < list.length; i++) {
    if (i === 0) {
      continue;
    }

    if (list.findIndex(op => op.value === list[i].value) !== i) {
      list.splice(i, 1);
      i--;
    }
  }
}

export function removeDuplicatedOptionsWithFieldValue(list: Array<any>, newValue) {
  for (let i = 0; i < list.length; i++) {
    if (i === 0) {
      continue;
    }

    if (list.findIndex(op => op[newValue] === list[i][newValue]) !== i) {
      list.splice(i, 1);
      i--;
    }
  }
}

export function removeUndefinedAndNullOptions(list: Array<any>) {
  for (let i = 0; i < list.length; i++) {
    if (list[i].value === undefined || list[i].value === null) {
      list.splice(i, 1);
      i--;
    }
  }
}

export function removeUndefinedAndNullOptionsWithFieldValue(list: Array<any>, newValue) {
  for (let i = 0; i < list.length; i++) {
    if (list[i][newValue] === undefined || list[i][newValue] === null) {
      list.splice(i, 1);
      i--;
    }
  }
}

export function validValue(value: any) {
  return (value !== null && value !== undefined && value !== '') || value === false;
}

export function isExternalLink(url): boolean {
  return url ? url.startsWith('http') : false;
}

export function openExternalLink(url): void {
  window.open(url, '_blank');
}

export function getFormattedLink(link: string): string {
  let formattedLink = '';
  // Retira todos os pontos no comeo da URL.
  if (link) {
    formattedLink = link.replace(/^(\.)+/g, '');
  }
  // Verifica se foi utilizado uma rota que no comece com barra.
  if (!formattedLink.startsWith('/')) {
    formattedLink = '/'.concat(formattedLink);
  }
  return formattedLink;
}

/**
 * Mtodo responsvel por ordenar dois valores.
 *
 * @param leftSide Primeiro valor a ser comparado.
 * @param rightSide Segundo valor a ser comparado.
 * @param ascending Determina se ser em ordem ascendente ou descendente.
 */
export function sortValues(leftSide: string | Date, rightSide: string | Date, ascending: boolean = true): number {
  const left = isTypeof(leftSide, 'string') ? (leftSide as string).toLowerCase() : leftSide;
  const right = isTypeof(rightSide, 'string') ? (rightSide as string).toLowerCase() : rightSide;

  const leftIsInvalid = left === null || left === undefined || Number.isNaN(left);
  const rightIsInvalid = right === null || right === undefined || Number.isNaN(right);

  if (ascending) {
    if (left < right || leftIsInvalid) {
      return -1;
    } else if (left > right || rightIsInvalid) {
      return 1;
    }
  } else if (ascending === false) {
    if (left < right || leftIsInvalid) {
      return 1;
    } else if (left > right || rightIsInvalid) {
      return -1;
    }
  }
  return 0;
}

export function validateDateRange(date: Date, dateStart: Date, dateEnd: Date) {
  if (dateStart && dateEnd) {
    return date >= dateStart && date <= dateEnd;
  } else if (dateStart && !dateEnd) {
    return date >= dateStart;
  } else if (!dateStart && dateEnd) {
    return date <= dateEnd;
  } else {
    return true;
  }
}

export function uuid() {
  function hex4() {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  }

  return hex4() + hex4() + '-' + hex4() + '-' + hex4() + '-' + hex4() + '-' + hex4() + hex4() + hex4();
}

export function capitalizeFirstLetter(text: string): string {
  return `${text.charAt(0).toUpperCase()}${text.slice(1)}`;
}

/**
 * Mapeia um novo array apenas com as propriedades definidas pelo desenvolvedor baseado em um array de
 * origem.
 *
 * Exemplo:
 *
 * ```
 * const people = [
 *  { id: 1, name: 'Fulano', birthdate: '1980-11-01', genre: 'Male', city: 'So Paulo', dependents: 2 },
 *  { id: 2, name: 'Beltrano', birthdate: '1997-01-21', genre: 'Female', city: 'Joinville', dependents: 0 },
 *  { id: 3, name: 'Siclano', birthdate: '1995-07-15', genre: 'Male', city: 'Joinville', dependents: 0 }
 * ];
 *
 * const properties = ['id', 'name'];
 *
 * const idAndName = mapArrayByProperties(people, properties);
 *
 * console.log(idAndName); // [{ id: 1, name: 'Fulano' }, { id: 2, name: 'Beltrano' }, { id: 3, name: 'Siclano' }]
 * ```
 *
 * Um outro uso para o mtodo  "parear" todos os objetos do array com as mesmas propriedades.
 *
 * ```
 * const customers = [
 *  { id: 1, name: 'Fulano', city: 'So Paulo', dependents: 2 }, // sem genre
 *  { id: 2, name: 'Beltrano', genre: 'Female', city: 'Joinville' }, // sem dependents
 *  { id: 3, name: 'Siclano', genre: 'Male', city: 'Joinville', dependents: 0 }
 * ];
 * const properties = ['id', 'name', 'city', 'genre', 'dependents'];
 *
 * const pattern = mapArrayByProperties(customers, properties);
 * console.log(pattern);
 *
 * // [
 * //   { id: 1, name: 'Fulano', city: 'So Paulo', genre: undefined, dependents: 2 },
 * //   { id: 2, name: 'Beltrano', city: 'Joinville', genre: 'Female', dependents: undefined },
 * //   { id: 3, name: 'Siclano', city: 'Joinville', genre: 'Male', dependents: 0 }
 * // ]
 * ```
 *
 * @param items {Array<any>} Array de items original.
 * @param properties {Array<string>} Array de string com a lista de propriedades que devem ser retornadas.
 *
 * @returns Array<any>
 */
export function mapArrayByProperties(items: Array<any> = [], properties: Array<string> = []): Array<any> {
  return items.map(item => mapObjectByProperties(item, properties));
}

/**
 * Mapeia um novo objeto apenas com as propriedades definidas pelo desenvolvedor.
 *
 * Exemplo:
 *
 * ```
 * const person = { id: 1, name: 'Fulano', birthdate: '1980-11-01', genre: 'Male', city: 'So Paulo', dependents: 2 };
 *
 * const properties = ['id', 'name'];
 *
 * const idAndName = mapObjectByProperties(person, properties);
 *
 * console.log(idAndName); // { id: 1, name: 'Fulano' }
 * ```
 *
 * @param object {Array<any>} Array de items original.
 * @param properties {Array<string>} Array de string com a lista de propriedades que devem ser retornadas.
 *
 * @returns Array<any>
 */
export function mapObjectByProperties(object: any = {}, properties: Array<string> = []) {
  const getSelectedProperties = (selectedProperties, property) => ({
    ...selectedProperties,
    [property]: object[property]
  });

  return properties.reduce(getSelectedProperties, {});
}

/**
 * Retorna os valores de um objeto dentro de um array.
 *
 * > Simula o Object.values(obj), o mesmo deve ser removido assim que a verso typescrit for atualizada.
 *
 * @param object Objeto de onde ser pego os valores.
 */
export function valuesFromObject(object: any = {}): Array<any> {
  return Object.keys(object).map(property => object[property]);
}

/**
 * Converte um arquivo em base64.
 *
 * @param file arquivo que ser convertido.
 */
export function convertImageToBase64(file: File): Promise<any> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    /* istanbul ignore next */
    reader.onerror = error => reject(error);
  });
}

/**
 * Valida se a imagem  do tipo base64 valida
 *
 */
export function isValidImageBase64(value: string): boolean {
  try {
    return !!value.match(/^data:image\/([a-zA-Z]*);base64,([^\"]*)$/);
  } catch (err) {
    return false;
  }
}

/**
 * Converte um nmero em decimal baseado na quantidade de casas decimais.
 *
 * Caso o valor seja invlido, ser retornado o valor `undefined`.
 * Valores invlidos so: `false`, `NaN`, `strings` que no numricas, `undefined` e `null`.
 *
 * @param number valor que ser convertido
 * @param decimalsPlace quantidade de casas decimais
 */
export function convertNumberToDecimal(number: any, decimalsPlace: number): number {
  const isValidValue = (number || number === 0) && !isNaN(number);

  const floatValue = isValidValue ? parseFloat(number) : undefined;

  try {
    return parseFloat(floatValue.toFixed(decimalsPlace));
  } catch {
    return floatValue;
  }
}

/**
 * Retorna uma copia do objeto sujo, sem as propriedades nulas ou indefinidas.
 * Retorna o objeto sem as propriedades que contm valores nulos ou indefinidos.
 *
 * @param dirtyObject
 */
export function clearObject(dirtyObject: object): any {
  const cleanObject = {};

  Object.keys(dirtyObject).forEach(key => {
    if (dirtyObject[key] !== null && dirtyObject[key] !== undefined) {
      cleanObject[key] = dirtyObject[key];
    }
  });

  return cleanObject;
}

export function validateObjectType(value: any) {
  return isTypeof(value, 'object') && !Array.isArray(value) ? value : undefined;
}

/**
 * Retorna os elementos DOM capazes de receber foco.
 *
 * > Atualmente so considerados "focveis" os elementos DOM `input`, `select`,
 * `textarea`, `button` e `a`.
 *
 * @param parentElement Elemento DOM pai.
 * @returns Lista dos elementos DOM filhos "focveis".
 */
export function getFocusableElements(parentElement: Element): NodeListOf<Element> {
  const focusableElements = 'button:not([disabled]), [href], input, select, textarea, [tabindex]:not([tabindex="-1"]';
  return parentElement.querySelectorAll(focusableElements);
}

// Retorna o formato de acordo com o locale.
export function replaceFormatSeparator(format: string, separator: string) {
  let newFormat = format;
  if (separator !== '/') {
    newFormat = newFormat.replace(/\//g, separator);
  }
  return newFormat;
}

/**
 * Recebe um array de objetos para ordenao utilizando chave como comparativo e
 * se a order  crescente(true) ou descrescente(false)
 *
 * Exemplo:
 *
 * ```
 * items: [{country: 'japao'}, {country: 'brasil'} , {country: 'china'}]
 * key: 'country'
 * isAscendingOrder: true
 * Resultado do retorno:
 *    [{country: 'brasil'}, {country: 'china'} , {country: 'japao'}]
 * ```
 *
 *
 * @param items : lista de itens.
 * @param key : propriedade utilizada na comparao.
 * @param isAscendingOrder : ordenao crescente ou descrescente.
 */
export function sortArrayOfObjects(items, key, isAscendingOrder) {
  if (items) {
    return items.sort((a, b) => {
      const valueA = a[key];
      const valueB = b[key];

      if (typeof valueA === 'number' && typeof valueB === 'number') {
        return isAscendingOrder ? valueA - valueB : valueB - valueA;
      } else {
        const compareResult = valueA.toString().localeCompare(valueB.toString());
        return isAscendingOrder ? compareResult : -compareResult;
      }
    });
  }
}

/**
 * @deprecated use validateSizeFn
 *
 * Valida e retorna um tamanho permitido para os componentes, considerando a acessibilidade.
 */
export function validateSize<T>(value: string, poThemeService: PoThemeService, sizeEnum: T): T[keyof T] {
  const validSizes = Object.values(sizeEnum) as Array<string>;

  if (value && validSizes.includes(value)) {
    if (value === sizeEnum['Small'] && poThemeService.getA11yLevel() === PoThemeA11yEnum.AAA) {
      return sizeEnum['Medium'];
    }
    return value as T[keyof T];
  }

  return getDefaultSize(poThemeService, sizeEnum);
}

/**
 * Valida e retorna um tamanho permitido para os componentes, considerando a acessibilidade.
 */
export function validateSizeFn<T>(value: string, sizeEnum: T): T[keyof T] {
  const validSizes = Object.values(sizeEnum) as Array<string>;

  if (value && validSizes.includes(value)) {
    if (value === sizeEnum['Small'] && getA11yLevel() === PoThemeA11yEnum.AAA) {
      return sizeEnum['Medium'];
    }
    return value as T[keyof T];
  }

  return getDefaultSizeFn(sizeEnum);
}

/**
 * Retorna a preferncia global de tamanho dos componentes.
 *
 * @returns `'small'` ou `'medium'`.
 */
export function getA11yDefaultSize(): string {
  const defaultSize = localStorage.getItem('po-default-size');
  const a11yLevel = document.documentElement.getAttribute('data-a11y');
  return defaultSize === PoFieldSize.Small && a11yLevel === PoThemeA11yEnum.AA ? PoFieldSize.Small : PoFieldSize.Medium;
}

/**
 * Retorna o nvel de acessibilidade configurado no tema.
 * Se no estiver configurado, retorna `AAA` como padro.
 * @returns {PoThemeA11yEnum} O nvel de acessibilidade, que pode ser `AA` ou `AAA`.
 */
export function getA11yLevel(): PoThemeA11yEnum {
  const a11yLevel = document.documentElement.getAttribute('data-a11y');

  if (a11yLevel === PoThemeA11yEnum.AA) {
    return PoThemeA11yEnum.AA;
  }

  localStorage.setItem('po-default-size', PoFieldSize.Medium);
  return PoThemeA11yEnum.AAA;
}

/**
 * Retorna o modo de adensamento dos componentes agrupadores.
 * Se no estiver configurado, retorna `medium` como padro.
 * @returns {PoDensityMode} O modo de adensamento, que pode ser `small` ou `medium`.
 */
export function getDensityMode(): PoDensityMode {
  const densityMode = document.documentElement.getAttribute('po-density-mode');

  if (densityMode === PoDensityMode.Small) {
    return PoDensityMode.Small;
  }

  return PoDensityMode.Medium;
}

/**
 * Realiza a traduo das propriedades de ajuda dos componentes.
 * Type do Helper default  'help' e size 'medium'.
 * @returns Objeto {helperSettings} com as propriedades do helper
 * @returns Propriedade {hideAdditionalHelp} para controle ao esconder o cone de ajuda adicional depreciado.
 */
export function setHelperSettings(
  label: string,
  additionalHelpTooltip: string,
  poHelperComponent?: any,
  size?: string,
  onClick?: any
): { hideAdditionalHelp: boolean; helperSettings?: any } {
  const baseSize = size ?? poHelperComponent?.size ?? 'medium';
  const resolvedSize = baseSize === 'large' ? 'medium' : baseSize;
  const defaulWithoutLabel = { hideAdditionalHelp: false, helperSettings: null };

  if (onClick) {
    return {
      hideAdditionalHelp: true,
      helperSettings: {
        type: 'help',
        eventOnClick: onClick,
        size: resolvedSize
      }
    };
  }
  if (additionalHelpTooltip && !poHelperComponent) {
    return {
      hideAdditionalHelp: true,
      helperSettings: {
        content: additionalHelpTooltip,
        type: 'help',
        size: resolvedSize
      }
    };
  }
  if (poHelperComponent) {
    if (poHelperComponent.eventOnClick) {
      return {
        hideAdditionalHelp: true,
        helperSettings: {
          type: poHelperComponent.type || 'help',
          eventOnClick: poHelperComponent.eventOnClick,
          size: resolvedSize
        }
      };
    }
    if (typeof poHelperComponent === 'string' && poHelperComponent.trim() !== '') {
      return {
        hideAdditionalHelp: true,
        helperSettings: { content: poHelperComponent, size: resolvedSize }
      };
    }
    return {
      hideAdditionalHelp: true,
      helperSettings: { ...poHelperComponent, size: resolvedSize }
    };
  }
  return defaulWithoutLabel;
}

/** Atualiza o estado do tooltip baseado na propriedade scrollWidth e clientWidth do labelElement.
 * @param isTooltipActive Indica se o tooltip j est ativo
 * @param labelElement ElementRef do label que ser verificado se est com ellipsis
 * @returns boolean indicando se o tooltip deve ser ativado ou desativado
 */
export function updateTooltip(isTooltipActive: boolean, labelElement: ElementRef<HTMLElement>): boolean {
  const el = getMeasurableEl(labelElement);
  if (!el) return false;

  const isEllipsed = el.scrollWidth > el.clientWidth;
  if (isTooltipActive !== isEllipsed) {
    return isEllipsed;
  } else {
    return isTooltipActive;
  }
}

/**
 * Retorna o elemento label que deve ser medido para verificar se est com ellipsis.
 * @param labelElement ElementRef do label que ser verificado se est com ellipsis
 * @returns O elemento que deve ser medido para verificar se est com ellipsis.
 */
export function getMeasurableEl(labelElement: ElementRef<HTMLElement>): Element {
  const host = labelElement?.nativeElement;
  if (!host) return null;
  const inner = host.querySelector('.po-label, label, .po-label-title, .po-field-title .po-checkbox-label');
  return inner ?? host;
}
