import { Directive, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { poLocaleDefault } from '../../services/po-language/po-language.constant';
import { PoLanguageService } from '../../services/po-language/po-language.service';
import { PoChartType } from '../po-chart/enums/po-chart-type.enum';
import { poChartLiteralsDefault } from '../po-chart/interfaces/po-chart-literals-default.interface';
import { PoChartLiterals } from '../po-chart/interfaces/po-chart-literals.interface';
import { PoChartOptions } from '../po-chart/interfaces/po-chart-options.interface';
import { PoChartDataLabel } from '../po-chart/interfaces/po-chart-serie-data-label.interface';
import { PoChartSerie } from '../po-chart/interfaces/po-chart-serie.interface';
import { PoPopupAction } from '../po-popup';

const poChartMinHeight = 200;
const poChartDefaultHeight = 400;

/**
 * @description
 *
 * O `po-chart`  um componente para renderizao de dados atravs de grficos, com isso facilitando a compreenso e tornando a
 * visualizao destes dados mais agradvel.
 *
 * Atravs de suas principais propriedades  possvel definir atributos, tais como tipo de grfico, altura, ttulo, cores customizadas, opes para os eixos, entre outros.
 *
 * O componente permite utilizar em conjunto sries do tipo linha e coluna.
 *
 * Alm disso, tambm  possvel definir uma ao que ser executada ao clicar em determinado elemento do grfico
 * e outra que ser executada ao passar o *mouse* sobre o elemento.
 *
 * #### Guia de uso para Grficos
 *
 * > Veja nosso [guia de uso para grficos](/guides/guide-charts) para auxiliar na construo do seu grfico,
 * informando em qual caso utilizar, o que devemos evitar e boas prticas relacionada a cores.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                              | Descrio                                                               | Valor Padro                                      |
 * |------------------------------------------|-------------------------------------------------------------------------|---------------------------------------------------|
 * | **Chart (po-chart)**                     |                                                                         |                                                   |
 * | `--background-color-grid`                | Cor de background dos grficos                                          | `var(--color-neutral-light-00)`                   |
 * | `--color-description-chart`              | Cor da descrio dos grficos                                           | `var(--color-neutral-dark-70)`                    |
 * | `--font-family-description-chart`        | Fonte da descrio dos grficos                                         | `var(--font-family-theme)`                        |
 * | `--font-size-description-chart`          | Tamanho da fonte da descrio dos grficos                              | `var(--font-size-sm)`                             |
 * | `--font-weight-description-chart`        | Peso da fonte da descrio dos grficos                                 | `var(--font-weight-normal)`                       |
 * | **Header (po-chart .po-chart-header )**  |                                                                         |                                                   |
 * | `--background-color`                     | Cor de background do cabealho                                          | `var(--color-neutral-light-00)`                   |
 * | `--color`                                | Cor da fonte do cabealho                                               | `var(--color-neutral-dark-70)`                    |
 * | `--font-family`                          | Famlia tipogrfica usada                                               | `var(--font-family-theme)`                        |
 * | `--font-size-title`                      | Tamanho da fonte                                                        | `var(--font-size-default)`                        |
 * | `--font-size-icons`                      | Tamanho dos cones                                                      | `var(--font-size-md)`                             |
 * | `--font-weight`                          | Peso da fonte                                                           | `var(--font-weight-bold)`                         |
 * | **Chart (po-chart .po-chart)**           |                                                                         |                                                   |
 * | `--color-grid`                           | Cor da linha dos grficos que possuem eixo                              | `var(--color-neutral-light-20)`                   |
 * | `--font-family-grid`                     | Famlia tipogrfica usada nos valores dos eixos                         | `var(--font-family-theme)`                        |
 * | `--font-size-grid`                       | Tamanho da fonte usada nos valores dos eixos                            | `var(--font-size-xs)`                             |
 * | `--font-weight-grid`                     | Peso da fonte usada nos valores dos eixos                               | `var(--font-weight-normal)`                       |
 * | `--color-legend`                         | Cor da fonte da legenda                                                 | `var(--color-neutral-dark-70)`                    |
 * | `--border-radius-bar`                    | Tamanho da borda nos graficos `Bar` e `Column`                          | `var(--border-radius-none)`                       |
 * | `--border-color`                         | Cor da borda do grfico nos Grficos `Donut` e `Pie`                    | `var(--color-neutral-light-00)`                   |
 * | `--color-hightlight-value`               | Cor do valor de destaque nos Grficos `Donut` e `Gauge`                 | `var(--color-neutral-dark-70)`                    |
 * | `--font-family-hightlight-value`         | Famlia tipogrfica do valor de destaque nos Grficos `Donut` e `Gauge` | `var(--font-family-theme)`                        |
 * | `--font-weight-hightlight-value`         | Peso da fonte do valor de destaque nos Grficos `Donut` e `Gauge`       | `var(--font-weight-bold)`                         |
 * | `--color-base-gauge`                     | Cor da base do grfico `Gauge`                                          | `var(--color-neutral-light-20)`                   |
 * | `--color-gauge-pointer-color`            | Cor do ponteiro do grfico `Gauge`                                      | `var(--color-neutral-dark-70)`                    |
 * | `--color-chart-line-point-fill`          | Cor de dentro do crculo dos grficos `Line` e `Area`                   | `var(--color-neutral-light-00)`                   |
 * | **Wrapper (.po-chart-container-gauge)**  |                                                                         |                                                   |
 * | `--background-color-container-gauge`     | Cor de background do container do gauge                                 | `var(--color-neutral-light-00)`                   |
 */
@Directive()
export abstract class PoChartBaseComponent implements OnInit {
  private _literals?: PoChartLiterals;
  private setHeightGauge = false;
  private readonly language: string;

  @Input('t-id') id: string = 'myChart';

  /** Define o ttulo do grfico. */
  @Input('p-title') title?: string;

  /**
   * @description
   *
   * Define os elementos do grfico que sero criados dinamicamente.
   */
  @Input('p-series') series: Array<PoChartSerie>;

  /**
   * @description
   *
   * Define o valor do grfico do tipo `Gauge` quando utliza as propriedades `From` `To`.
   */
  @Input('p-value-gauge-multiple') valueGaugeMultiple?: number;

  /**
   * @optional
   *
   * @description
   *
   * Define os nomes das categorias que sero plotadas no eixo X do grfico caso seja do tipo `bar`, ou ento nos eixos Y do grid de grficos dos tipos `area`, `columnn` e `line`.
   *
   * > Grficos do tipo `bar` dimensionam a rea do grfico de acordo com a largura do maior texto de categorias. No entanto,  uma boa prtica optar por palavras curtas para que a leitura do grfico no seja prejudicada.
   *
   * > Caso no seja especificado um valor para a categoria, ser plotado um hfen na categoria referente a cada srie.
   */
  @Input('p-categories') categories?: Array<string>;

  /**
   * @optional
   *
   * @description
   *
   * Essa propriedade permite que o desenvolvedor adicione aes customizadas no popup do header, oferecendo mais flexibilidade e controle sobre as interaes do componente.
   */
  @Input('p-custom-actions') customActions?: Array<PoPopupAction>;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as configuraes usadas no `po-chart`.
   *
   *  possvel, por exemplo, definir as configuraes de exibio das legendas,
   * configurar os eixos(*axis*) para os grficos dos tipos `area`, `line`, `column` e `bar` da seguinte forma:
   *
   * ```
   *  chartOptions: PoChartOptions = {
   *    legend: true,
   *    axis: {
   *      minRange: 0,
   *      maxRange: 100,
   *      gridLines: 5,
   *    },
   *  };
   * ```
   */
  @Input('p-options') options?: PoChartOptions;

  /**
   * @optional
   *
   * @description
   *
   * Permite configurar as propriedades de exibio dos rtulos das sries no grfico.
   *
   * Essa configurao possibilita fixar os valores das sries diretamente no grfico, alterando o comportamento visual:
   * - Os valores das sries permanecem visveis, sem a necessidade de hover.
   * - O *tooltip* no ser exibido.
   * - Os marcadores (*bullets*) tero seu estilo ajustado.
   * - As outras sries ficaro com opacidade reduzida ao passar o mouse sobre a srie ativa.
   *
   * > Disponvel apenas para grficos do tipo `line`.
   *
   * #### Exemplo de utilizao:
   * ```typescript
   * dataLabel: PoChartDataLabel = {
   *   fixed: true,
   * };
   * ```
   */
  @Input('p-data-label') dataLabel?: PoChartDataLabel;

  /**
   * @optional
   *
   * @description
   *
   * Define a altura do grfico em px.
   *
   * > No caso do tipo `Gauge`, o valor padro  `300` e esse  seu valor minimo aceito. Nos outros tipos, o valor mnimo aceito nesta propriedade  200.
   *
   * @default `400`
   */
  @Input('p-height')
  set height(value: number) {
    let heightGauge = null;
    this.setHeightGauge = true;
    if (this.type === PoChartType.Gauge) {
      heightGauge = 300;
    }
    this._height = Math.max(value ?? heightGauge ?? poChartDefaultHeight, heightGauge ?? poChartMinHeight);
  }

  get height(): number {
    return this._height;
  }

  private _height: number = poChartDefaultHeight;

  /**
   * @optional
   *
   * @description
   *
   * Define o tipo de grfico.
   *
   *  possvel tambm combinar grficos dos tipos linha e coluna. Para isso, opte pela declarao de `type` conforme a interface `PoChartSerie`.
   *
   * > Note que, se houver declarao de tipo de grfico tanto em `p-type` quanto em `PochartSerie.type`, o valor `{ type }` da primeira srie anular o valor definido em `p-type`.
   *
   * Se no passado valor, o padro ser relativo  primeira srie passada em `p-series`:
   * - Se `p-series = [{ data: [1,2,3] }]`: ser `PoChartType.Column`.
   * - Se `p-series = [{ data: 1 }]`: ser `PoChartType.Pie`.
   *
   * > Veja os valores vlidos no *enum* `PoChartType`.
   */
  @Input('p-type') type: PoChartType;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `po-chart`.
   *
   * Para utilizar basta passar a literal que deseja customizar:
   *
   * ```
   *  const customLiterals: PoChartLiterals = {
   *    downloadCSV: 'Obter CSV',
   *  };
   * ```
   *
   * E para carregar a literal customizada, basta apenas passar o objeto para o componente.
   *
   * ```
   * <po-chart
   *   [p-literals]="customLiterals">
   * </po-chart>
   * ```
   *
   * > O objeto padro de literais ser traduzido de acordo com o idioma do
   * [`PoI18nService`](/documentation/po-i18n) ou do browser.
   */
  @Input('p-literals') set literals(value: PoChartLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...poChartLiteralsDefault[poLocaleDefault],
        ...poChartLiteralsDefault[this.language],
        ...value
      };
    } else {
      this._literals = poChartLiteralsDefault[this.language];
    }
  }

  get literals() {
    return this._literals || poChartLiteralsDefault[this.language];
  }

  /**
   * @optional
   *
   * @description
   *
   * Evento executado quando o usurio clicar sobre um elemento do grfico.
   *
   * O evento emitir o seguinte parmetro:
   * - *donut* e *pie*: um objeto contendo a categoria e valor da srie.
   * - *area*, *line*, *column* e *bar*: um objeto contendo o nome da srie, valor e categoria do eixo do grfico.
   */
  @Output('p-series-click')
  seriesClick: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento executado quando o usurio passar o *mouse* sobre um elemento do grfico.
   *
   * O evento emitir o seguinte parmetro de acordo com o tipo de grfico:
   * - *donut* e *pie*: um objeto contendo a categoria e valor da srie.
   * - *area*, *line*, *column* e *bar*: um objeto contendo a categoria, valor da srie e categoria do eixo do grfico.
   */
  @Output('p-series-hover')
  seriesHover: EventEmitter<any> = new EventEmitter<any>();

  constructor(languageService: PoLanguageService) {
    this.language = languageService.getShortLanguage();
  }

  ngOnInit(): void {
    if (this.type === PoChartType.Gauge && !this.setHeightGauge) {
      this._height = 300;
    }
  }
}
