import { Component, input, Input } from '@angular/core';
import { convertToBoolean } from '../../utils/util';
import { poFieldContainerLiterals } from '../po-field/po-field-container/po-field-container-literals';

/**
 * @docsPrivate
 *
 * Componente de uso interno.
 */

@Component({
  selector: 'po-label',
  templateUrl: './po-label.component.html',
  standalone: false
})
export class PoLabelComponent {
  private _disabled?: boolean = false;
  private _field?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Indica se o label est desativado.
   *
   * @default `false`
   */
  @Input('p-disabled') set disabled(value: boolean) {
    this._disabled = <any>value === '' ? true : convertToBoolean(value);
  }
  get disabled(): boolean {
    return this._disabled;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica se o label ser de um field.
   *
   * @default `false`
   */
  @Input('p-field') set field(value: boolean) {
    this._field = <any>value === '' ? true : convertToBoolean(value);
  }

  get field(): boolean {
    return this._field;
  }

  /** Indica o campo vinculado ao label */
  @Input('p-for') for: string;

  /** Label do campo. */
  @Input('p-label') label?: string;

  /** Indica o tipo do campo vinculado ao label */
  @Input('p-requirement') requirement?: string;

  /**
   * @optional
   *
   * @description
   *
   * Habilita a quebra automtica do texto. Quando ativada, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * @default `false`
   */
  textWrap = input<boolean>(false, { alias: 'p-text-wrap' });

  isRequiredLiteral(value: string): boolean {
    return Object.values(poFieldContainerLiterals).some(
      literals => literals.required.toLowerCase() === value.toLowerCase()
    );
  }
}
