/**
 * @usedBy PoDynamicFormComponent
 *
 * @description
 *
 * Enum para definio do tipo de componente a ser renderizado.
 */
export enum ForceBooleanComponentEnum {
  /** Fora a renderizao de um po-switch */
  switch = 'switch',

  /** Fora a renderizao de um po-checkbox */
  checkbox = 'checkbox'
}

/**
 * @usedBy PoDynamicFormComponent
 *
 * @description
 *
 * Enum para definio do tipo de componente a ser renderizado.
 */
export enum ForceOptionComponentEnum {
  /** Fora a renderizao de um po-radio-group independente da quantidade do opes */
  radioGroup = 'radioGroup',

  /** Fora a renderizao de um po-select independente da quantidade do opes */
  select = 'select'
}
