import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  HostListener,
  Input,
  inject,
  AfterViewInit,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR } from '@angular/forms';

import { maxFailed, minFailed } from '../validators';

import { PoNumberBaseComponent } from './po-number-base.component';

import { uuid } from '../../../utils/util';

/**
 * @docsExtends PoInputBaseComponent
 *
 * @description
 *
 * O `po-number`  um input especfico para receber apenas nmeros.
 *  possvel configurar um valor mnimo, mximo e um step com p-min, p-max e p-step,
 * respectivamente.
 *
 * @example
 *
 * <example name="po-number-basic" title="PO Number Basic">
 *  <file name="sample-po-number-basic/sample-po-number-basic.component.html"> </file>
 *  <file name="sample-po-number-basic/sample-po-number-basic.component.ts"> </file>
 * </example>
 *
 * <example name="po-number-labs" title="PO Number Labs">
 *  <file name="sample-po-number-labs/sample-po-number-labs.component.html"> </file>
 *  <file name="sample-po-number-labs/sample-po-number-labs.component.ts"> </file>
 * </example>
 *
 * <example name="po-number-calculate" title="PO Number - Calculate">
 *  <file name="sample-po-number-calculate/sample-po-number-calculate.component.html"> </file>
 *  <file name="sample-po-number-calculate/sample-po-number-calculate.component.ts"> </file>
 * </example>
 */
@Component({
  selector: 'po-number',
  templateUrl: './po-number.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PoNumberComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => PoNumberComponent),
      multi: true
    }
  ],
  standalone: false
})
export class PoNumberComponent extends PoNumberBaseComponent implements AfterViewInit, OnChanges {
  /**
   * @optional
   *
   * @description
   *
   * Intervalo.
   *
   * @default 1
   */
  @Input('p-step') step?: string = '1';

  /** Valor mnimo.
   *
   * > Quando o valor mnimo for um nmero com decimais aconselha-se utilizar junto da propriedade `p-step` tambm passando a ela um valor decimal.
   */
  min?: number;
  @Input('p-min') set setMin(min: number) {
    this.min = !isNaN(min) ? min : undefined;
    this.validateModel();
  }

  /** Valor mximo.
   *
   * > Quando o valor mximo for um nmero com decimais aconselha-se utilizar junto da propriedade `p-step` tambm passando a ela um valor decimal.
   */
  max?: number;
  @Input('p-max') set setMax(max: number) {
    this.max = !isNaN(max) ? max : undefined;
    this.validateModel();
  }

  id = `po-number[${uuid()}]`;

  /* istanbul ignore next */
  constructor() {
    const el = inject(ElementRef);
    const cd = inject(ChangeDetectorRef);

    super(el, cd);
  }

  @HostListener('wheel', ['$event'])
  onWheel(event: Event) {
    event.preventDefault();
  }

  ngAfterViewInit() {
    if (this.inputEl?.nativeElement?.closest('.components-form-custom-template')) {
      setTimeout(() => {
        if (this.inputEl?.nativeElement?.classList?.contains('enable-append-box')) {
          this.appendBox = true;
        }
      }, 300);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.label) {
      this.displayAdditionalHelp = false;
    }
  }

  extraValidation(abstractControl: AbstractControl): { [key: string]: any } {
    // Verifica se j possui algum error pattern padro.
    this.errorPattern = this.errorPattern !== 'Valor Invlido' ? this.errorPattern : '';

    if (minFailed(this.min, abstractControl.value)) {
      return {
        min: {
          valid: false
        }
      };
    }

    if (maxFailed(this.max, abstractControl.value)) {
      return {
        max: {
          valid: false
        }
      };
    }

    if (this.invalidInputValueOnBlur) {
      this.errorPattern = this.errorPattern || 'Valor Invlido';

      return {
        number: {
          valid: false
        }
      };
    }

    return null;
  }

  getErrorPatternMessage() {
    return this.errorPattern !== '' && this.containsInvalidClass() ? this.errorPattern : '';
  }

  private containsInvalidClass(): boolean {
    return (
      (this.el.nativeElement.classList.contains('ng-invalid') &&
        this.el.nativeElement.classList.contains('ng-dirty') &&
        (this.inputEl.nativeElement.value !== '' ||
          (this.showErrorMessageRequired && (this.required || this.hasValidatorRequired)))) ||
      this.invalidInputValueOnBlur
    );
  }
}
