import { ChangeDetectorRef, Directive, EventEmitter, input, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { AbstractControl, ControlValueAccessor, Validator, Validators } from '@angular/forms';

import {
  convertDateToISODate,
  convertDateToISOExtended,
  convertIsoToDate,
  convertToBoolean,
  formatYear,
  getDefaultSizeFn,
  isTypeof,
  replaceFormatSeparator,
  setYearFrom0To100,
  validateDateRange,
  validateSizeFn
} from '../../../utils/util';
import { PoMask } from '../po-input/po-mask';
import { dateFailed, requiredFailed } from './../validators';

import { Observable, Subscription, switchMap } from 'rxjs';
import { PoFieldSize } from '../../../enums/po-field-size.enum';
import { poLocaleDefault } from '../../../services/po-language/po-language.constant';
import { PoLanguageService } from '../../../services/po-language/po-language.service';
import { PoDatepickerIsoFormat } from './enums/po-datepicker-iso-format.enum';
import { PoHelperOptions } from '../../po-helper';

const poDatepickerFormatDefault: string = 'dd/mm/yyyy';

/**
 * @description
 *
 * O `po-datepicker`  um componente especfico para manipulao de datas permitindo a digitao e / ou seleo.
 *
 * O formato de exibio da data, ou seja, o formato que  apresentado ao usurio  o dd/mm/yyyy,
 * mas podem ser definidos outros padres (veja mais na propriedade `p-format`).
 *
 * O idioma padro do calendrio ser exibido de acordo com o navegador, caso tenha necessidade de alterar
 * use a propriedade `p-locale`.
 *
 * O datepicker aceita trs formatos de data: o E8601DZw (yyyy-mm-ddThh:mm:ss+|-hh:mm), o E8601DAw (yyyy-mm-dd) e o
 * Date padro do Javascript.
 *
 * > Por padro, o formato de sada do *model* se ajustar conforme o formato de entrada. Se por acaso precisar controlar o valor de sada,
 * a propriedade `p-iso-format` prov esse controle independentemente do formato de entrada. Veja abaixo os formatos disponveis:
 *
 * - Formato de entrada e sada (E8601DZw) - `'2017-11-28T00:00:00-02:00'`;
 *
 * - Formato de entrada e sada (E8601DAw) - `'2017-11-28'`;
 *
 * - Formato de entrada (Date) - `new Date(2017, 10, 28)` e sada (E8601DAw) - `'2017-11-28'`;
 *
 * **Importante:**
 *
 * - Para utilizar datas com ano inferior a 100, verificar o comportamento do [`new Date`](https://www.w3schools.com/js/js_dates.asp)
 * e utilizar o mtodo [`setFullYear`](https://www.w3schools.com/jsref/jsref_setfullyear.asp).
 * - Caso a data esteja invlida, o `model` receber **'Data invlida'**.
 * - Caso o `input` esteja passando um `[(ngModel)]`, mas no tenha um `name`, ento ir ocorrer um erro
 * do prprio Angular (`[ngModelOptions]="{standalone: true}"`).
 *
 * Exemplo:
 *
 * ```
 * <po-datepicker
 *   [(ngModel)]="pessoa.nome"
 *   [ngModelOptions]="{standalone: true}"
 * </po-datepicker>
 * ```
 *
 * > No esquea de importar o `FormsModule` em seu mdulo, tal como para utilizar o `input default`.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS): <br>
 * Obs: S  possvel realizar alteraes ao adicionar a classe `.po-input`
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                     |
 * |----------------------------------------|-------------------------------------------------------|--------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                  |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                       |
 * | `--font-size`                          | Tamanho da fonte                                      | `var(--font-size-default)`                       |
 * | `--text-color-placeholder` &nbsp;      | Cor principal do texto do placeholder                 | `var(--color-neutral-light-30)`                  |
 * | `--color`                              | Cor principal do datepicker                           | `var(--color-neutral-dark-70)`                   |
 * | `--background`                         | Cor de background                                     | `var(--color-neutral-light-05)`                  |
 * | `--padding`                            | Preenchimento                                         | `0 0.5rem`                                       |
 * | `--text-color`                         | Cor do texto                                          | `var(--color-neutral-dark-90)`                   |
 * | **Hover**                              |                                                       |                                                  |
 * | `--color-hover`                        | Cor principal no estado hover                         | `var(--color-brand-01-dark)`                     |
 * | `--background-hover`                   | Cor de background no estado hover                     | `var(--color-brand-01-lightest)`                 |
 * | **Focused**                            |                                                       |                                                  |
 * | `--color-focused`                      | Cor principal no estado de focus                      | `var(--color-action-default)`                    |
 * | `--outline-color-focused`              | Cor do outline do estado de focus                     | `var(--color-action-focus)`                      |
 * | **Disabled**                           |                                                       |                                                  |
 * | `--color-disabled`                     | Cor principal no estado disabled                      | `var(--color-neutral-light-30)`                  |
 * | `--background-disabled`                | Cor de background no estado disabled &nbsp;           | `var(--color-neutral-light-20)`                  |
 * | `--text-color-disabled`                | Cor do texto no estado disabled                       | `var(--color-neutral-dark-70)`                   |
 *
 */
@Directive()
export abstract class PoDatepickerBaseComponent implements ControlValueAccessor, OnInit, OnDestroy, Validator {
  // Propriedade interna que define se o cone de ajuda adicional ter cursor clicvel (evento) ou padro (tooltip).
  @Input() additionalHelpEventTrigger: string | undefined;

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Exibe um cone de ajuda adicional, com o texto desta propriedade sendo passado para o popover do componente `po-helper`.
   * **Como boa prtica, indica-se utilizar um texto com at 140 caracteres.**
   * > Requer um recuo mnimo de 8px se o componente estiver prximo  lateral da tela.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   *
   */
  @Input('p-additional-help-tooltip') additionalHelpTooltip?: string;

  /**
   * @optional
   *
   * @description
   *
   * Aplica foco no elemento ao ser iniciado.
   *
   * > Caso mais de um elemento seja configurado com essa propriedade, apenas o ltimo elemento declarado com ela ter o foco.
   *
   * @default `false`
   */
  @Input({ alias: 'p-auto-focus', transform: convertToBoolean }) autoFocus: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Funo executada para realizar a validao assncrona personalizada.
   * Executada ao disparar o output `change`.
   *
   * @param value Valor atual preenchido no campo.
   *
   * @returns Retorna Observable com o valor `true` para sinalizar o erro `false` para indicar que no h erro.
   */
  @Input('p-error-async') errorAsync: (value) => Observable<boolean>;

  /* Nome do componente datepicker. */
  @Input('name') name: string;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `p-required`;
   * - No possuir `p-help` e/ou `p-label`.
   *
   * @default `false`
   */
  @Input('p-optional') optional: boolean;

  /**
   * Mensagem apresentada quando a data for invlida ou fora do perodo.
   *
   * > Por padro, esta mensagem no  apresentada quando o campo estiver vazio, mesmo que ele seja requerido.
   * Para exibir a mensagem com o campo vazio, utilize a propriedade `p-required-field-error-message` em conjunto.
   */
  @Input('p-error-pattern') errorPattern?: string = '';

  /**
   * @optional
   *
   * @description
   *
   * Limita a exibio da mensagem de erro a duas linhas e exibe um tooltip com o texto completo.
   *
   * > Caso essa propriedade seja definida como `true`, a mensagem de erro ser limitada a duas linhas
   * e um tooltip ser exibido ao passar o mouse sobre a mensagem para mostrar o contedo completo.
   *
   * @default `false`
   */
  @Input('p-error-limit') errorLimit: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Exibe a mensagem setada na propriedade `p-error-pattern` se o campo estiver vazio e for requerido.
   *
   * > Necessrio que a propriedade `p-required` esteja habilitada.
   *
   * @default `false`
   */
  @Input('p-required-field-error-message') showErrorMessageRequired: boolean = false;

  /**
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Evento disparado ao clicar no cone de ajuda adicional.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Output('p-additional-help') additionalHelp: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao sair do campo.
   */
  @Output('p-blur') onblur: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao alterar valor do campo.
   */
  @Output('p-change') onchange: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   * Evento disparado quando uma tecla  pressionada enquanto o foco est no componente.
   * Retorna um objeto `KeyboardEvent` com informaes sobre a tecla.
   */
  @Output('p-keydown') keydown: EventEmitter<KeyboardEvent> = new EventEmitter<KeyboardEvent>();

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define as opes do componente de ajuda (po-helper) que ser exibido ao lado do label quando a propriedade `p-label` for definida, ou, ao lado do componente na ausncia da propriedade `p-label`.
   * > Para mais informaes acesse: https://po-ui.io/documentation/po-helper.
   *
   * > Ao configurar esta propriedade, o antigo cone de ajuda adicional (`p-additional-help-tooltip` e `p-additional-help`) ser ignorado.
   */
  poHelperComponent = input<PoHelperOptions | string>(undefined, { alias: 'p-helper' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Habilita a quebra automtica do texto da propriedade `p-label`. Quando `p-label-text-wrap` for verdadeiro, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * @default `false`
   */
  labelTextWrap = input<boolean>(false, { alias: 'p-label-text-wrap' });

  offset: number;
  protected firstStart = true;
  protected hour: string = 'T00:00:00-00:00';
  protected isExtendedISO: boolean = false;
  protected objMask: any;
  protected onChangeModel: any = null;
  protected validatorChange: any;
  protected onTouchedModel: any = null;
  protected shortLanguage: string;
  protected isInvalid: boolean;
  protected hasValidatorRequired: boolean;

  private _format?: string = poDatepickerFormatDefault;
  private _isoFormat: PoDatepickerIsoFormat;
  private _maxDate: Date;
  private _minDate: Date;
  private _noAutocomplete?: boolean = false;
  private _placeholder?: string = '';
  private previousValue: any;
  private _size?: string = undefined;
  private subscription: Subscription = new Subscription();
  private _date: Date;

  /**
   * @optional
   *
   * @description
   *
   * Define a propriedade nativa `autocomplete` do campo como `off`.
   *
   * @default `false`
   */
  @Input('p-no-autocomplete') set noAutocomplete(value: boolean) {
    this._noAutocomplete = convertToBoolean(value);
  }

  get noAutocomplete() {
    return this._noAutocomplete;
  }

  /**
   * @optional
   *
   * @description
   *
   * Mensagem que aparecer enquanto o campo no estiver preenchido.
   */
  @Input('p-placeholder') set placeholder(placeholder: string) {
    this._placeholder = isTypeof(placeholder, 'string') ? placeholder : '';
  }

  get placeholder() {
    return this._placeholder;
  }

  /** Desabilita o campo. */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  disabled?: boolean = false;
  @Input('p-disabled') set setDisabled(disabled: string) {
    this.disabled = disabled === '' ? true : convertToBoolean(disabled);

    this.validateModel(convertDateToISOExtended(this.date, this.hour));
  }

  /** Torna o elemento somente leitura. */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  readonly?: boolean = false;
  @Input('p-readonly') set setReadonly(readonly: string) {
    this.readonly = readonly === '' ? true : convertToBoolean(readonly);
  }

  /**
   * @optional
   *
   * @description
   *
   * Define que o campo ser obrigatrio.
   *
   * @default `false`
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  required?: boolean = false;
  @Input('p-required') set setRequired(required: string) {
    this.required = required === '' ? true : convertToBoolean(required);

    this.validateModel(convertDateToISOExtended(this.date, this.hour));
  }

  /**
   * Define se a indicao de campo obrigatrio ser exibida.
   *
   * > No ser exibida a indicao se:
   * - No possuir `p-help` e/ou `p-label`.
   */
  @Input('p-show-required') showRequired: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho do componente:
   * - `small`: altura do input como 32px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura do input como 44px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  /** Habilita ao para limpar o campo. */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  clean?: boolean = false;
  @Input('p-clean') set setClean(clean: string) {
    this.clean = clean === '' ? true : convertToBoolean(clean);
  }

  /**
   * @optional
   *
   * @description
   *
   * Define uma data mnima para o `po-datepicker`.
   */
  @Input('p-min-date') set minDate(value: string | Date) {
    if (value instanceof Date) {
      const year = value.getFullYear();

      const date = new Date(year, value.getMonth(), value.getDate(), 0, 0, 0);
      setYearFrom0To100(date, year);

      this._minDate = date;
    } else {
      this._minDate = convertIsoToDate(value, true, false);
    }

    this.validateModel(convertDateToISOExtended(this.date, this.hour));
  }

  get minDate() {
    return this._minDate;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define uma data mxima para o `po-datepicker`.
   */
  @Input('p-max-date') set maxDate(value: string | Date) {
    if (value instanceof Date) {
      const year = value.getFullYear();

      const date = new Date(year, value.getMonth(), value.getDate(), 23, 59, 59);
      setYearFrom0To100(date, year);

      this._maxDate = date;
    } else {
      this._maxDate = convertIsoToDate(value, false, true);
    }

    this.validateModel(convertDateToISOExtended(this.date, this.hour));
  }

  get maxDate() {
    return this._maxDate;
  }

  /**
   * @optional
   *
   * @description
   *
   * Formato de exibio da data.
   *
   * Valores vlidos:
   *  - `dd/mm/yyyy`
   *  - `mm/dd/yyyy`
   *  - `yyyy/mm/dd`
   *
   * @default `dd/mm/yyyy`
   */
  @Input('p-format') set format(value: string) {
    if (value) {
      value = value.toLowerCase();
      if (value.match(/dd/) && value.match(/mm/) && value.match(/yyyy/)) {
        this._format = value;
        this.objMask = this.buildMask(
          replaceFormatSeparator(this.format, this.languageService.getDateSeparator(this.locale))
        );
        this.refreshValue(this.date);
        return;
      }
    }
    this._format = poDatepickerFormatDefault;
    this.objMask = this.buildMask(
      replaceFormatSeparator(this.format, this.languageService.getDateSeparator(this.locale))
    );
  }

  get format() {
    return this._format;
  }

  /**
   * @optional
   *
   * @description
   *
   * Padro de formatao para sada do *model*, independentemente do formato de entrada.
   *
   * > Veja os valores vlidos no *enum* `PoDatepickerIsoFormat`.
   */
  @Input('p-iso-format') set isoFormat(value: PoDatepickerIsoFormat) {
    if (Object.values(PoDatepickerIsoFormat).includes(value)) {
      this._isoFormat = value;
      this.isExtendedISO = value === PoDatepickerIsoFormat.Extended;
    }
  }

  get isoFormat() {
    return this._isoFormat;
  }

  /**
   * @optional
   *
   * @description
   *
   * Idioma do Datepicker.
   *
   * > O locale padro sera recuperado com base no [`PoI18nService`](/documentation/po-i18n) ou *browser*.
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  _locale?: string;
  @Input('p-locale') set locale(value: string) {
    if (value) {
      this._locale = value.length >= 2 ? value : poLocaleDefault;
      this.objMask = this.buildMask(
        replaceFormatSeparator(this.format, this.languageService.getDateSeparator(this.locale))
      );
    } else {
      this._locale = this.shortLanguage;
      this.objMask = this.buildMask(
        replaceFormatSeparator(this.format, this.languageService.getDateSeparator(this.locale))
      );
    }
    this.refreshValue(this.date);
  }
  get locale() {
    return this._locale || this.shortLanguage;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define que o `calendar` e/ou tooltip (`p-additional-help-tooltip` e/ou `p-error-limit`) sero includos no body da
   * pgina e no dentro do componente. Essa opo pode ser necessria em cenrios com containers que possuem scroll ou
   * overflow escondido, garantindo o posicionamento correto de ambos prximo ao elemento.
   *
   * > Quando utilizado com `p-helper`, leitores de tela como o NVDA podem no ler o contedo do popover.
   *
   * @default `false`
   */
  @Input({ alias: 'p-append-in-body', transform: convertToBoolean }) appendBox: boolean = false;

  constructor(
    protected languageService: PoLanguageService,
    protected cd: ChangeDetectorRef
  ) {}

  set date(value: any) {
    this._date = typeof value === 'string' ? convertIsoToDate(value, false, false) : value;
  }

  get date() {
    return this._date;
  }

  ngOnInit() {
    this.offset = new Date().getTimezoneOffset();
    this.formatTimezoneAndHour(this.offset);
    // Classe de mscara
    this.objMask = this.buildMask(
      replaceFormatSeparator(this.format, this.languageService.getDateSeparator(this.locale))
    );
  }

  ngOnDestroy(): void {
    this.subscription?.unsubscribe();
  }

  // Converte um objeto string em Date
  getDateFromString(dateString: string) {
    const day = parseInt(dateString.substring(this.format.indexOf('d'), this.format.indexOf('d') + 2), 10);
    const month = parseInt(dateString.substring(this.format.indexOf('m'), this.format.indexOf('m') + 2), 10) - 1;
    const year = parseInt(dateString.substring(this.format.indexOf('y'), this.format.indexOf('y') + 4), 10);

    const date = new Date(year, month, day);

    setYearFrom0To100(date, year);

    return date.getFullYear() === year && date.getMonth() === month && date.getDate() === day ? date : null;
  }

  // Formata a data.
  formatToDate(value: Date) {
    let dateFormatted = this.format;

    dateFormatted = dateFormatted.replace('dd', ('0' + value.getDate()).slice(-2));
    dateFormatted = dateFormatted.replace('mm', ('0' + (value.getMonth() + 1)).slice(-2));
    dateFormatted = dateFormatted.replace('yyyy', formatYear(value.getFullYear()));

    return dateFormatted;
  }

  // Mtodo responsvel por controlar o modelo.
  controlModel(date: Date) {
    this.date = date;
    if (date && this.isExtendedISO) {
      this.callOnChange(convertDateToISOExtended(this.date, this.hour));
    } else if (date && !this.isExtendedISO) {
      this.callOnChange(convertDateToISODate(this.date));
    } else {
      date === undefined ? this.callOnChange('') : this.callOnChange('Data invlida');
    }
  }

  // Executa a funo onChange
  callOnChange(value: any, retry: boolean = true) {
    if (this.onChangeModel && value !== this.previousValue) {
      this.onChangeModel(value);
      this.previousValue = value;
    } else if (retry) {
      setTimeout(() => this.callOnChange(value, false));
    }
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar os estados de habilitado via forms api
  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.cd.markForCheck();
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnChange(func: any): void {
    this.onChangeModel = func;
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnTouched(func: any): void {
    this.onTouchedModel = func;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  validate(c: AbstractControl): { [key: string]: any } {
    // Verifica se j possui algum error pattern padro.
    this.errorPattern =
      this.errorPattern !== 'Data invlida' && this.errorPattern !== 'Data fora do perodo' ? this.errorPattern : '';

    if (!this.hasValidatorRequired && this.showErrorMessageRequired && c.hasValidator(Validators.required)) {
      this.hasValidatorRequired = true;
    }

    if (dateFailed(c.value)) {
      this.errorPattern = this.errorPattern || 'Data invlida';

      this.cd?.markForCheck();
      return {
        date: {
          valid: false
        }
      };
    }

    if (requiredFailed(this.required, this.disabled, c.value)) {
      this.cd?.markForCheck();
      return {
        required: {
          valid: false
        }
      };
    }

    if (this.date && !validateDateRange(this.date, this._minDate, this._maxDate)) {
      this.errorPattern = this.errorPattern || 'Data fora do perodo';

      this.cd?.markForCheck();
      return {
        date: {
          valid: false
        }
      };
    }

    if (this.errorPattern !== '') {
      this.subscription?.unsubscribe();
      this.subscription = c.statusChanges
        .pipe(
          switchMap(status => {
            if (status === 'INVALID') {
              this.cd?.markForCheck();
            }
            return [];
          })
        )
        .subscribe();
    }

    return null;
  }

  protected validateModel(model: any) {
    if (this.validatorChange) {
      this.validatorChange(model);
    }
  }

  // Retorna um objeto do tipo PoMask com a mascara configurada.
  protected buildMask(format: string = this.format) {
    let mask = format.toUpperCase();

    mask = mask.replace(/DD/g, '99');
    mask = mask.replace(/MM/g, '99');
    mask = mask.replace(/YYYY/g, '9999');

    return new PoMask(mask, true);
  }

  formatTimezoneAndHour(offset: number) {
    const offsetAbsolute = Math.abs(offset);
    const timezone =
      (offset < 0 ? '+' : '-') +
      ('00' + Math.floor(offsetAbsolute / 60)).slice(-2) +
      ':' +
      ('00' + (offsetAbsolute % 60)).slice(-2);
    this.hour = 'T00:00:00' + timezone;
  }

  abstract writeValue(value: any): void;

  abstract refreshValue(value: Date): void;
}
