/**
 * @usedBy PoTagComponent
 *
 * @description
 *
 * Define os tipos disponveis para o `po-tag`.
 */
export enum PoTagType {
  /** Erro, perigo, problema ou aviso crtico. */
  Danger = 'danger',

  /** Informativo ou explicativo. */
  Info = 'info',

  /** Confirmao, resultados positivos ou xito. */
  Success = 'success',

  /** Aviso ou advertncia. */
  Warning = 'warning',

  /** De uso geral, quando os tipos Info, Warning, Success e Danger no atendem a necessidade. */
  Neutral = 'neutral'
}
