/**
 * @usedBy PoButtonComponent, PoWidgetComponent
 *
 * @description
 *
 * Estilos disponveis do button.
 */
export enum PoButtonKind {
  /** Estilo primrio, usado para aes principais que requerem maior destaque. */
  primary = 'primary',

  /** Estilo secundrio, usado como padro, para aes comuns. */
  secondary = 'secondary',

  /** Estilo tercirio, ideal para aes menos importantes, sem fundo preenchido. */
  tertiary = 'tertiary'
}
