import {
  ChangeDetectorRef,
  Directive,
  EventEmitter,
  input,
  Input,
  OnDestroy,
  Output,
  TemplateRef,
  ViewChild
} from '@angular/core';
import { AbstractControl, ControlValueAccessor, Validator, Validators } from '@angular/forms';

import { Subscription, switchMap } from 'rxjs';
import { PoFieldSize } from '../../../enums/po-field-size.enum';
import { convertToBoolean, getDefaultSizeFn, validateSizeFn } from '../../../utils/util';
import { ErrorAsyncProperties } from '../shared/interfaces/error-async-properties.interface';
import { maxlengpoailed, minlengpoailed, patternFailed, requiredFailed } from './../validators';
import { PoMask } from './po-mask';
import { PoHelperComponent, PoHelperOptions } from '../../po-helper';
import { PoFieldContainerComponent } from '../po-field-container';

/**
 * @description
 *
 * Este  um componente baseado em input, com vrias propriedades do input nativo e outras
 * propriedades extras como: mscara, pattern, mensagem de erro e etc.
 * Voc deve informar a varivel que contm o valor como [(ngModel)]="variavel", para que o
 * input receba o valor da varivel e para que ela receba as alteraes do valor (two-way-databinding).
 * A propriedade name  obrigatria para que o formulrio e o model funcionem corretamente.
 *
 * Importante:
 *
 * - Caso o input tenha um [(ngModel)] sem o atributo name, ocorrer um erro de angular.
 * Ento voc precisa informar o atributo name ou o atributo [ngModelOptions]="{standalone: true}".
 * Exemplo: [(ngModel)]="pessoa.nome" [ngModelOptions]="{standalone: true}".
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS): <br>
 * Obs: S  possvel realizar alteraes ao adicionar a classe `.po-input`
 *
 * > Para correto alinhamento  recomendado o uso das classes de espaamento do [Grid System](https://po-ui.io/guides/grid-system).
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                    |
 * |----------------------------------------|-------------------------------------------------------|-------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                 |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                      |
 * | `--font-size`                          | Tamanho da fonte                                      | `var(--font-size-default)`                      |
 * | `--text-color-placeholder`             | Cor do texto placeholder                              | `var(--color-neutral-light-30)`                 |
 * | `--color`                              | Cor pincipal do input                                 | `var(--color-neutral-dark-70)`                  |
 * | `--background`                         | Cor de background                                     | `var(--color-neutral-light-05)`                 |
 * | `--padding`                            | Preenchimento                                         |  `0 0.5rem`                                     |
 * | `--text-color`                         | Cor do texto                                          | `var(--color-neutral-dark-90)`                  |
 * | **Hover**                              |                                                       |                                                 |
 * | `--color-hover`                        | Cor principal no estado hover                         | `var(--color-brand-01-dark)`                    |
 * | `--background-hover`                   | Cor de background no estado hover                     | `var(--color-brand-01-lightest)`                |
 * | **Focused**                            |                                                       |                                                 |
 * | `--color-focused`                      | Cor principal no estado de focus                      | `var(--color-action-default)`                   |
 * | `--outline-color-focused`              | Cor do outline do estado de focus                     | `var(--color-action-focus)`                     |
 * | **Disabled**                           |                                                       |                                                 |
 * | `--color-disabled`                     | Cor principal no estado disabled                      | `var(--color-neutral-light-30)`                 |
 * | `--background-disabled`                | Cor de background no estado disabled                  | `var(--color-neutral-light-20)`                 |
 * | `--text-color-disabled`                | Cor do texto no estado disabled                       | `var(--color-neutral-dark-70)`                  |
 *
 * <br>
 */
@Directive()
export abstract class PoInputBaseComponent implements ControlValueAccessor, Validator, OnDestroy {
  @ViewChild('fieldContainer', { read: PoFieldContainerComponent, static: false })
  fieldContainer?: PoFieldContainerComponent;
  @ViewChild('helperEl', { read: PoHelperComponent, static: false }) helperEl?: PoHelperComponent;
  // Propriedade interna que define se o cone de ajuda adicional ter cursor clicvel (evento) ou padro (tooltip).
  @Input() additionalHelpEventTrigger: string | undefined;

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Exibe um cone de ajuda adicional, com o texto desta propriedade sendo passado para o popover do componente `po-helper`.
   * **Como boa prtica, indica-se utilizar um texto com at 140 caracteres.**
   * > Requer um recuo mnimo de 8px se o componente estiver prximo  lateral da tela.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Input('p-additional-help-tooltip') additionalHelpTooltip?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define que o popover (`p-helper` e/ou `p-error-limit`) ser includo no body da pgina e no
   * dentro do componente. Essa opo pode ser necessria em cenrios com containers que possuem scroll ou overflow
   * escondido, garantindo o posicionamento correto do tooltip prximo ao elemento.
   *
   * > Quando utilizado com `p-helper`, leitores de tela como o NVDA podem no ler o contedo do popover.
   *
   * @default `false`
   */
  @Input({ alias: 'p-append-in-body', transform: convertToBoolean }) appendBox: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Aplica foco no elemento ao ser iniciado.
   *
   * > Caso mais de um elemento seja configurado com essa propriedade, apenas o ltimo elemento declarado com ela ter o foco.
   *
   * @default `false`
   */
  @Input({ alias: 'p-auto-focus', transform: convertToBoolean }) autoFocus: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define o cone que ser exibido no incio do campo.
   *
   *  possvel usar qualquer um dos cones da [Biblioteca de cones](https://po-ui.io/icons). conforme exemplo abaixo:
   * ```
   * <po-input p-icon="an an-user" p-label="PO input"></po-input>
   * ```
   * Tambm  possvel utilizar outras fontes de cones, por exemplo a biblioteca *Font Awesome*, da seguinte forma:
   * ```
   * <po-input p-icon="fa fa-podcast" p-label="PO input"></po-input>
   * ```
   * Outra opo seria a customizao do cone atravs do `TemplateRef`, conforme exemplo abaixo:
   * ```
   * <po-input [p-icon]="template" p-label="input template ionic"></po-input>
   *
   * <ng-template #template>
   *  <ion-icon style="font-size: inherit" name="heart"></ion-icon>
   * </ng-template>
   * ```
   * > Para o cone enquadrar corretamente, deve-se utilizar `font-size: inherit` caso o cone utilizado no aplique-o.
   */
  @Input('p-icon') icon?: string | TemplateRef<void>;

  /**
   * @optional
   *
   * @description
   *
   * Sempre emite as alteraes do model mesmo quando o valor atual for igual ao valor anterior.
   *
   * @default `false`
   */
  @Input({ alias: 'p-emit-all-changes', transform: convertToBoolean }) emitAllChanges: boolean = false;

  /** Rtulo do campo. */
  @Input('p-label') label?: string;

  /** Texto de apoio do campo. */
  @Input('p-help') help?: string;

  /** Nome e identificador do campo. */
  @Input('name') name: string;

  /**
   * @optional
   *
   * @description
   *
   * Realiza alguma validao customizada assncrona no componente.
   * Aconselhamos a utilizao dessa propriedade somente em componentes que no estejam
   * utilizando `Reactive Forms`. Em formulrios reativos, pode-se utilizar o prprio `asyncValidators`.
   */
  @Input('p-error-async-properties') errorAsyncProperties: ErrorAsyncProperties;

  /**
   * @description
   *
   * Mensagem que ser apresentada quando o `pattern` ou a mscara no for satisfeita.
   *
   * > Por padro, esta mensagem no  apresentada quando o campo estiver vazio, mesmo que ele seja requerido.
   * Para exibir a mensagem com o campo vazio, utilize a propriedade `p-required-field-error-message` em conjunto.
   */
  @Input('p-error-pattern') errorPattern?: string = '';

  /**
   * @optional
   *
   * @description
   *
   * Limita a exibio da mensagem de erro a duas linhas e exibe um tooltip com o texto completo.
   *
   * > Caso essa propriedade seja definida como `true`, a mensagem de erro ser limitada a duas linhas
   * e um tooltip ser exibido ao passar o mouse sobre a mensagem para mostrar o contedo completo.
   *
   * @default `false`
   */
  @Input('p-error-limit') errorLimit: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `p-required`;
   * - No possuir `p-help` e/ou `p-label`.
   *
   * @default `false`
   */
  @Input('p-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Exibe a mensagem setada na propriedade `p-error-pattern` se o campo estiver vazio e for requerido.
   *
   * > Necessrio que a propriedade `p-required` esteja habilitada.
   *
   * @default `false`
   */
  @Input('p-required-field-error-message') showErrorMessageRequired: boolean = false;

  /**
   * @description
   *
   * Converte o contedo do campo em maisulo automaticamente.
   *
   */
  @Input({ alias: 'p-upper-case', transform: convertToBoolean }) upperCase: boolean = false;

  _maskNoLengthValidation = false;
  /**
   * @description
   *
   * Define se os caracteres especiais da mscara devem ser ignorados ao validar os comprimentos mnimo (`minLength`) e mximo (`maxLength`) do campo.
   *
   * - Quando `true`, apenas os caracteres alfanumricos sero contabilizados para a validao dos comprimentos.
   * - Quando `false`, todos os caracteres, incluindo os especiais da mscara, sero considerados na validao.
   *
   * > Ser ignorado essa propriedade , caso esteja utilizando junto com a propriedade `p-mask-format-model`.
   *
   * Exemplo:
   * ```
   * <po-input
   *   p-mask="999-999"
   *   p-maxlength="6"
   *   p-minlength="4"
   *   p-mask-no-length-validation="true"
   * ></po-input>
   * ```
   * - Entrada: `123-456`  Validao ser aplicada somente aos nmeros, ignorando o caractere especial `-`.
   *
   * @default `false`
   */
  @Input('p-mask-no-length-validation') set maskNoLengthValidation(value: boolean) {
    this._maskNoLengthValidation = convertToBoolean(value);

    this.validateModel();
  }

  get maskNoLengthValidation() {
    return this._maskNoLengthValidation;
  }

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Evento disparado ao clicar no cone de ajuda adicional.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Output('p-additional-help') additionalHelp = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao sair do campo.
   */
  @Output('p-blur') blur: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao entrar do campo.
   */
  @Output('p-enter') enter: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao alterar valor e deixar o campo.
   */
  @Output('p-change') change: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao alterar valor do model.
   */
  @Output('p-change-model') changeModel: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   * Evento disparado quando uma tecla  pressionada enquanto o foco est no componente.
   * Retorna um objeto `KeyboardEvent` com informaes sobre a tecla.
   */
  @Output('p-keydown') keydown: EventEmitter<KeyboardEvent> = new EventEmitter<KeyboardEvent>();

  displayAdditionalHelp: boolean = false;
  type: string;
  onChangePropagate: any = null;
  objMask: any;
  modelLastUpdate: any;
  isInvalid: boolean;
  hasValidatorRequired = false;

  private subscription: Subscription = new Subscription();
  protected onTouched: any = null;

  protected passedWriteValue: boolean = false;
  protected validatorChange: any;

  private _maxlength?: number;
  private _minlength?: number;
  private _noAutocomplete?: boolean = false;
  private _placeholder?: string = '';
  private _size?: string = undefined;

  /**
   * @optional
   *
   * @description
   *
   * Define a propriedade nativa `autocomplete` do campo como `off`.
   *
   * > No componente `po-password` ser definido como `new-password`.
   *
   * Nos componentes `po-password` e `po-login` o valor padro ser `true`.
   *
   * @default `false`
   */
  @Input('p-no-autocomplete') set noAutocomplete(value: boolean) {
    this._noAutocomplete = convertToBoolean(value);
  }

  get noAutocomplete() {
    return this._noAutocomplete;
  }

  /**
   * @optional
   *
   * @description
   *
   * Mensagem que aparecer enquanto o campo no estiver preenchido.
   *
   * @default ''
   */
  @Input('p-placeholder') set placeholder(value: string) {
    this._placeholder = value || '';
  }
  get placeholder() {
    return this._placeholder;
  }

  /**
   * @description
   *
   * Se verdadeiro, desabilita o campo.
   *
   * @default `false`
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  disabled?: boolean = false;
  @Input('p-disabled') set setDisabled(disabled: string) {
    this.disabled = disabled === '' ? true : convertToBoolean(disabled);

    this.validateModel();
  }

  /** Indica que o campo ser somente leitura. */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  readonly?: boolean = false;
  @Input('p-readonly') set setReadonly(readonly: string) {
    this.readonly = readonly === '' ? true : convertToBoolean(readonly);
  }

  /**
   * @optional
   *
   * @description
   *
   * Define que o campo ser obrigatrio.
   * > Esta propriedade  desconsiderada quando o input est desabilitado `(p-disabled)`.
   *
   * @default `false`
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  required?: boolean = false;
  @Input('p-required') set setRequired(required: string) {
    this.required = required === '' ? true : convertToBoolean(required);

    this.validateModel();
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho do componente:
   * - `small`: altura do input como 32px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura do input como 44px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * Define se a indicao de campo obrigatrio ser exibida.
   *
   * > No ser exibida a indicao se:
   * - No possuir `p-help` e/ou `p-label`.
   */
  @Input('p-show-required') showRequired: boolean = false;

  /** Se verdadeiro, o campo receber um boto para ser limpo. */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  clean?: boolean = false;
  @Input('p-clean') set setClean(clean: string) {
    this.clean = clean === '' ? true : convertToBoolean(clean);
  }

  /**
   * @description
   *
   * Expresso regular para validar o campo.
   * Quando o campo possuir uma mscara `(p-mask)` ser automaticamente validado por ela, porm
   *  possvel definir um p-pattern para substituir a validao da mscara.
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  pattern?: string;
  @Input('p-pattern') set setPattern(pattern: string) {
    this.pattern = pattern;

    this.validateModel();
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica a quantidade mxima de caracteres que o campo aceita.
   */
  @Input('p-maxlength') set maxlength(value: number) {
    if (!isNaN(parseInt(<any>value, 10))) {
      this._maxlength = parseInt(<any>value, 10);

      this.validateModel();
    } else if (!value) {
      this._maxlength = undefined;

      this.validateModel();
    }
  }

  get maxlength() {
    return this._maxlength;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica a quantidade mnima de caracteres que o campo aceita.
   */
  @Input('p-minlength') set minlength(value: number) {
    if (!isNaN(parseInt(<any>value, 10))) {
      this._minlength = parseInt(<any>value, 10);

      this.validateModel();
    } else if (!value) {
      this._minlength = undefined;

      this.validateModel();
    }
  }

  get minlength() {
    return this._minlength;
  }

  /**
   * @description
   *
   * Indica uma mscara para o campo. Exemplos: (+99) (99) 99999?-9999, 99999-999, 999.999.999-99.
   * A mscara gera uma validao automtica do campo, podendo esta ser substituda por um REGEX especfico
   * atravs da propriedade p-pattern.
   * O campo ser sinalizado e o formulrio ficar invlido quando o valor informado estiver fora do padro definido,
   * mesmo quando desabilitado.
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  mask?: string = '';
  @Input('p-mask') set setMask(mask: string) {
    this.mask = mask;

    // Atualiza Mscara do Campo
    this.objMask = new PoMask(this.mask, this.maskFormatModel);
  }

  /**
   * @description
   *
   * Indica se o `model` receber o valor formatado pela mscara ou apenas o valor puro (sem formatao).
   *
   * @default `false`
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  maskFormatModel?: boolean = false;
  @Input('p-mask-format-model') set setMaskFormatModel(maskFormatModel: string) {
    this.maskFormatModel = maskFormatModel === '' ? true : convertToBoolean(maskFormatModel);

    if (this.objMask instanceof PoMask) {
      this.objMask.formatModel = this.maskFormatModel;

      this.validateModel();
    }
  }

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define as opes do componente de ajuda (po-helper) que ser exibido ao lado do label quando a propriedade `p-label` for definida, ou, ao lado do componente na ausncia da propriedade `p-label`.
   * > Para mais informaes acesse: https://po-ui.io/documentation/po-helper.
   *
   * > Ao configurar esta propriedade, o antigo cone de ajuda adicional (`p-additional-help-tooltip` e `p-additional-help`) ser ignorado.
   */
  poHelperComponent = input<PoHelperOptions | string>(undefined, { alias: 'p-helper' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Habilita a quebra automtica do texto da propriedade `p-label`. Quando `p-label-text-wrap` for verdadeiro, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * @default `false`
   */
  labelTextWrap = input<boolean>(false, { alias: 'p-label-text-wrap' });

  constructor(protected cd?: ChangeDetectorRef) {
    this.objMask = new PoMask(this.mask, this.maskFormatModel);
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  callOnChange(value: any) {
    this.updateModel(value);

    this.controlChangeModelEmitter(value);
  }

  callUpdateModelWithTimeout(value) {
    setTimeout(() => this.updateModel(value));
  }

  controlChangeModelEmitter(value: any) {
    if (this.modelLastUpdate !== value || this.emitAllChanges) {
      this.changeModel.emit(value);
      this.modelLastUpdate = value;
    }
  }

  emitAdditionalHelp() {
    if (this.label && this.isAdditionalHelpEventTriggered()) {
      this.additionalHelp.emit();
    }
  }

  getAdditionalHelpTooltip() {
    return this.isAdditionalHelpEventTriggered() ? null : this.additionalHelpTooltip;
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar os estados de habilitado via forms api
  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.cd?.markForCheck();
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnChange(func: any): void {
    this.onChangePropagate = func;
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnTouched(func: any): void {
    this.onTouched = func;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  showAdditionalHelpIcon() {
    return !!this.additionalHelpTooltip || this.isAdditionalHelpEventTriggered();
  }

  /**
   * Mtodo que exibe `p-helper` ou executa a ao definida em `p-helper{eventOnClick}` ou em `p-additionalHelp`.
   * Para isso, ser necessrio configurar uma tecla de atalho utilizando o evento `p-keydown`.
   *
   * > Exibe ou oculta o contedo do componente `po-helper` quando o componente estiver com foco.
   *
   * ```
   * // Exemplo com p-label e p-helper
   * <po-nome-component
   *  #component
   *  ...
   *  p-label="Label do componente"
   *  [p-helper]="helperOptions"
   *  (p-keydown)="onKeyDown($event, component)"
   * ></po-nome-component>
   * ```
   *
   * ```
   * ...
   * onKeyDown(event: KeyboardEvent, inp: PoNomeDoComponente): void {
   *  if (event.code === 'F9') {
   *    inp.showAdditionalHelp();
   *  }
   * }
   * ```
   */
  showAdditionalHelp(): boolean {
    this.displayAdditionalHelp = !this.displayAdditionalHelp;
    const helper = this.poHelperComponent();
    const isHelpEvt = this.isAdditionalHelpEventTriggered();
    if (!this.label && (helper || this.additionalHelpTooltip || isHelpEvt)) {
      if (isHelpEvt) {
        this.additionalHelp.emit();
      }
      if (typeof helper !== 'string' && typeof helper?.eventOnClick === 'function') {
        helper.eventOnClick();
        return;
      }
      if (this.helperEl?.helperIsVisible()) {
        this.helperEl?.closeHelperPopover();
        return;
      }
      this.helperEl?.openHelperPopover();
      return;
    }
    return this.displayAdditionalHelp;
  }

  updateModel(value: any) {
    // Quando o input no possui um formulrio, ento esta funo no  registrada
    if (this.onChangePropagate) {
      this.onChangePropagate(value);
    }
  }

  validate(c: AbstractControl): { [key: string]: any } {
    this.subscription?.unsubscribe();
    if (!this.hasValidatorRequired && this.showErrorMessageRequired && c.hasValidator(Validators.required)) {
      this.hasValidatorRequired = true;
    }

    if (requiredFailed(this.required, this.disabled, this.getScreenValue())) {
      this.isInvalid = true;
      this.cd?.markForCheck();
      return {
        required: {
          valid: false
        }
      };
    }

    if (
      maxlengpoailed(this.maxlength, this.getScreenValue(), this.maskFormatModel ? false : this.maskNoLengthValidation)
    ) {
      this.isInvalid = true;
      this.cd?.markForCheck();
      return {
        maxlength: {
          valid: false
        }
      };
    }

    if (
      minlengpoailed(this.minlength, this.getScreenValue(), this.maskFormatModel ? false : this.maskNoLengthValidation)
    ) {
      this.isInvalid = true;
      this.cd?.markForCheck();
      return {
        minlength: {
          valid: false
        }
      };
    }

    if (patternFailed(this.pattern, c.value)) {
      this.isInvalid = true;
      this.cd?.markForCheck();
      this.validatePatternOnWriteValue(c.value);
      return {
        pattern: {
          valid: false
        }
      };
    }

    if (this.errorPattern !== '') {
      this.subscription = c.statusChanges
        .pipe(
          switchMap(status => {
            if (status === 'INVALID') {
              this.isInvalid = true;
              this.cd?.markForCheck();
            }
            return [];
          })
        )
        .subscribe();
    }

    this.isInvalid = false;
    return this.extraValidation(c);
  }

  // Funo implementada do ControlValueAccessor
  writeValue(value: any) {
    this.writeValueModel(value);
    this.cd?.markForCheck();
  }

  protected validateModel() {
    if (this.validatorChange) {
      this.validatorChange();
    }
  }

  protected isAdditionalHelpEventTriggered(): boolean {
    return (
      this.additionalHelpEventTrigger === 'event' ||
      (this.additionalHelpEventTrigger === undefined && this.additionalHelp.observed)
    );
  }

  // utilizado para validar o pattern na inicializacao, fazendo dessa forma o campo fica sujo (dirty).
  private validatePatternOnWriteValue(value: string) {
    if (value && this.passedWriteValue) {
      setTimeout(() => {
        this.updateModel(value);
      });

      this.passedWriteValue = false;
    }
  }

  /**
   * Funo que atribui foco ao componente.
   *
   * Para utiliz-la  necessrio ter a instncia do componente no DOM, podendo ser utilizado o ViewChild da seguinte forma:
   *
   * ```
   * import { PoNomeDoComponenteComponent } from '@po-ui/ng-components';
   *
   * ...
   *
   * @ViewChild(PoNomeDoComponenteComponent, { static: true }) nomeDoComponente: PoNomeDoComponenteComponent;
   *
   * focusComponent() {
   *   this.nomeDoComponente.focus();
   * }
   * ```
   */
  abstract focus(): void;

  // Mtodo que receber o valor do model
  abstract writeValueModel(value: any): void;

  // Validaes do campo
  abstract extraValidation(c: AbstractControl): { [key: string]: any };

  // Deve retornar o valor do campo
  abstract getScreenValue(): string;
}
