const poUploadFormFieldDefault = 'files';

const poUploadMaxFileSize = 31457280; // 30MB
const poUploadMinFileSize = 0;

/**
 * @description
 *
 * O componente `po-upload` permite que o usurio envie arquivo(s) ao servidor e acompanhe o progresso.
 * Este componente tambm possibilita algumas configuraes como:
 *   Envio de diretrios, onde ele acessa o diretrio selecionado assim como seus sub-diretrios;
 *  - Mltipla seleo, onde o usurio pode enviar mais de um arquivo ao servidor.
 *  - Auto envio, onde o arquivo  enviado imediatamente aps a seleo do usurio, no necessitando que o usurio
 * clique em enviar.
 *  - Restries de formatos de arquivo e tamanho.
 *  - Funo de sucesso que ser disparada quando os arquivos forem enviados com sucesso.
 *  - Funo de erro que ser disparada quando houver erro no envio dos arquivos.
 *  - Permite habilitar uma rea onde os arquivos podem ser arrastados.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                      |
 * |----------------------------------------|-------------------------------------------------------|---------------------------------------------------|
 * | **TEXT SUPPORT**                       |                                                       |                                                   |
 * | `--font-family-text-support`           | Famlia tipogrfica usada no texto de suporte         | `var(--font-family-theme)`                        |
 * | `--text-color-text-support`            | Cor da fonte no texto de suporte                      | `var(--color-neutral-dark-90)`                    |
 * | **UPLOAD CONTENT**                     |                                                       |                                                   |
 * | `--background-color-content` &nbsp;    | Cor de fundo                                          | `var(--color-neutral-light-10)`                   |
 * | `--border-color-content`               | Cor da borda                                          | `var(--color-neutral-light-20)`                   |
 * | `--border-radius-content`              | Contm o valor do raio dos cantos do elemento         | `var(--border-radius-md)`                         |
 * | `--text-color-file-name`               | Cor do texto do nome do arquivo                       | `var(--color-neutral-dark-90)`                    |
 * | `--font-family-file-name`              | Famlia tipogrfica usada no texto do arquivo         | `var(--font-family-theme)`                        |
 * | `--text-color-info-bar`                | Cor do texto de informao                            | `var(--color-neutral-mid-60)`                     |
 * | `--font-family-info-bar`               | Famlia tipogrfica usada no texto de informao      | `var(--font-family-theme)`                        |
 * | **ERROR STATE**                        |                                                       |                                                   |
 * | `--background-color-content-error`     | Cor de fundo do container de erro                     | `var(--color-neutral-light-00)`                   |
 * | `--border-color-content-error`         | Cor da borda do container de erro                     | `var(--color-feedback-negative-base)`             |
 * | `--text-color-error`                   | Cor do texto do container de erro                     | `var(--color-feedback-negative-dark)`             |
 * | `--color-icon-error`                   | Cor do cone no estado de erro                        | `var(--color-feedback-negative-base)`             |
 * | `--font-family-error`                  | Famlia tipogrfica usada no texto de erro            | `var(--font-family-theme)`                        |
 * | **UPLOADED STATE**                     |                                                       |                                                   |
 * | `--background-color-content-uploaded`  | Cor de fundo do container com status de enviado       | `var(--color-neutral-light-00)`                   |
 * | `--border-color-content-uploaded`      | Cor da borda do container com status de enviado       | `var(--color-neutral-light-20)`                   |
 * | **INTERACTIVE STATE**                  |                                                       |                                                   |
 * | `--text-color-file-name-interactive`   | Cor do texto do nome do arquivo quando interativo     | `var(--color-action-default)`                     |
 * | **THUMBNAIL**                          |                                                       |                                                   |
 * | `--color-icon-thumbnail`               | Cor do cone na thumbnail                             | `var(--color-action-default)`                     |
 * | `--border-width-thumbnail`             | Tamanho da fonte na thumbnail                         | `var(--border-width-sm)`                          |
 * | `--border-radius-thumbnail`            | Contm o valor do raio dos cantos na thumbnail        | `var(--border-radius-md)`                         |
 * | `--background-color-thumbnail`         | Cor de fundo na thumbnail                             | `var(--color-neutral-light-05)`                   |
 * | **Focused**                            |                                                       |                                                   |
 * | `--outline-color-focused`              | Cor do outline do estado de focus                     | `var(--color-action-focus)`                       |
 */
@Directive()
export abstract class PoUploadBaseComponent implements ControlValueAccessor, Validator {
  // Propriedade interna que define se o cone de ajuda adicional ter cursor clicvel (evento) ou padro (tooltip).
  @Input() additionalHelpEventTrigger: string | undefined;

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Exibe um cone de ajuda adicional, com o texto desta propriedade sendo passado para o popover do componente `po-helper`.
   * **Como boa prtica, indica-se utilizar um texto com at 140 caracteres.**
   * > Requer um recuo mnimo de 8px se o componente estiver prximo  lateral da tela.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Input('p-additional-help-tooltip') additionalHelpTooltip?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define que o popover (`p-helper`) ser includo no body da pgina e no dentro do componente. Essa
   * opo pode ser necessria em cenrios com containers que possuem scroll ou overflow escondido, garantindo o
   * posicionamento correto do tooltip prximo ao elemento.
   *
   * > Quando utilizado com `p-helper`, leitores de tela como o NVDA podem no ler o contedo do popover.
   *
   * @default `false`
   */
  @Input({ alias: 'p-append-in-body', transform: convertToBoolean }) appendBox?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Aplica foco no elemento ao ser iniciado.
   *
   * > Caso mais de um elemento seja configurado com essa propriedade, apenas o ltimo elemento declarado com ela ter o foco.
   *
   * @default `false`
   */
  @Input({ alias: 'p-auto-focus', transform: convertToBoolean }) autoFocus: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define em *pixels* a altura da rea onde podem ser arrastados os arquivos. A altura mnima aceita  `160px`.
   *
   * > Esta propriedade funciona somente se a propriedade `p-drag-drop` estiver habilitada.
   *
   * @default `320`
   */
  @Input('p-drag-drop-height') dragDropHeight: number;

  /** Rtulo do campo. */
  @Input('p-label') label?: string;

  /** Texto de apoio para o campo. */
  @Input('p-help') help?: string;

  /** URL que deve ser feita a requisio com os arquivos selecionados. */
  @Input('p-url') url: string;

  /** Define o valor do atributo `name` do componente. */
  @Input('name') name: string = 'file';

  /**
   * @optional
   *
   * @description
   *
   * Define se o envio do arquivo ser automtico ao selecionar o mesmo.
   *
   * > Esta propriedade funciona somente se a propriedade `p-url` tiver um valor atribudo.
   *
   * @default `false`
   */
  @Input('p-auto-upload') autoUpload?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Desabilita boto de remover o(s) arquivo(s) selecionado(s).
   *
   * @default `false`
   */
  @Input({ alias: 'p-disabled-remove-file', transform: convertToBoolean }) disabledRemoveFile: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   *  - O campo conter `p-required`;
   *  - No possuir `p-help` e/ou `p-label`.
   *
   * @default `false`
   */
  @Input('p-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Exibe a pr-visualizao de imagens ao anex-las.
   *
   * > Propriedade funciona apenas em arquivos de formato de imagem (`.png`, `.jpg`, `.jpeg` e `.gif`).
   * Ser ignorada em outros tipos de arquivo.
   *
   * @default `true`
   */
  @Input('p-show-thumbnail') showThumbnail: boolean = true;

  /** Objeto que contm os cabealhos que ser enviado na requisio dos arquivos. */
  @Input('p-headers') headers: { [name: string]: string | Array<string> };

  /**
   * @optional
   *
   * @description
   *
   * Define se a propriedade `p-url`  obrigatria.
   *
   * Caso a propriedade seja definida como `false`:
   * - o boto de "Selecionar arquivo" ficar habilitado mesmo sem a propriedade `p-url` definida.
   * - o boto "Iniciar envio" ficar oculto at que a propriedade `p-url` seja definida.
   *
   * > Se utilizada com a propriedade `p-auto-upload` definida como `true` ser necessrio definir a propriedade `p-url`.
   *
   * @default `true`
   */
  @HostBinding('attr.p-required-url')
  @Input({ alias: 'p-required-url', transform: convertToBoolean })
  requiredUrl: boolean = true;

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define as opes do componente de ajuda (po-helper) que ser exibido ao lado do label quando a propriedade `p-label` for definida, ou, ao lado do componente na ausncia da propriedade `p-label`.
   * > Para mais informaes acesse: https://po-ui.io/documentation/po-helper.
   *
   * > Ao configurar esta propriedade, o antigo cone de ajuda adicional (`p-additional-help-tooltip` e `p-additional-help`) ser ignorado.
   */
  poHelperComponent = input<PoHelperOptions | string>(undefined, { alias: 'p-helper' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Habilita a quebra automtica do texto da propriedade `p-label`. Quando `p-label-text-wrap` for verdadeiro, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * @default `false`
   */
  labelTextWrap = input<boolean>(false, { alias: 'p-label-text-wrap' });

  /**
   *
   * @deprecated v23.x.x use `p-helper`
   *
   * @optional
   *
   * @description
   * Evento disparado ao clicar no cone de ajuda adicional.
   *
   * > Essa propriedade est **depreciada** e ser removida na verso `23.x.x`. Recomendamos utilizar a propriedade `p-helper` que oferece mais recursos e flexibilidade.
   */
  @Output('p-additional-help') additionalHelp = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Define uma ao personalizada no componente `po-upload`, adicionando um boto no canto inferior direito
   * de cada barra de progresso associada aos arquivos enviados ou em envio.
   *
   * A ao deve implementar a interface **PoProgressAction**, permitindo configurar propriedades como:
   * - `label`: Texto do boto.
   * - `icon`: cone a ser exibido no boto.
   * - `type`: Tipo de boto (ex.: `danger` ou `default`).
   * - `disabled`: Indica se o boto deve estar desabilitado.
   * - `visible`: Indica se o boto deve estar visvel.
   *
   * **Exemplo de uso:**
   *
   * ```html
   * <po-upload
   *  [p-custom-action]="customAction"
   *  (p-custom-action-click)="onCustomActionClick($event)">
   * </po-upload>
   * ```
   *
   * ```typescript
   * customAction: PoProgressAction = {
   *   label: 'Baixar',
   *   icon: 'an an-download',
   *   type: 'default',
   *   visible: true
   * };
   *
   * onCustomActionClick(file: PoUploadFile) {
   *   console.log(`Ao personalizada clicada para o arquivo: ${file.name}`);
   * }
   * ```
   */
  @Input('p-custom-action') customAction?: PoProgressAction;

  /**
   * @optional
   *
   * @description
   *
   * Define uma ou duas aes personalizadas do modal de pr-visualizao, adicionando um boto ou dois botes no canto inferior direito
   * do modal.
   *
   * A ao deve implementar a interface **PoModalAction**, permitindo configurar propriedades como:
   * - `label`: Texto do boto.
   * - `action`: cone a ser exibido no boto.
   * - `danger`: Define a propriedade `p-danger` do boto.
   * - `disabled`: Indica se o boto deve estar desabilitado.
   * - `visible`: Indica se o boto deve estar visvel.
   *
   * **Exemplo de uso:**
   *
   * ```html
   * <po-upload
   *  [p-custom-modal-actions]="customActions"
   * </po-upload>
   * ```
   *
   * ```typescript
   * customActions:  Array<PoModalAction> = [
   *  { label: 'Confirmar', action: this.confirmModal.bind(this) },
   *  { label: 'Cancelar', action: this.closeModal.bind(this) }
   *];
   *
   * ```
   */
  @Input('p-custom-modal-actions') customModalActions?: Array<PoModalAction>;

  /**
   * @optional
   *
   * @description
   *
   * Evento emitido ao clicar na ao personalizada configurada no `p-custom-action`.
   *
   * O evento retorna o arquivo associado  barra de progresso onde a ao foi clicada,
   * permitindo executar operaes especficas para aquele arquivo.
   *
   * **Exemplo de uso:**
   *
   * ```html
   * <po-upload
   *  [p-custom-action]="customAction"
   *  (p-custom-action-click)="onCustomActionClick($event)">
   * </po-upload>
   * ```
   *
   * ```typescript
   * customAction: PoProgressAction = {
   *   label: 'Baixar',
   *   icon: 'an an-download',
   *   type: 'default',
   *   visible: true
   * };
   *
   * onCustomActionClick(file: PoUploadFile) {
   *   console.log(`Ao personalizada clicada para o arquivo: ${file.name}`);
   *   // Lgica para download do arquivo
   *   this.downloadFile(file);
   * }
   *
   * downloadFile(file: PoUploadFile) {
   *   // Exemplo de download
   *   console.log(`Iniciando o download do arquivo: ${file.name}`);
   * }
   * ```
   */
  @Output('p-custom-action-click') customActionClick: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   * Evento disparado quando uma tecla  pressionada enquanto o foco est no componente.
   * Retorna um objeto `KeyboardEvent` com informaes sobre a tecla.
   */
  @Output('p-keydown') keydown: EventEmitter<KeyboardEvent> = new EventEmitter<KeyboardEvent>();

  /**
   * @optional
   *
   * @description
   *
   * Funo que ser executada no momento de realizar o envio do arquivo,
   * onde ser possvel adicionar informaes ao parmetro que ser enviado na requisio.
   *  passado por parmetro um objeto com o arquivo e as propriedades data e extraFormData,
   * que sero enviadas em conjunto com o arquivo na requisio, por exemplo:
   *
   * > data, nesta propriedade pode ser informado algum dado
   * ```
   *   event.data = {id: 'id do usurio'};
   * ```
   * > extraFormData, nesta propriedade pode ser informado algum dado solicitado pela API
   * > que no possa estar no objeto `data`, assim o contedo sar extrado do objeto e
   * > enviado como parmetro
   * ```
   *   event.extraFormData = {id: 'id do usurio'};
   * ```
   */
  @Output('p-upload') onUpload: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento ser disparado ao clicar no cone de fechar.
   * > Por parmetro ser passado o objeto do arquivo.
   */
  @Output('p-cancel') onCancel: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento ser disparado ao abrir o modal de pr-visualizao.
   * > Por parmetro ser passado o objeto do arquivo.
   */
  @Output('p-open-modal-preview') onOpenModalPreview: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento ser disparado ao clicar no cone de remover.
   * > Por parmetro ser passado o objeto do arquivo.
   */
  @Output('p-remove') onRemove: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento ser disparado quando ocorrer algum erro no envio do arquivo.
   * > Por parmetro ser passado o objeto do retorno que  do tipo `HttpErrorResponse`.
   */
  @Output('p-error') onError: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Evento ser disparado quando o envio do arquivo for realizado com sucesso.
   * > Por parmetro ser passado o objeto do retorno que  do tipo `HttpResponse`.
   */
  @Output('p-success') onSuccess: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Funo para atualizar o ngModel do componente, necessrio quando no for utilizado dentro da *tag* `form`.
   *
   * Na verso 12.2.0 do Angular a verificao `strictTemplates` vem true como default. Portanto, para utilizar
   * two-way binding no componente deve se utilizar da seguinte forma:
   *
   * ```
   * <po-upload ... [ngModel]="UploadModel" (ngModelChange)="uploadModel = $event"> </po-upload>
   * ```
   *
   */
  @Output('ngModelChange') ngModelChange: EventEmitter<any> = new EventEmitter<any>();

  allowedExtensions: string;
  currentFiles: Array<PoUploadFile>;

  canHandleDirectory: boolean;
  displayAdditionalHelp: boolean = false;
  onModelChange: any;

  protected extensionNotAllowed = 0;
  protected quantityNotAllowed = 0;
  protected sizeNotAllowed = 0;
  protected onModelTouched: any = null;

  private _directory?: boolean;
  private _disabled?: boolean;
  private _dragDrop?: boolean = false;
  private _fileRestrictions?: PoUploadFileRestrictions;
  private _formField?: string;
  private _hideRestrictionsInfo?: boolean;
  private _hideSelectButton?: boolean;
  private _hideSendButton?: boolean;
  private _isMultiple?: boolean;
  private _literals?: any;
  private _required?: boolean;
  private _size?: string = undefined;
  private language: string;
  private validatorChange: any;

  /**
   * @optional
   *
   * @description
   *
   * Permite a seleo de diretrios contendo um ou mais arquivos para envio.
   *
   * > A habilitao desta propriedade se restringe apenas  seleo de diretrios.
   *
   * > Definio no suportada pelo browser **Internet Explorer**, todavia ser possvel a seleo de arquivos padro.
   *
   * @default `false`
   */
  @Input('p-directory') set directory(value: boolean) {
    this._directory = convertToBoolean(value);

    this.canHandleDirectory = this._directory && !isIE() && !isMobile();
    this.setDirectoryAttribute(this.canHandleDirectory);
  }

  get directory() {
    return this._directory;
  }

  /**
   * @optional
   *
   * @description
   *
   * Exibe a rea onde  possvel arrastar e selecionar os arquivos. Quando estiver definida, omite o boto para seleo de arquivos
   * automaticamente.
   *
   * > Recomendamos utilizar apenas um `po-upload` com esta funcionalidade por tela.
   *
   * @default `false`
   */
  @Input('p-drag-drop') set dragDrop(value: boolean) {
    this._dragDrop = convertToBoolean(value);
  }

  get dragDrop() {
    return this._dragDrop;
  }

  /**
   * @optional
   *
   * @description
   *
   * Oculta visualmente as informaes de restries para o upload.
   *
   * @default `false`
   */
  @Input('p-hide-restrictions-info') set hideRestrictionsInfo(value: boolean) {
    this._hideRestrictionsInfo = convertToBoolean(value);
  }

  get hideRestrictionsInfo() {
    return this._hideRestrictionsInfo;
  }

  /**
   * @optional
   *
   * @description
   *
   * Omite o boto de seleo de arquivos.
   *
   * > Caso o valor definido seja `true`, caber ao desenvolvedor a responsabilidade
   * pela chamada do mtodo `selectFiles()` para seleo de arquivos.
   *
   * @default `false`
   */
  @Input('p-hide-select-button') set hideSelectButton(value: boolean) {
    this._hideSelectButton = convertToBoolean(value);
  }
  get hideSelectButton(): boolean {
    return this._hideSelectButton;
  }

  /**
   * @optional
   *
   * @description
   *
   * Omite o boto de envio de arquivos.
   *
   * > Caso o valor definido seja `true`, caber ao desenvolvedor a responsabilidade
   * pela chamada do mtodo `sendFiles()` para envio do(s) arquivo(s) selecionado(s).
   *
   * @default `false`
   */
  @Input('p-hide-send-button') set hideSendButton(value: boolean) {
    this._hideSendButton = convertToBoolean(value);
  }
  get hideSendButton(): boolean {
    return this._hideSendButton;
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `po-upload`.
   *
   * Existem duas maneiras de customizar o componente:
   *
   * - passando um objeto implementando a interface `PoUploadLiterals` com todas as literais disponveis;
   * - passando apenas as literais que deseja customizar:
   *   ```
   *    const customLiterals: PoUploadLiterals = {
   *      folders: 'Pastas',
   *      selectFile: 'Buscar arquivo',
   *      startSending: 'Enviar'
   *    };
   *   ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente:
   *
   * ```
   * <po-upload
   *   [p-literals]="customLiterals">
   * </po-upload>
   * ```
   *
   * > O objeto padro de literais ser traduzido de acordo com o idioma do *browser* (pt, en, es, ru).
   */
  @Input('p-literals') set literals(value: PoUploadLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...poUploadLiteralsDefault[poLocaleDefault],
        ...poUploadLiteralsDefault[this.language],
        ...value
      };
    } else {
      this._literals = poUploadLiteralsDefault[this.language];
    }
  }
  get literals() {
    return this._literals || poUploadLiteralsDefault[this.language];
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto que segue a definio da interface `PoUploadFileRestrictions`,
   * que possibilita definir tamanho mximo/mnimo e extenso dos arquivos permitidos.
   */
  @Input('p-restrictions') set fileRestrictions(restrictions: PoUploadFileRestrictions) {
    this._fileRestrictions = this.initRestrictions(restrictions);

    this.setAllowedExtensions(restrictions);
  }

  get fileRestrictions(): PoUploadFileRestrictions {
    return this._fileRestrictions;
  }

  /**
   * @optional
   *
   * @description
   *
   * Nome do campo de formulrio que ser enviado para o servio informado na propriedade `p-url`.
   *
   * @default `files`
   */
  @Input('p-form-field') set formField(value: string) {
    this._formField = value && typeof value === 'string' ? value : poUploadFormFieldDefault;

    this.getUploadService().formField = this.formField;
  }

  get formField(): string {
    return this._formField;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser desabilitado.
   */
  @Input('p-disabled') set disabled(value: boolean) {
    this._disabled = convertToBoolean(value);

    this.validateModel(this.currentFiles);
  }

  get disabled() {
    return this._disabled;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define se pode selecionar mais de um arquivo.
   *
   * > Se utilizada a `p-directory`, habilita-se automaticamente esta propriedade.
   */
  @Input('p-multiple') set isMultiple(value: boolean) {
    this._isMultiple = convertToBoolean(value);
  }

  get isMultiple() {
    return this.canHandleDirectory ? true : this._isMultiple;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define que o campo ser obrigatrio.
   *
   * @default `false`
   */
  @Input('p-required') set required(required: boolean) {
    this._required = convertToBoolean(required);

    this.validateModel(this.currentFiles);
  }

  get required() {
    return this._required;
  }

  /**
   * Define se a indicao de campo obrigatrio ser exibida.
   *
   * > No ser exibida a indicao se:
   * - No possuir `p-help` e/ou `p-label`.
   */
  @Input('p-show-required') showRequired: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho e as aes do componente:
   * - `small`: altura do button como 32px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura do button como 44px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  constructor(
    protected uploadService: PoUploadService,
    languageService: PoLanguageService,
    protected cd: ChangeDetectorRef
  ) {
    this.language = languageService.getShortLanguage();
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar os estados de habilitado via forms api
  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.cd.markForCheck();
  }

  registerOnChange(fn: any): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onModelTouched = fn;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  validate(abstractControl: AbstractControl): { [key: string]: any } {
    if (requiredFailed(this.required, this.disabled, abstractControl.value)) {
      return {
        required: {
          valid: false
        }
      };
    }
  }

  writeValue(model: any): void {
    if (model) {
      if (!isEquals(this.currentFiles, model)) {
        this.currentFiles = this.parseFiles(model);
      }
    } else {
      this.currentFiles = undefined;
    }
  }

  protected isExceededFileLimit(currentFilesLength: number): boolean {
    return (
      this.isMultiple &&
      this.fileRestrictions &&
      this.fileRestrictions.maxFiles > 0 &&
      this.fileRestrictions.maxFiles <= currentFilesLength
    );
  }

  // Faz o parse dos arquivos selecionados para arquivos do formato PoUploadFile e atualiza os arquivos correntes.
  protected parseFiles(files: Array<File>): Array<PoUploadFile> {
    let poUploadFiles: Array<PoUploadFile> = this.currentFiles || [];
    const filesLength = files.length;

    for (let i = 0; i < filesLength; i++) {
      if (this.isExceededFileLimit(poUploadFiles.length)) {
        this.quantityNotAllowed = filesLength - this.fileRestrictions.maxFiles;
        break;
      }
      const file = new PoUploadFile(files[i]);
      let currentFile: any = file;

      if (!this.checkRestrictions(file)) {
        currentFile = {
          uid: file.uid,
          displayName: file.displayName,
          name: file.name,
          extension: file.extension,
          size: file.size,
          status: 2,
          sizeNotAllowed: file.sizeNotAllowed,
          extensionNotAllowed: file.extensionNotAllowed,
          errorMessage: ''
        };
      }

      if (this.showThumbnail && currentFile.rawFile?.type?.startsWith('image/')) {
        convertImageToBase64(currentFile.rawFile).then(base64 => {
          currentFile.thumbnailUrl = base64;
          this.cd.detectChanges();
        });
      }

      poUploadFiles = this.insertFileInFiles(currentFile, poUploadFiles);
      this.sendFeedback(currentFile);
    }
    this.sendFeedback();
    return poUploadFiles;
  }

  protected validateModel(model: any) {
    if (this.validatorChange) {
      this.validatorChange(model);
    }
  }

  // Verifica se o arquivo est de acordo com as restries.
  private checkRestrictions(file: PoUploadFile): boolean {
    const restrictions = this.fileRestrictions;

    if (restrictions) {
      const allowedExtensions = restrictions.allowedExtensions;
      const minFileSize = restrictions.minFileSize;
      const maxFileSize = restrictions.maxFileSize;

      const isAccept = allowedExtensions ? this.isAllowedExtension(file.extension, allowedExtensions) : true;
      const isAcceptSize = file.size >= minFileSize && file.size <= maxFileSize;

      if (!isAcceptSize) {
        file.sizeNotAllowed = true;
        this.sizeNotAllowed = this.sizeNotAllowed + 1;
      }

      if (!isAccept) {
        file.extensionNotAllowed = true;
      }

      return isAccept && isAcceptSize;
    }

    return true;
  }

  private existsFileSameName(file: PoUploadFile, files: Array<PoUploadFile>): boolean {
    return files.some(currentFile => file.name === currentFile.name);
  }

  private getUploadService(): PoUploadService {
    return this.uploadService;
  }

  private insertFileInFiles(newFile: PoUploadFile, files: Array<PoUploadFile>) {
    if (this.existsFileSameName(newFile, files)) {
      return this.updateExistsFileInFiles(newFile, files);
    }

    if (this.isMultiple) {
      files.push(newFile);
    } else {
      files.splice(0, files.length, newFile);
    }

    return files;
  }

  private isAllowedExtension(extension: string, allowedExtensions: Array<string> = []): boolean {
    const isAllowed = allowedExtensions.some(ext => ext.toLowerCase() === extension);
    if (!isAllowed) {
      this.extensionNotAllowed = this.extensionNotAllowed + 1;
    }
    return isAllowed;
  }

  private setAllowedExtensions(restrictions: PoUploadFileRestrictions = {}) {
    const _allowedExtensions = restrictions.allowedExtensions || [];

    this.allowedExtensions = _allowedExtensions.join(',');
  }

  private initRestrictions(restrictions: PoUploadFileRestrictions): PoUploadFileRestrictions {
    if (!restrictions) {
      return;
    }

    const minFileSize = restrictions.minFileSize || poUploadMinFileSize;
    const maxFileSize = restrictions.maxFileSize || poUploadMaxFileSize;

    return {
      ...restrictions,
      maxFileSize: maxFileSize,
      minFileSize: minFileSize
    };
  }

  private updateExistsFileInFiles(newFile: PoUploadFile, files: Array<PoUploadFile>) {
    const fileIndex = files.findIndex(
      currentFile => newFile.name === currentFile.name && currentFile.status !== PoUploadStatus.Uploaded
    );

    if (fileIndex !== -1) {
      files.splice(fileIndex, 1, newFile);
    }

    return files;
  }

  abstract sendFeedback(file?): void;

  abstract setDirectoryAttribute(value: boolean);
}
