export enum PoItemListFilterMode {
  /** Verifica se o texto *inicia* com o valor pesquisado. Caso no seja especificado um tipo, ser esse o utilizado. */
  startsWith,
  /** Verifica se o texto *contm* o valor pesquisado. */
  contains,
  /** Verifica se o texto *finaliza* com o valor pesquisado. */
  endsWith
}
