import { Directive, ElementRef, EventEmitter, Input, Output } from '@angular/core';

import { convertToBoolean, getDefaultSizeFn, validateSizeFn } from '../../utils/util';
import { PO_CONTROL_POSITIONS } from './../../services/po-control-position/po-control-position.constants';

import { PoFieldSize } from '../../enums/po-field-size.enum';
import { PoPopupAction } from './po-popup-action.interface';

const poPopupDefaultPosition = 'bottom-left';

/**
 * @description
 *
 * O componente `po-popup`  um container pequeno recomendado para aes de navegao:
 * Ele abre sobreposto aos outros componentes.
 *
 *  possvel escolher as posies do `po-popup` em relao ao componente alvo, para isto veja a propriedade `p-position`.
 *
 * Tambm  possvel informar um _template_ _header_ para o `po-popup`, que ser exibido acima das aes.
 * Para funcionar corretamente  preciso adicionar a propriedade `p-popup-header-template` no elemento que servir de template, por exemplo:
 *
 * ```
 * <po-popup [p-target]="target">
 *   <div p-popup-header-template>
 *     <div>
 *       Dev PO
 *     </div>
 *     <div>
 *       dev.po@po-ui.com.br
 *     </div>
 *   </div>
 * </po-popup >
 * ```
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                    |
 * |----------------------------------------|-------------------------------------------------------|-------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                 |
 * | `--border-radius`                      | Contm o valor do raio dos cantos do elemento&nbsp;   | `var(--border-radius-md)`                       |
 * | `--border-width`                       | Contm o valor da largura dos cantos do elemento&nbsp;| `var(--border-width-sm)`                        |
 * | `--border-color`                       | Cor da borda                                          | `var(--color-neutral-light-20)`                 |
 * | `--background`                         | Cor do background                                     | `var(--color-neutral-light-00)`                 |
 * | `--shadow`                             | Contm o valor da sombra do elemento                  | `var(--shadow-md)`                              |
 * | **po-popup po-item-list**              |                                                       |                                                 |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                      |
 * | `--font-size`                          | Tamanho da fonte                                      | `var(--font-size-default)`                      |
 * | `--line-height`                        | Tamanho da label                                      | `var(--line-height-md)`                         |
 * | **Action**                             |                                                       |                                                 |
 * | `--font-weight`                        | Peso da fonte                                         | `var(--font-weight-bold)`                       |
 * | `--color`                              | Cor principal do popup                                | `var(--color-action-default)`                   |
 * | **Hover**                              |                                                       |                                                 |
 * | `--color-hover`                        | Cor principal no estado hover                         | `var(--color-brand-01-darkest)`                 |
 * | `--background-hover`                   | Cor de background no estado hover                     | `var(--color-brand-01-lighter)`                 |
 * | **Focused**                            |                                                       |                                                 |
 * | `--outline-color-focused`              | Cor do outline do estado de focus                     | `var(--color-action-focus)`                     |
 * | **Pressed**                            |                                                       |                                                 |
 * | `--background-pressed`                 | Cor de background no estado de pressionado&nbsp;      | `var(--color-brand-01-light)`                   |
 * | **Disabled**                           |                                                       |                                                 |
 * | `--color-disabled`                     | Cor principal no estado disabled                      | `var(--color-action-disabled)`                  |
 * | **Selected**                           |                                                       |                                                 |
 * | `--font-weight-selected`               | Peso da fonte no estado selecionado                   | `var(--font-weight-bold)`                       |
 * | `--background-selected`                | Cor de background no estado selecionado               | `var(--color-brand-01-lightest)`                |
 * | **Option e check**                     |                                                       |                                                 |
 * | `--color-option`                       | Cor principa no estado Option/check                   | `var(--color-neutral-dark-90)`                  |
 *
 */
@Directive()
export class PoPopupBaseComponent {
  arrowDirection: string = 'top-right';
  showPopup: boolean = false;

  protected oldTarget;
  protected param;
  protected clickoutListener: () => void;
  protected resizeListener: () => void;

  private _actions: Array<PoPopupAction>;
  private _customPositions?: Array<string>;
  private _hideArrow: boolean = false;
  private _isCornerAlign: boolean = false;
  private _position?: string = poPopupDefaultPosition;
  private _size?: string = undefined;
  private _target: any;

  // Indica se h um listbox com subitens
  @Input('p-listbox-subitems') listboxSubitems = false;

  /** Lista de aes que sero exibidas no componente. */
  @Input('p-actions') set actions(value: Array<PoPopupAction>) {
    this._actions = Array.isArray(value) ? value : [];
  }

  get actions() {
    return this._actions;
  }

  /**
   * @optional
   *
   * @description
   *
   * Oculta a seta do componente *popup*.
   *
   * @default `false`
   */
  @Input('p-hide-arrow') set hideArrow(value: boolean) {
    this._hideArrow = convertToBoolean(value);
  }

  get hideArrow(): boolean {
    return this._hideArrow;
  }

  // Define se o `po-popup` ser alinhado pelos cantos do elemento target.
  @Input('p-is-corner-align') set isCornerAlign(value: boolean) {
    this._isCornerAlign = convertToBoolean(value);
  }

  get isCornerAlign(): boolean {
    return this._isCornerAlign;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a posio inicial que o `po-popup` abrir em relao ao componente alvo. Sugere-se que seja
   * usada a orientao `bottom-left` (abaixo e a esquerda), porm o mesmo  flexvel e ser rotacionado
   * automaticamente para se adequar a tela, caso necessrio.
   *
   * > Caso seja definido um `p-custom-positions` o componente ir abrir na posio definida na propriedade `p-position`
   * e caso no caiba na posio inicial ele ir rotacionar seguindo a ordem de posies definidas no `p-custom-positions`.
   *
   * Posies vlidas:
   * - `right`: Posiciona o po-popup no lado direito do componente alvo.
   * - `right-bottom`: Posiciona o po-popup no lado direito inferior do componente alvo.
   * - `right-top`: Posiciona o po-popup no lado direito superior do componente alvo.
   * - `bottom`: Posiciona o po-popup abaixo do componente alvo.
   * - `bottom-left`: Posiciona o po-popup abaixo e  esquerda do componente alvo.
   * - `bottom-right`: Posiciona o po-popup abaixo e  direita do componente alvo.
   * - `left`: Posiciona o po-popup no lado esquerdo do componente alvo.
   * - `left-top`: Posiciona o po-popup no lado esquerdo superior do componente alvo.
   * - `left-bottom`: Posiciona o po-popup no lado esquerdo inferior do componente alvo.
   * - `top`: Posiciona o po-popup acima do componente alvo.
   * - `top-right`: Posiciona o po-popup acima e  direita do componente alvo.
   * - `top-left`: Posiciona o po-popup acima e  esquerda do componente alvo.
   *
   * @default `bottom-left`
   */
  @Input('p-position') set position(value: string) {
    this._position = PO_CONTROL_POSITIONS.includes(value) ? value : poPopupDefaultPosition;
  }

  get position(): string {
    return this._position;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define as posies e a sequncia que o `po-popup` poder rotacionar. A sequncia ser definida pela ordem passada
   * no *array*. Caso no seja definido, o `po-popup` ir rotacionar em todas as posies vlidas.
   *
   * > O componente sempre ir abrir na posio definida no `p-position` e caso no caiba na posio definida o mesmo
   * ir rotacionar seguindo a ordem definida pelo `p-custom-position`.
   *
   * Posies vlidas:
   * - `right`: Posiciona o po-popup no lado direito do componente alvo.
   * - `right-bottom`: Posiciona o po-popup no lado direito inferior do componente alvo.
   * - `right-top`: Posiciona o po-popup no lado direito superior do componente alvo.
   * - `bottom`: Posiciona o po-popup abaixo do componente alvo.
   * - `bottom-left`: Posiciona o po-popup abaixo e  esquerda do componente alvo.
   * - `bottom-right`: Posiciona o po-popup abaixo e  direita do componente alvo.
   * - `left`: Posiciona o po-popup no lado esquerdo do componente alvo.
   * - `left-top`: Posiciona o po-popup no lado esquerdo superior do componente alvo.
   * - `left-bottom`: Posiciona o po-popup no lado esquerdo inferior do componente alvo.
   * - `top`: Posiciona o po-popup acima do componente alvo.
   * - `top-right`: Posiciona o po-popup acima e  direita do componente alvo.
   * - `top-left`: Posiciona o po-popup acima e  esquerda do componente alvo.
   */
  @Input('p-custom-positions') set customPositions(value: Array<string>) {
    this._customPositions = Array.isArray(value) ? value : [];
  }

  get customPositions() {
    return this._customPositions;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho dos componentes de formulrio no template:
   * - `small`: aplica a medida small de cada componente (disponvel apenas para acessibilidade AA).
   * - `medium`: aplica a medida medium de cada componente.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * @description
   *
   * Para utilizar o `po-popup` deve-se colocar uma varivel local no componente que disparar o evento
   * de abertura no mesmo e com isso, invocar a funo `toggle`, por exemplo:
   *
   * ```
   * <span #icon class="an an-credit-card" (click)="popup.toggle()">
   *   Credit Actions
   * </span>
   *
   * <po-popup #popup
   *   [p-actions]="actions"
   *   [p-target]="icon">
   * </po-popup>
   * ```
   *
   * Caso o elemento alvo for um componente, ser preciso obter o `ElementRef` do mesmo e pass-lo  propriedade, por exemplo:
   *
   * ```
   * // component.html
   *
   * <po-button #poButton
   *   p-label="Open Popover"
   *   (p-click)="popup.toggle()">
   * </po-button>
   *
   * <po-popup #popup
   *   [p-actions]="actions"
   *   [p-target]="poButtonRef">
   * </po-popup>
   *
   * // component.ts
   *
   * @ViewChild('poButton', { read: ElementRef }) poButtonRef: ElementRef;
   * ```
   */
  @Input('p-target') set target(value: any) {
    this._target = value instanceof ElementRef ? value.nativeElement : value;
  }

  get target() {
    return this._target;
  }

  @Output('p-close') closeEvent: EventEmitter<any> = new EventEmitter();

  @Output('p-click-item') clickItem: EventEmitter<any> = new EventEmitter();
}
