import { Component, EventEmitter, HostBinding, Input, Output, TemplateRef, input } from '@angular/core';

import { convertToBoolean, getDefaultSizeFn, validateSizeFn } from '../../utils/util';

import { PoButtonKind } from './enums/po-button-kind.enum';
import { PoButtonSize } from './enums/po-button-size.enum';
import { PoButtonType } from './enums/po-button-type.enum';
/**
 * @description
 *
 * O `po-button` permite que o usurio execute aes predefinidas pelo desenvolvedor.
 *
 * Atravs dos tipos,  possvel identificar a importncia de cada ao.
 *
 * #### Boas prticas
 *
 * - Evite `labels` extensos que quebram o layout do `po-button`, use `labels` diretos, curtos e intuitivos.
 * - Utilize apenas um `po-button` configurado como `primary` por pgina.
 * - Para aes irreversveis use sempre a propriedade `p-danger`.
 *
 * #### Acessibilidade tratada no componente
 *
 * Algumas diretrizes de acessibilidade j so tratadas no componente, internamente, e no podem ser alteradas pelo proprietrio do contedo. So elas:
 *
 * - Quando em foco, o boto  ativado usando as teclas de Espao e Enter do teclado. [W3C WAI-ARIA 3.5 Button - Keyboard Interaction](https://www.w3.org/WAI/ARIA/apg/#keyboard-interaction-3)
 * - A rea do foco precisar ter uma espessura de pelo menos 2 pixels CSS e o foco no pode ficar escondido por outros elementos da tela. [WCAG 2.4.12: Focus Appearance](https://www.w3.org/WAI/WCAG22/Understanding/focus-appearance-enhanced)
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                              | Descrio                                             | Valor Padro                                      |
 * |------------------------------------------|-------------------------------------------------------|---------------------------------------------------|
 * | `--font-family`                          | Famlia tipogrfica usada                             | `var(--font-family-theme)`                        |
 * | `--font-size`                            | Tamanho da fonte                                      | `var(--font-size-default)`                        |
 * | `--font-weight`                          | Peso da fonte                                         | `var(--font-weight-bold)`                         |
 * | `--line-height`                          | Tamanho da label                                      | `var(--line-height-none)`                         |
 * | `--border-radius`                        | Contm o valor do raio dos cantos do elemento&nbsp;   | `var(--border-radius-md)`                         |
 * | `--border-width`                         | Contm o valor da largura dos cantos do elemento&nbsp;| `var(--border-width-md)`                          |
 * | `--padding`                              | Preenchimento                                         | `0 1em`                                           |                                                                        | ---                                             |
 * | **Danger**                               |                                                       |                                                   |
 * | `--text-color-danger`                    | Cor do texto no estado danger                         | `var(--color-neutral-light-00)`                   |
 * | `--color-button-danger`                  | Cor do boto no estado danger                         | `var(--color-feedback-negative-dark)`             |
 * | `--color-danger-hover`                   | Cor de hover no estado danger                         | `var(--color-feedback-negative-darker)`           |
 * | `--color-danger-pressed`                 | Cor pressionada no estado danger                      | `var(--color-feedback-negative-darkest)`          |
 * | `--background-danger-hover`              | Cor de background de hover no estado danger           | `var(--color-feedback-negative-lighter)`          |
 * | `--border-color-danger-hover`            | Cor da borda de hover no estado danger                | `var(--color-feedback-negative-darkest)`          |
 * | `--background-danger-pressed`            | Cor de background pressionado no estado danger        | `var(--color-feedback-negative-light)`            |
 * | `--background-color-button-danger`&nbsp; | Cor de background do boto no estado danger           | `var(--color-transparent)`                        |
 * | **Default Values**                       |                                                       |                                                   |
 * | `--text-color`                           | Cor do texto                                          | `var(--color-neutral-light-00)`                   |
 * | `--color`                                | Cor principal do boto                                | `var(--color-action-default)`                     |
 * | `--background-color`                     | Cor de background                                     | `var(--color-transparent)`                        |
 * | `--border-color`                         | Cor da borda                                          | `var(--color-transparent)`                        |
 * | `--shadow`                               | Contm o valor da sombra do elemento                  | `var(--shadow-none)`                              |
 * | **Hover**                                |                                                       |                                                   |
 * | `--color-hover`                          | Cor principal no estado hover                         | `var(--color-action-hover)`                       |
 * | `--background-hover`                     | Cor de background no estado hover                     | `var(--color-brand-01-lighter)`                   |
 * | `--border-color-hover`                   | Cor da borda no estado hover                          | `var(--color-brand-01-darkest)`                   |
 * | **Focused**                              |                                                       |                                                   |
 * | `--outline-color-focused`                | Cor do outline do estado de focus                     | `var(--color-action-focus)`                       |
 * | **Pressed**                              |                                                       |                                                   |
 * | `--color-pressed`                        | Cor principal no estado de pressionado                | `var(--color-action-pressed)`                     |
 * | `--background-pressed`                   | Cor de background no estado de pressionado&nbsp;      | `var(--color-brand-01-light)`                     |
 * | **Disabled**                             |                                                       |                                                   |
 * | `--text-color-disabled`                  | Cor do texto no estado disabled                       | `var(--color-neutral-dark-70)`                    |
 * | `--color-disabled`                       | Cor principal no estado disabled                      | `var(--color-action-disabled)`                    |
 * | `--background-color-disabled`            | Cor de background no estado disabled                  | `var(--color-transparent)`                        |
 *
 */
@Component({
  selector: 'po-button-base',
  template: '',
  standalone: false
})
export class PoButtonBaseComponent {
  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Label do boto.
   */
  readonly label = input<string>(undefined, { alias: 'p-label' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   * cone exibido ao lado esquerdo do label do boto.
   *
   *  possvel usar qualquer um dos cones da [Biblioteca de cones](https://po-ui.io/icons). conforme exemplo abaixo:
   * ```
   * <po-button p-icon="an an-user" p-label="PO button"></po-button>
   * ```
   * Tambm  possvel utilizar outras fontes de cones, por exemplo a biblioteca *Font Awesome*, da seguinte forma:
   * ```
   * <po-button p-icon="fa fa-podcast" p-label="PO button"></po-button>
   * ```
   * Outra opo seria a customizao do cone atravs do `TemplateRef`, conforme exemplo abaixo:
   * ```
   * <po-button [p-icon]="template" p-label="button template ionic"></po-button>
   *
   * <ng-template #template>
   *  <ion-icon style="font-size: inherit" name="heart"></ion-icon>
   * </ng-template>
   * ```
   * > Para o cone enquadrar corretamente, deve-se utilizar `font-size: inherit` caso o cone utilizado no aplique-o.
   */
  readonly icon = input<string | TemplateRef<void>>(undefined, { alias: 'p-icon' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   * Define o tipo do boto.
   *
   * @default `PoButtonType.Button`
   */
  readonly type = input<PoButtonType>(PoButtonType.Button, { alias: 'p-type' });

  // Evento disparado ao sair do campo.
  @Output('p-blur') blur: EventEmitter<any> = new EventEmitter();

  /** Ao que ser executada quando o usurio clicar sobre o `po-button`. */
  @Output('p-click') click = new EventEmitter<null>();

  private _danger?: boolean = false;
  private _disabled?: boolean = false;
  private _loading?: boolean = false;
  private _kind?: string = PoButtonKind.secondary;
  private _size?: string = undefined;
  protected hasSize?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Exibe um cone de carregamento  esquerda do _label_ do boto.
   *
   * > Quando esta propriedade estiver habilitada, desabilitar o boto.
   *
   * @default `false`
   */
  @Input('p-loading') set loading(value: boolean) {
    this._loading = convertToBoolean(value);
  }

  get loading(): boolean {
    return this._loading;
  }

  /**
   * @optional
   *
   * @description
   *
   * Deve ser usado em aes irreversveis que o usurio precisa ter cuidado ao execut-la, como a excluso de um registro.
   *
   * > A propriedade `p-kind="tertiary"` ser inativada ao utilizar esta propriedade.
   */

  @HostBinding('attr.p-danger')
  @Input({ alias: 'p-danger', transform: convertToBoolean })
  set danger(value: boolean) {
    this._danger = this.kind !== PoButtonKind.tertiary ? value : false;
  }

  get danger(): boolean {
    return this._danger;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o estilo visual do componente conforme valores especificados no enum `PoButtonKind`:
   *  - `primary`: destaca o boto, sendo recomendado para aes principais.
   *  - `secondary`: estilo padro, ideal para aes secundrias.
   *  - `tertiary`: exibe o boto sem preenchimento no fundo, indicado para aes opcionais.
   *
   * @default `secondary`
   */
  @HostBinding('attr.p-kind')
  @Input('p-kind')
  set kind(value: string) {
    this._kind = PoButtonKind[value] ? PoButtonKind[value] : PoButtonKind.secondary;
  }

  get kind(): string {
    return this._kind;
  }

  /**
   * @optional
   *
   * @description
   *
   * Desabilita o `po-button` e no permite que o usurio interaja com o mesmo.
   *
   * @default `false`
   */
  @Input('p-disabled') set disabled(value: boolean) {
    this._disabled = <any>value === '' ? true : convertToBoolean(value);
  }
  get disabled(): boolean {
    return this._disabled;
  }

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define um `aria-label` para o `po-button`.
   *
   * Caso esta propriedade no seja informada ser considerada a label do boto.
   *
   * > Em caso de botes com apenas cone a atribuio de valor  esta propriedade  muito importante para acessibilidade.
   */
  readonly ariaLabel = input<string>(undefined, { alias: 'p-aria-label' });

  @Input('p-aria-expanded') ariaExpanded?: boolean = null;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho do componente:
   * - `small`: altura de 32px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura de 44px.
   * - `large`: altura de 56px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoButtonSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoButtonSize);
  }
}
