/**
 * @usedBy PoThemeService
 *
 * @description
 * Enum para configurar o nvel de acessibilidade dos componentes atravs do servio de tema.
 *
 * ```
 * import { PoThemeA11yEnum } from '@po-ui/theme';
 *
 * // Definindo o nvel de acessibilidade ao configurar as cores e o tipo do tema (light | dark)
 * themeService.setTheme(...theme, ...type, PoThemeA11yEnum.AA);
 *
 * // Definindo o nvel de acessibilidade ao configurar apenas as cores do tema
 * themeService.setThemeA11y(...theme, PoThemeA11yEnum.AAA);
 *
 * // Alterando o nvel de acessibilidade com as cores do tema j definidas
 * themeService.setCurrentThemeA11y(PoThemeA11yEnum.AAA);
 * ```
 */
export enum PoThemeA11yEnum {
  /** Nvel de acessibilidade AA.
   * - Define a espessura do `outline` para **2px**.
   * - Disponibiliza o tamanho `small` para componentes de formulrio (buttons, inputs, checkboxes, radios e switches)
   * conforme suas documentaes.
   */
  AA = 'AA',

  /** Nvel de acessibilidade AAA.
   * - Define a espessura do `outline` para **4px**.
   * - No disponibiliza o tamanho `small` para componentes de formulrio.
   */
  AAA = 'AAA'
}
