import { Directive, Input } from '@angular/core';
import { isValidImageBase64 } from '../../utils/util';

type PoImageLoading = 'lazy' | 'eager' | 'auto';

/**
 * @description
 *
 * As imagens tm a funo de traduzir visualmente ideias especficas ou mensagens complexas, mostrar um produto ou contar uma histria, estabelecendo empatia e se conectando com os usurios.
 *
 * #### Boas Prticas
 *
 * O componente image foi projetado para atender os requisitos das Diretrizes de Acessibilidade para Contedo Web (WCAG) 2.1. Tambm foram estruturadas padres de usabilidade para auxiliar na utilizao do componente e garantir uma boa experincia para os usurios. Por isso,  muito importante que, ao aplicar esse componente, o proprietrio do contedo leve em considerao alguns critrios e prticas:
 * ##### Uso
 * - Ao utilizar imagens, mantenha uma coerncia entre elas no produto, de modo que compartilhem um mesmo estilo e inteno entre si.
 * - Utilize imagens que expressem a mensagem e estilo do produto, respeitando as diretrizes e guia da marca.
 * - Ao utilizar fotografias,  recomendvel o uso de propores de aspecto padro, como 1:1, 3:1, 3:2, 16:9.
 * - Mantenha um ponto focal na imagem, pois isso influencia em como ela se comportar em diferentes formatos. Isso tambm ajuda a transmitir a mensagem de forma objetiva e consistente.
 *
 * ##### Imagem como plano de fundo
 * - Avalie se  realmente necessrio o uso de imagem como plano de fundo e evite sempre que possvel, pois pode ocasionar em um baixo contraste entre texto e imagem.
 * - Caso utilize, redobre a ateno na escolha da imagem e certifique-se de que ela est adequada para a leitura do texto e no est sendo apenas um rudo.
 * - Tenha especial ateno em telas menores. Embora seja possvel posicionar o texto em uma rea mais vazia ou escurecida, o texto e imagem se ajustam aos diferentes espaos, de acordo com o dispositivo. Muitas vezes acaba resultando no comprometimento tanto da leitura do texto e quando na visualizao da imagem.
 * - Verifique a taxa de contraste do texto em relao ao fundo. Deve ser suficiente para atender aos padres de acessibilidade, sendo 4,5:1 para textos acima de 18pt ou bold e 7,1: 1 para textos menores que 18pt.
 * - Se no tiver controle sobre qual imagem ser colocada por trs do texto, o recomendado  no utilizar nesse formato.
 *
 * #### Acessibilidade tratada no componente
 * As boas prticas de acessibilidade variam de acordo com tipo da imagem, que podem ser divididas em:
 * - Imagem informativa simples, como por exemplo uma fotografia de um produto.
 * - Imagem complexa, como um grfico, infogrfico ou diagrama.
 * - Imagem decorativa, como um plano de fundo ou uma fotografia que ilustra um assunto, mas no  essencial para compreender a informao.
 */
@Directive()
export class PoImageBaseComponent {
  isBase64: boolean = false;
  private _source: string;

  /**
   * @optional
   *
   * @description
   *
   * Define a altura da imagem em *pixels*. Caso no seja definida,
   * atribui o tamanho da imagem
   */
  @Input('p-height') height: number;

  /**
   * @optional
   *
   * @description
   *
   * Defini o texto alternativo descrevendo a imagem.
   */
  @Input('p-alt') alternate: string;

  /**
   * @optional
   *
   * @description
   *
   * Fonte da imagem que pode ser um caminho local (`./assets/images/logo-black-small.png`)
   * ou um servidor externo (`https://po-ui.io/assets/images/logo-black-small.png`).
   */
  @Input('p-src') set source(value: string) {
    if (isValidImageBase64(value)) {
      this.isBase64 = true;
    }
    this._source = value;
  }

  get source(): string {
    return this._source;
  }

  /**
   * @optional
   *
   * @description
   *
   * Defini a prioridade de carregamento da imagem.
   * > Para as imagens com carregamento priorittio ativo  necessrio incluir
   * > uma tag link no head do arquivo index.html da sua aplicao.
   *
   * ```
   * <link rel="preconnect" href="<url_base_da_imagem>">
   * ```
   *
   * @default `false`
   */
  @Input('p-priority') priority: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Defini o carregamento que pode ser dos tipo:
   *
   *  lazy
   *  eager
   *  auto
   *
   * > No  permitido definir esta propriedade em conjunto com a propriedade `p-priority`.
   */
  @Input('p-loading') loading?: PoImageLoading;
}
