/**
 * @docsPrivate
 *
 * @description
 *
 * Define o tamanho padro utilizado em componentes interativos. Este enum tambm  usado pelo servio de tema para
 * definir a preferncia global de tamanho dos componentes. Por padro, os componentes utilizam o tamanho `medium` por
 * ser mais acessvel (conforme diretrizes WCAG nvel AAA).
 *
 * > O tamanho `small` est disponvel apenas em contextos com acessibilidade AA.
 */
export enum PoFieldSize {
  Small = 'small',
  Medium = 'medium'
}
