import { Directive, EventEmitter, HostBinding, Input, Output } from '@angular/core';
import { PoFieldSize } from '../../enums/po-field-size.enum';
import { convertToBoolean, getDefaultSizeFn, validateSizeFn } from '../../utils/util';
import { PoToasterMode } from './enum/po-toaster-mode.enum';
import { PoToasterOrientation } from './enum/po-toaster-orientation.enum';
import { PoToasterType } from './enum/po-toaster-type.enum';
import { PoToaster } from './interface/po-toaster.interface';

/**
 *
 * @description
 *
 * O Toaster serve para exibir uma mensagem temporria em linha na  interface, podendo ou no ser removida pelos usurios a depender do uso especificado.
 *
 * #### Acessibilidade tratada no componente
 *
 * Algumas diretrizes de acessibilidade j so tratadas no componente, internamente, e no podem ser alteradas pelo proprietrio do contedo. So elas:
 *
 * - Permitir a interao via teclado (2.1.1: Keyboard (A));
 * - Permitir que o usurio feche facilmente o toaster e no retirar o foco de onde est. (2.2.4: Interrupes (AAA));
 * - Preservar o foco visvel na navegao via teclado. (2.4.7: Foco visvel (A));
 * - reas de clique ou toque para elementos interativos devem ter pelo menos 44x44 pixels (2.5.5: rea de clique (AAA));
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                             | Descrio                                            | Valor Padro                                      |
 * |----------------------------------------|-------------------------------------------------------|---------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                   |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                        |
 * | `--font-color`                         | Cor principal do texto                                | `var(--color-neutral-dark-90)`                    |
 * | `--font-color-support`                 | Cor principal do texto de supporte                    | `var(--color-neutral-dark-80)`                    |
 * | `--border-radius`                      | Contm o valor do raio dos cantos do elemento&nbsp;   | `var(--border-radius-md)`                         |
 * | **Type Success**                       |                                                       |                                                   |
 * | `--color-success`                      | Cor principal no tipo success                         | `var(--color-feedback-positive-base)`             |
 * | `--background-success`                 | Cor de fundo principal no tipo success                | `var(--color-feedback-positive-lightest)`         |
 * | `--border-color-success`               | Cor da borda principal tipo success                   | `var(--color-feedback-positive-lighter)`          |
 * | **Type Error**                         |                                                       |                                                   |
 * | `--color-error`                        | Cor principal no tipo error                           | `var(--color-feedback-negative-base)`             |
 * | `--background-error`                   | Cor de fundo principal no tipo error                  | `var(--color-feedback-negative-lightest)`         |
 * | `--border-color-error`                 | Cor da borda principal tipo error                     | `var(--color-feedback-negative-lighter)`          |
 * | **Type Warning**                       |                                                       |                                                   |
 * | `--color-icon-warning`                 | Cor principal do icone no tipo warning                | `var(--color-neutral-dark-90)`                    |
 * | `--color-warning`                      | Cor principal no tipo warning                         | `var(--color-feedback-warning-base)`              |
 * | `--background-warning`                 | Cor de fundo principal no tipo warning                | `var(--color-feedback-warning-lightest)`          |
 * | `--border-color-warning`               | Cor da borda principal tipo warning                   | `var(--color-feedback-warning-lighter)`           |
 * | **Type Info**                          |                                                       |                                                   |
 * | `--color-info`                         | Cor principal no tipo info                            | `var(--color-feedback-info-base)`                 |
 * | `--background-info`                    | Cor de fundo principal no tipo info                   | `var(--color-feedback-info-lightest)`             |
 * | `--border-color-info`                  | Cor da borda principal tipo info                      | `var(--color-feedback-info-lighter)`              |
 *
 */
@Directive()
export abstract class PoToasterBaseComponent {
  private _isHide: boolean;
  private _sizeActions: string = undefined;

  /**
   * @optional
   *
   * @description
   *
   * Ao para a notificao.
   */
  @Input('p-action') action: Function;

  /**
   * @optional
   *
   * @description
   *
   * Label do boto quando houver uma ao definida.
   */
  @Input('p-action-label') actionLabel: string;

  /**
   * @optional
   *
   * @description
   *
   * Mensagem a ser exibida na notificao.
   */
  @Input('p-message') message: string;

  /**
   * @optional
   *
   * @description
   *
   * Define se o Toaster esta invisivel.
   *
   * @default `false`
   */
  @Input({ alias: 'p-hide', transform: convertToBoolean }) isHide: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Evento emitido quando o valor de `isHide`  alterado.
   *
   */
  @Output('p-hide-change') isHideChange = new EventEmitter<boolean>();

  /**
   * @optional
   *
   * @description
   *
   * Exibe boto de fechar no toaster modo inline.
   *
   * @default `true`
   */
  @Input({ alias: 'p-show-close', transform: convertToBoolean }) showClose: boolean = true;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho das aes no componente:
   * - `small`: aplica a medida small de cada componente (disponvel apenas para acessibilidade AA).
   * - `medium`: aplica a medida medium de cada componente.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @HostBinding('attr.p-size-actions')
  @Input('p-size-actions')
  set sizeActions(value: string) {
    this._sizeActions = validateSizeFn(value, PoFieldSize);
  }

  get sizeActions(): string {
    return this._sizeActions ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * @optional
   *
   * @description
   *
   * Mensagem de suporte a ser exibida na notificao.
   */
  @Input('p-support-message') supportMessage?: string;

  /**
   * @optional
   *
   * @description
   *
   * Determina o tipo de notificao.
   *
   * Valores aceitos: `error`, `information`, `success` e `warning`.
   * @see PoToasterType
   *
   * @default `PoToasterType.Information`
   */
  @Input('p-type') type: PoToasterType = PoToasterType.Information;

  // Determina o modo do Toaster
  @Input('p-mode') mode: PoToasterMode = PoToasterMode.Inline;

  // Orientao da notificao, a mesma pode ser exibida na parte superior ou inferior da pgina.
  orientation: PoToasterOrientation = PoToasterOrientation.Bottom;

  // ComponentRef
  componentRef: any;

  // Posio para notificao aparecer na tela.
  position: number;

  // Fecha a notificao.
  abstract close(): void;

  // Altera a posio da notificao.
  abstract changePosition(value: number): void;

  // Configura o componente po-toaster de acordo com as definies do usurio.
  abstract configToaster(poToaster: PoToaster): void;
}
