import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  OnChanges,
  Output,
  Renderer2,
  SimpleChanges,
  ViewChild,
  inject,
  AfterViewInit,
  input
} from '@angular/core';
import { AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR } from '@angular/forms';

import {
  convertToBoolean,
  getDefaultSizeFn,
  isSafari,
  removeDuplicatedOptions,
  removeUndefinedAndNullOptions,
  setHelperSettings,
  uuid,
  validateSizeFn,
  validValue
} from '../../../utils/util';

import { PoFieldSize } from '../../../enums/po-field-size.enum';
import { AnimaliaIconDictionary, ICONS_DICTIONARY } from '../../po-icon';
import { PoFieldValidateModel } from '../po-field-validate.model';
import { PoSelectOptionGroup } from './po-select-option-group.interface';
import { PoSelectOption } from './po-select-option.interface';
import { PoHelperComponent, PoHelperOptions } from '../../po-helper';

const PO_SELECT_FIELD_LABEL_DEFAULT = 'label';
const PO_SELECT_FIELD_VALUE_DEFAULT = 'value';

/**
 * @docsExtends PoFieldValidateModel
 *
 * @example
 *
 * <example name="po-select-basic" title="PO Select Basic">
 *   <file name="sample-po-select-basic/sample-po-select-basic.component.html"> </file>
 *   <file name="sample-po-select-basic/sample-po-select-basic.component.ts"> </file>
 * </example>
 *
 * <example name="po-select-labs" title="PO Select Labs">
 *   <file name="sample-po-select-labs/sample-po-select-labs.component.html"> </file>
 *   <file name="sample-po-select-labs/sample-po-select-labs.component.ts"> </file>
 * </example>
 *
 * <example name="po-select-customer-registration" title="PO Select - Customer registration">
 *   <file name="sample-po-select-customer-registration/sample-po-select-customer-registration.component.html"> </file>
 *   <file name="sample-po-select-customer-registration/sample-po-select-customer-registration.component.ts"> </file>
 *   <file name="sample-po-select-customer-registration/sample-po-select-customer-registration.service.ts"> </file>
 * </example>
 *
 * <example name="po-select-companies" title="PO Select Companies">
 *   <file name="sample-po-select-companies/sample-po-select-companies.component.html"> </file>
 *   <file name="sample-po-select-companies/sample-po-select-companies.component.ts"> </file>
 * </example>
 *
 * @description
 *
 * O componente po-select exibe uma lista de valores e permite que o usurio selecione um desses valores.
 * Os valores listados podem ser fixos ou dinmicos de acordo com a necessidade do desenvolvedor, dando mais flexibilidade ao componente.
 * O po-select no permite que o usurio informe um valor diferente dos valores listados, isso garante a consistncia da informao.
 * O po-select no permite que sejam passados valores duplicados, undefined e null para as opes, excluindo-os da lista.
 *
 * > Ao passar um valor para o _model_ que no est na lista de opes, o mesmo ser definido como `undefined`.
 *
 * Tambm existe a possibilidade de utilizar um _template_ para a exibio dos itens da lista,
 * veja mais em **[p-combo-option-template](/documentation/po-combo-option-template)**.
 *
 * > Obs: o template **[p-select-option-template](/documentation/po-select-option-template)** ser depreciado na verso 14.x.x.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                    |
 * |----------------------------------------|-------------------------------------------------------|-------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                 |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                      |
 * | `--font-size`                          | Tamanho da fonte                                      | `var(--font-size-default)`                      |
 * | `--text-color-empty`                   | Cor do placeholder                                    | `var(--color-neutral-light-30)`                 |
 * | `--color`                              | Cor da borda                                          | `var(--color-neutral-dark-70)`                  |
 * | `--background`                         | Cor de background                                     | `var(--color-neutral-light-05)`                 |
 * | `--text-color`                         | Cor do texto                                          | `var(--color-neutral-dark-90)`                  |
 * | `--padding-horizontal`                 | Preenchimento horizontal                              | `0.5em`                                         |
 * | `--padding-vertical`                   | Preenchimento vertical                                | `0.7em`                                         |
 * | **Hover**                              |                                                       |                                                 |
 * | `--color-hover`                        | Cor principal no estado hover                         | `var(--color-brand-01-dark)`                    |
 * | `--background-hover`                   | Cor de background no estado hover                     | `var(--color-brand-01-lighter)`                 |
 * | **Focused**                            |                                                       |                                                 |
 * | `--outline-color-focused`              | Cor do outline do estado de focus                     | `var(--color-action-focus)`                     |
 * | `--color-focused`                      | Cor da borda no estado de focus                       | `var(--color-action-default)`                   |
 * | **Disabled**                           |                                                       |                                                 |
 * | `--color-disabled`                     | Cor principal no estado disabled                      | `var(--color-neutral-light-30)`                 |
 * | `--background-color-disabled`&nbsp;    | Cor de background no estado disabled                  | `var(--color-neutral-light-20)`                 |
 *
 */
@Component({
  selector: 'po-select',
  templateUrl: './po-select.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PoSelectComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => PoSelectComponent),
      multi: true
    }
  ],
  standalone: false
})
export class PoSelectComponent extends PoFieldValidateModel<any> implements AfterViewInit, OnChanges {
  private el = inject(ElementRef);
  renderer = inject(Renderer2);

  private _iconToken: { [key: string]: string };

  @ViewChild('select', { read: ElementRef, static: true }) selectElement: ElementRef;
  @ViewChild('helperEl', { read: PoHelperComponent, static: false }) helperEl?: PoHelperComponent;

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao sair do campo.
   */
  @Output('p-blur') blur: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Funo para atualizar o ngModel do componente, necessrio quando no for utilizado dentro da tag form.
   *
   * Na verso 12.2.0 do Angular a verificao `strictTemplates` vem true como default. Portanto, para utilizar
   * two-way binding no componente deve se utilizar da seguinte forma:
   *
   * ```
   * <po-select ... [ngModel]="selectModel" (ngModelChange)="selectModel = $event"> </po-select>
   * ```
   */
  @Output('ngModelChange') ngModelChange: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser somente para leitura.
   *
   * @default `false`
   */
  @Input({ alias: 'p-readonly', transform: convertToBoolean }) readonly: boolean = false;

  /** Mensagem que aparecer enquanto nenhuma opo estiver selecionada. */
  @Input('p-placeholder') placeholder?: string;

  displayValue;
  id = `po-select[${uuid()}]`;
  modelValue: any;
  selectedValue: any;
  optionsDefault = [];
  listGroupOptions = [];
  optionWithoutGroup = [];
  isSafari: boolean = isSafari();

  protected onModelTouched: any;

  private _fieldLabel?: string = PO_SELECT_FIELD_LABEL_DEFAULT;
  private _fieldValue?: string = PO_SELECT_FIELD_VALUE_DEFAULT;
  private _options: Array<PoSelectOption> | Array<PoSelectOptionGroup> | Array<any>;
  private _size?: string = undefined;

  /**
   * Nesta propriedade deve ser definido uma coleo de objetos que implementam a interface `PoSelectOption`,
   * ou uma coleo de objetos dentro de grupos diferentes, que seriam da interface `PoSelectOptionGroup`.
   *
   * Caso esta lista estiver vazia, o model ser `undefined`.
   *
   * > Essa propriedade  imutvel, ou seja, sempre que quiser atualizar a lista de opes disponveis
   * atualize a referncia do objeto:
   *
   * ```
   * // atualiza a referncia do objeto garantindo a atualizao do template
   * this.options = [...this.options, { value: 'x', label: 'Nova opo' }];
   *
   * // evite, pois no atualiza a referncia do objeto podendo gerar atrasos na atualizao do template
   * this.options.push({ value: 'x', label: 'Nova opo' });
   * ```
   *
   * > Para coleo de objetos dentro de grupos distintos ser exibido a label e opes somente se a propriedade `options` possua valores.
   *  Sendo assim, a estrutura seguiria dessa forma:
   *
   * ```
   * this.options = [{
   *  label: 'Opes',
   *  options: [
   *    { value: 1, label: 'opo 1' },
   *    { value: 2, label: 'opo 2' }
   *  ],
   * }];
   * ```
   *
   *  possvel a utilizao de opes agrupadas e desagrupadas em conjunto, porm ser feita a ordenao de exibir as opes
   * desagrupadas acima.
   *
   */
  @Input('p-options') set options(options: Array<PoSelectOption | PoSelectOptionGroup | any>) {
    this.listGroupOptions = [];
    this.optionWithoutGroup = [];

    if (this.fieldLabel && this.fieldValue && options) {
      options.map(option => {
        if (this.isItemGroup(option)) {
          option.options.map(opt => {
            opt.label = opt[this.fieldLabel];
            opt.value = opt[this.fieldValue];
          });
        } else {
          option.label = option[this.fieldLabel];
          option.value = option[this.fieldValue];
        }
      });
    }

    if (options) {
      this.optionsDefault = [...options];
      this.separateOptions();

      this.optionsDefault = [];
      this.optionsDefault = [...this.optionWithoutGroup, ...this.transformInArray(this.listGroupOptions)];
      this.onUpdateOptions();
      this._options = [...this.optionsDefault];
    }
  }

  get options() {
    return this._options;
  }

  /**
   * @optional
   *
   * @description
   * Deve ser informado o nome da propriedade do objeto que ser utilizado para a converso dos itens apresentados na lista do componente
   * (`p-options`), esta propriedade ser responsvel pelo texto de apresentao de cada item da lista.
   *
   * @default `label`
   */
  @Input('p-field-label') set fieldLabel(value: string) {
    this._fieldLabel = value || PO_SELECT_FIELD_LABEL_DEFAULT;
    if (this.options && this.options.length > 0) {
      this.options = [...this.options];
    }
  }

  get fieldLabel() {
    return this._fieldLabel;
  }

  /**
   * @optional
   *
   * @description
   * Deve ser informado o nome da propriedade do objeto que ser utilizado para a converso dos itens apresentados na lista do componente
   * (`p-options`), esta propriedade ser responsvel pelo valor de cada item da lista.
   *
   * @default `value`
   */
  @Input('p-field-value') set fieldValue(value: string) {
    this._fieldValue = value || PO_SELECT_FIELD_VALUE_DEFAULT;
    if (this.options && this.options.length > 0) {
      this.options = [...this.options];
    }
  }

  /**
   * @docsPrivate
   *
   * Determinar se o valor do compo deve retorna objeto do tipo {value: any, label: any}
   */
  @Input({ alias: 'p-control-value-with-label', transform: convertToBoolean }) controlValueWithLabel?: boolean = false;

  get fieldValue() {
    return this._fieldValue;
  }

  get iconNameLib() {
    return this._iconToken.NAME_LIB;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho do componente:
   * - `small`: altura do input como 32px (disponvel apenas para acessibilidade AA).
   * - `medium`: altura do input como 44px.
   *
   * > Caso a acessibilidade AA no esteja configurada, o tamanho `medium` ser mantido.
   * Para mais detalhes, consulte a documentao do [po-theme](https://po-ui.io/documentation/po-theme).
   *
   * @default `medium`
   */
  @Input('p-size') set size(value: string) {
    this._size = validateSizeFn(value, PoFieldSize);
  }

  get size(): string {
    return this._size ?? getDefaultSizeFn(PoFieldSize);
  }

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Define as opes do componente de ajuda (po-helper) que ser exibido ao lado do label quando a propriedade `p-label` for definida, ou, ao lado do componente na ausncia da propriedade `p-label`.
   * > Para mais informaes acesse: https://po-ui.io/documentation/po-helper.
   *
   * > Ao configurar esta propriedade, o antigo cone de ajuda adicional (`p-additional-help-tooltip` e `p-additional-help`) ser ignorado.
   */
  poHelperComponent = input<PoHelperOptions | string>(undefined, { alias: 'p-helper' });

  /**
   * @Input
   *
   * @optional
   *
   * @description
   *
   * Habilita a quebra automtica do texto da propriedade `p-label`. Quando `p-label-text-wrap` for verdadeiro, o texto que excede
   * o espao disponvel  transferido para a prxima linha em pontos apropriados para uma
   * leitura clara.
   *
   * @default `false`
   */
  labelTextWrap = input<boolean>(false, { alias: 'p-label-text-wrap' });

  /* istanbul ignore next */
  constructor() {
    const value = inject<{
      [key: string]: string;
    }>(ICONS_DICTIONARY, { optional: true });
    const changeDetector = inject(ChangeDetectorRef);

    super(changeDetector);

    this._iconToken = value ?? AnimaliaIconDictionary;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.label) {
      this.displayAdditionalHelp = false;
    }

    if (changes.options?.currentValue) {
      this.options = changes.options.currentValue;
    }
  }

  ngAfterViewInit(): void {
    if (this.selectElement?.nativeElement?.closest('.components-form-custom-template')) {
      setTimeout(() => {
        if (this.selectElement?.nativeElement?.classList?.contains('enable-append-box')) {
          this.appendBox = true;
        }
      }, 300);
    }
  }

  /**
   * Funo que atribui foco ao componente.
   *
   * Para utiliz-la  necessrio ter a instncia do componente no DOM, podendo ser utilizado o ViewChild da seguinte forma:
   *
   * ```
   * import { PoSelectComponent } from '@po-ui/ng-components';
   *
   * ...
   *
   * @ViewChild(PoSelectComponent, { static: true }) select: PoSelectComponent;
   *
   * focusSelect() {
   *   this.select.focus();
   * }
   * ```
   */
  focus(): void {
    if (!this.disabled) {
      this.selectElement.nativeElement.focus();
    }
  }

  getErrorPattern() {
    return this.fieldErrorMessage && this.hasInvalidClass() ? this.fieldErrorMessage : '';
  }

  hasInvalidClass() {
    return (
      this.el.nativeElement.classList.contains('ng-invalid') && this.el.nativeElement.classList.contains('ng-dirty')
    );
  }

  onBlur(event: any) {
    this.onModelTouched?.();

    if (event.type === 'blur') {
      this.blur.emit();
    }
  }

  // Altera o valor ao selecionar um item.
  onSelectChange(value: any) {
    this.onModelTouched?.();
    if (value && this.options && this.options.length) {
      const optionFound: any = this.findOptionValue(value);

      if (optionFound) {
        this.updateValues(optionFound);
      }
    }
  }

  onUpdateOptions() {
    if (this.modelValue) {
      this.onSelectChange(this.modelValue);
    }
  }

  // Atualiza valores
  updateValues(option: any): void {
    if (this.selectedValue !== option[this.fieldValue]) {
      this.selectedValue = option[this.fieldValue];
      this.selectElement.nativeElement.value = option[this.fieldValue];
      this.updateModel(this.getValueUpdate(option));
      this.displayValue = option[this.fieldLabel];
      this.emitChange(option[this.fieldValue]);
    }
  }

  // Recebe as alteraes do model
  onWriteValue(value: any) {
    value = this.getValueWrite(value);
    const optionFound: any = this.findOptionValue(value);

    if (optionFound) {
      this.selectElement.nativeElement.value = optionFound.value;
      this.selectedValue = optionFound[this.fieldValue];
      this.displayValue = optionFound[this.fieldLabel];
    } else if (validValue(this.selectedValue)) {
      this.selectElement.nativeElement.value = undefined;
      this.updateModel(undefined);
      this.selectedValue = undefined;
      this.displayValue = undefined;
    }

    this.modelValue = value;
    this.changeDetector.detectChanges();
  }

  extraValidation(c: AbstractControl): { [key: string]: any } {
    return null;
  }

  isItemGroup(item: PoSelectOption | PoSelectOptionGroup | any): boolean {
    if (item.options) {
      return Array.isArray(item.options) ? true : false;
    }
    return false;
  }

  onKeyDown(event: KeyboardEvent): void {
    const isFieldFocused = document.activeElement === this.selectElement.nativeElement;

    if (isFieldFocused) {
      this.keydown.emit(event);
    }
  }

  registerOnTouched(fn: any): void {
    this.onModelTouched = fn;
  }

  /**
   * Mtodo que exibe `p-helper` ou executa a ao definida em `p-helper{eventOnClick}` ou em `p-additionalHelp`.
   * Para isso, ser necessrio configurar uma tecla de atalho utilizando o evento `p-keydown`.
   *
   * > Exibe ou oculta o contedo do componente `po-helper` quando o componente estiver com foco.
   *
   * ```
   * //Exemplo com p-label e p-helper
   * <po-select
   *  #select
   *  ...
   *  p-label="Label do select"
   *  [p-helper]="helperOptions"
   *  (p-keydown)="onKeyDown($event, select)"
   * ></po-select>
   * ```
   * ```typescript
   * onKeyDown(event: KeyboardEvent, inp: PoSelectComponent): void {
   *  if (event.code === 'F9') {
   *    inp.showAdditionalHelp();
   *  }
   * }
   * ```
   */
  override showAdditionalHelp(): boolean {
    return super.showAdditionalHelp(this.helperEl, this.poHelperComponent());
  }

  setHelper(label?: string, additionalHelpTooltip?: string) {
    return setHelperSettings(
      label,
      additionalHelpTooltip,
      this.poHelperComponent(),
      this.size,
      this.isAdditionalHelpEventTriggered() ? this.additionalHelp : undefined
    );
  }

  private isEqual(value: any, inputValue: any): boolean {
    if ((value || value === 0) && inputValue) {
      return value.toString() === inputValue.toString();
    }

    if ((value === null && inputValue !== null) || (value === undefined && inputValue !== undefined)) {
      value = `${value}`; // Transformando em string
    }

    return value === inputValue;
  }

  private findOptionValue(value: any) {
    if (this.options) {
      return this.options.find(option => this.isEqual(option.value, value));
    }
  }

  private getValueUpdate(option: any) {
    if (this.controlValueWithLabel) {
      return {
        value: option[this.fieldValue],
        label: option[this.fieldLabel]
      };
    }

    return option[this.fieldValue];
  }

  private getValueWrite(data: any) {
    if (this.controlValueWithLabel && data?.value) {
      return data?.value;
    }

    return data;
  }

  private transformInArray(objectWithArray: Array<any>): Array<PoSelectOptionGroup | any> {
    return objectWithArray.reduce((options, items) => {
      if (items.options) {
        return options.concat(items.options);
      }
      return [];
    }, []);
  }

  private separateOptions() {
    this.optionsDefault.forEach(option => {
      if (this.isItemGroup(option)) {
        this.validateOptions(option.options);
        this.listGroupOptions.push(option);
      } else {
        this.optionWithoutGroup.push(option);
      }
    });

    if (this.optionWithoutGroup.length > 0) {
      this.validateOptions(this.optionWithoutGroup);
    }
  }

  private validateOptions(options: Array<any>) {
    removeDuplicatedOptions(options);
    removeUndefinedAndNullOptions(options);
  }
}
